import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import { withLocalization } from './localization';

const styles = theme => ({
  formControl: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    minWidth: 100
  },
});

function TextInputComponent(props) {
  const { classes } = props;
  const label = props.label || props.translate('field.' + props.field);

  return (
    <FormControl 
      className={classes.formControl} 
      fullWidth={props.fullWidth} 
      required={props.required}
      error={props.errorText && props.errorText !== ""}
      style={{width: props.width}}
    >
      <InputLabel htmlFor={props.field}>{label}</InputLabel>
      <Input 
        type={props.type === 'new-password' ? 'password' : props.type}
        id={props.field} 
        value={props.value || ''} 
        onChange={(e) => props.onChange(props.field, e.target.value)} 
        placeholder={props.placeholder}
        autoComplete={props.type === 'new-password' ? 'new-password' : null}
        disabled={props.disabled}
        multiline={props.multiline}
        rows={props.rows}
        startAdornment={props.startAdornment}
        inputProps={{
          onBlur: props.onBlur
        }}
      />
      <FormHelperText>{props.errorText}</FormHelperText>
    </FormControl>
  );
}

TextInputComponent.propTypes = {
  classes: PropTypes.object.isRequired,
  label: PropTypes.string,
  field: PropTypes.string.isRequired,
  value: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  placeholder: PropTypes.string,
  fullWidth: PropTypes.bool,
  required: PropTypes.bool,
  type: PropTypes.string,
  disabled: PropTypes.bool,
  multiline: PropTypes.bool,
  rows: PropTypes.number,
  startAdornment: PropTypes.string,
  onBlur: PropTypes.func,
  errorText: PropTypes.string,
  width: PropTypes.number
};

export default withLocalization(withStyles(styles)(TextInputComponent));