import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import TextInput from "../shared/TextInput";
import Form from "../shared/form/Form";
import Switch from "../shared/Switch";
import NumInput from "../shared/NumInput";
import TimeInput from "../shared/TimeInput";
import FileInput from "../shared/FileInput";
import config from "../../config";

import withDoby from "../../consumers/withDoby";
import withUser from "../../consumers/withUser";
import withStore from "../../consumers/withStore";
import compose from "../../compose";

const styles = (theme) => ({
  nameTextInput: {
    display: "flex",
    flex: 1,
  },
});

const INIT_STATE = {
  name: "",
  disabled: false,
  description: "",
  difficulty: 1,
  elevation: null,
  elevationGain: null,
  distance: null,
  time: "01:00",
  _img: null,
  _thumbnail: null,
};

class FormWrapper extends React.Component {
  constructor(props) {
    super(props);
    this.state = props.data || INIT_STATE;
  }

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleSubmit = async () => {
    const { addSummit, refreshSummits, editSummit } = this.props.doby;

    try {
      if (this.state._id) {
        await editSummit(this.state);
      } else {
        await addSummit(this.state);
      }

      refreshSummits();
      this.setState(INIT_STATE);

      if (this.props.onSubmit) this.props.onSubmit();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { classes, disabled } = this.props;
    const {
      name,
      description,
      difficulty,
      elevation,
      elevationGain,
      distance,
      time,
      _img,
      _thumbnail,
    } = this.state;

    return (
      <Form onSubmit={this.handleSubmit} inline disabled={disabled}>
        <div className={classes.nameTextInput}>
          <TextInput
            value={name}
            field="name"
            onChange={this.handleChange}
            fullWidth
            required
            disabled={disabled}
          />
        </div>

        <div className={classes.nameTextInput}>
          <TextInput
            value={description}
            field="description"
            onChange={this.handleChange}
            required
            fullWidth
            disabled={disabled}
          />
        </div>

        <NumInput
          value={difficulty}
          field="difficulty"
          onChange={this.handleChange}
          min={0}
          max={5}
          step={1}
          required
        />

        <NumInput
          value={elevation}
          field="elevation"
          onChange={this.handleChange}
          min={1}
          width={110}
          required
        />

        <NumInput
          value={elevationGain}
          field="elevationGain"
          onChange={this.handleChange}
          min={1}
          width={40}
        />

        <NumInput
          value={distance}
          field="distance"
          onChange={this.handleChange}
          min={1}
          width={40}
        />

        <TimeInput
          value={time}
          field="time"
          onChange={this.handleChange}
          disabled={disabled}
        />

        <Switch
          label=""
          value={this.state.disabled}
          field="disabled"
          onChange={this.handleChange}
        />

        <FileInput
          mimeType="image/jpeg"
          value={_img}
          field="_img"
          onChange={this.handleChange}
          disabled={this.state._id || this.state._id === "" || disabled}
          height={config.imageSize}
        />
        <FileInput
          mimeType="image/jpeg"
          value={_thumbnail}
          field="_thumbnail"
          onChange={this.handleChange}
          disabled={this.state._id || this.state._id === "" || disabled}
          height={config.thumbnailSize}
        />
      </Form>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object,
  onSubmit: PropTypes.func,
  date: PropTypes.instanceOf(Date),
  disabled: PropTypes.bool,
};

export default compose(
  withStyles(styles),
  withDoby,
  withUser,
  withStore
)(FormWrapper);
