import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Form from "../shared/form/Form";
import Button from "@material-ui/core/Button";
import TextField from "../dobyComponents/form/TextField";
import Dialog from "../shared/Dialog";
import Switch from "../shared/Switch";
import Avatar from "../shared/avatar/Avatar";
import GithubButton from "../shared/login/GithubButton";
import GoogleButton from "../shared/login/GoogleButton";
import StravaButton from "../shared/login/StravaButton";
import FbButton from "../shared/login/FbButton";

import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import withUtils from "../shared/utils/withUtils";
import compose from "../../compose";
import { getStyles } from "../shared/style";
import { withLocalization } from "../shared/localization";
import config from "../../config";
import Checkbox from "@material-ui/core/Checkbox";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormLabel from "@material-ui/core/FormLabel";
import _ from "lodash";

const styles = (theme) => ({
  ...getStyles(theme),
  heading: {
    marginLeft: theme.spacing.unit,
  },
  detailHeading: {
    marginLeft: theme.spacing.unit,
    marginTop: theme.spacing.unit * 3,
  },
  actions: {
    float: "right",
    margin: theme.spacing.unit,
  },
});

const INIT_STATE = {
  password: {
    value: "",
    errorText: "",
  },
  repeatedPassword: {
    value: "",
    errorText: "",
  },
  email: {
    value: "",
    isLoading: false,
    errorText: "",
  },
  name: "",
  username: {
    value: "",
    isLoading: false,
    errorText: "",
  },
  tel: {
    value: "",
    errorText: "",
  },
  address: "",
  city: "",
  postCode: "",
  country: "",
  otp: false,
  avatar: null,
  notifications: true,
  notifyAboutMe: false,
  notifyAboutFollowers: false,
  notifyNews: false,
};

const buildState = (user) => {
  return {
    ...INIT_STATE,
    ...user,
    email: { ...INIT_STATE.email, value: user.email },
    username: { ...INIT_STATE.username, value: user.username },
    tel: { ...INIT_STATE.tel, value: user.tel || "" },
    isOpenSecretDialog: false,
    avatarChanged: false,
  };
};

class FormWrapper extends React.Component {
  state = buildState(this.props.user.user);

  handleChange = (name, value) => {
    this.setState((state) => _.set(state, name, value));
  };

  handleChangeCheckBox = (event) => {
    const { name, checked } = event.target;
    this.setState((state) => _.set(state, name, checked));
  };

  validTelephone = () => {
    const { translate } = this.props;
    const { tel } = this.state;

    const regex = new RegExp("^[0-9]{3}[-][0-9]{3} [0-9]{3} [0-9]{3}$");
    const isValid = tel.value === "-  " || regex.test(tel.value);
    const errorText = isValid ? "" : translate("field.telNotValid");

    this.setState((state) => ({
      tel: { ...state.tel, errorText },
    }));

    return isValid;
  };

  validPasswords = () => {
    const { password, repeatedPassword } = this.state;
    const { translate } = this.props;

    const regex = new RegExp(config.passwordPattern);
    const isValidPassword = !password.value || regex.test(password.value);
    const errorTextPassword = isValidPassword
      ? ""
      : translate("field.passwordWeak");

    const isValidRepeatedPassword = password.value === repeatedPassword.value;
    const errorTextRepeatedPassword = isValidRepeatedPassword
      ? ""
      : translate("field.passwordNoMatch");

    this.setState((state) => ({
      password: { ...state.password, errorText: errorTextPassword },
      repeatedPassword: {
        ...this.state.repeatedPassword,
        errorText: errorTextRepeatedPassword,
      },
    }));

    return isValidPassword && isValidRepeatedPassword;
  };

  validEmail = async () => {
    const { translate } = this.props;
    const { email } = this.state;

    this.setState((state) => ({
      email: {
        ...state.email,
        isLoading: true,
      },
    }));

    const isValid = !email.value || (await this.isValid(email.value));
    const errorText = isValid ? "" : translate("field.emailInUse");

    this.setState((state) => ({
      email: { ...state.email, errorText, isLoading: false },
    }));

    return isValid;
  };

  isValid = async (username) => {
    const { user } = this.props;

    const ids = await this.props.user.checkUser(username);
    return (
      ids.length === 0 || (ids.length === 1 && ids[0]._id === user.user._id)
    );
  };

  validUsername = async () => {
    const { translate } = this.props;
    const { username } = this.state;

    this.setState((state) => ({
      username: {
        ...state.username,
        isLoading: true,
      },
    }));

    const isValid = !username.value || (await this.isValid(username.value));
    const errorText = isValid ? "" : translate("field.usernameInUse");

    this.setState((state) => ({
      username: { ...state.username, errorText, isLoading: false },
    }));

    return isValid;
  };

  handleRemoveAvatar = () => {
    this.setState({ avatar: null, avatarChanged: true });
  };

  handleChangeAvatar = (event) => {
    event.preventDefault();

    const reader = new FileReader();
    reader.onloadend = async () => {
      let data = await this.props.utils.resize(reader.result, 320);
      data = data.split(",").pop();
      this.setState({ avatar: data, avatarChanged: true });
    };
    reader.readAsDataURL(event.target.files[0]);

    // zabezpecuje volanie onChange aj pri uploadovani rovnakeho suboru
    // -> po ulozeni formularu nezavolalo onChange opat, v pripade nahrania
    // -> rovnakeho suboru ako predtym
    event.target.value = "";
  };

  generateSecret = async () => {
    try {
      await this.props.user.generateSecret();
      this.handleOpenSecretDialog();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  handleOpenSecretDialog = () => {
    this.setState({ isOpenSecretDialog: true });
  };

  handleCloseSecretDialog = () => {
    this.setState({ isOpenSecretDialog: false });
  };

  handleSubmit = async () => {
    const {
      password,
      name,
      username,
      email,
      tel,
      address,
      city,
      postCode,
      country,
      otp,
      avatar,
      avatarChanged,
    } = this.state;

    const isValid =
      this.validPasswords() &&
      (await this.validEmail()) &&
      (await this.validUsername());
    if (!isValid) {
      return;
    }

    try {
      await this.props.user.updateUser(this.state._id, {
        email: email.value,
        password: password.value,
        name,
        username: username.value,
        tel: tel.value,
        address,
        city,
        postCode,
        country,
        otp,
        avatar: avatarChanged ? avatar : null,
      });
      this.setState(buildState(this.props.user.user));
      this.props.user.showMsg("Ok.");
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { classes, translate, locale, disabled } = this.props;
    const { user } = this.props.user;
    const connectTitle = translate("connect");
    const {
      email,
      tel,
      otp,
      address,
      city,
      postCode,
      country,
      avatar,
      name,
      bio,
      notifications,
      notifyAboutMe,
      notifyAboutFollowers,
      notifyNews,
    } = this.state;

    return (
      <>
        <Form onSubmit={this.handleSubmit} disabled={disabled}>
          <p>Nastavenia</p>
          <div className={classes.row}>
            <div className={classes.col}>
              <div className={classes.center}>
                <Avatar
                  className={classes.bigAvatar}
                  value={avatar}
                  onClick={() => {
                    console.log("Avatar clicked");
                  }}
                  tooltip="Avatar"
                  placement="right"
                />
              </div>
            </div>
            <div className={classes.col}>
              <p>
                Tvoja profilov fotrografia je verejn no sli aj na
                schvalovanie vstupov
              </p>
              <Button variant="contained" component="label">
                {translate("changePhoto")}
                <input
                  hidden
                  type="file"
                  accept="image/jpeg"
                  onChange={this.handleChangeAvatar}
                />
              </Button>
              <Button variant="outlined" onClick={this.handleRemoveAvatar}>
                {translate("removePhoto")}
              </Button>
            </div>
          </div>
          <GoogleButton
            config={config.google}
            disabled={disabled || user.google}
            title={user.google ? user.google.name : connectTitle}
          />
          <GithubButton
            config={config._github}
            disabled={disabled || user.github}
            title={user.github ? user.github.name : connectTitle}
          />
          <FbButton
            config={config.fb}
            disabled={disabled || user.fb}
            title={user.fb ? user.fb.name : connectTitle}
          />
          <div>
            <StravaButton
              config={config.strava}
              disabled={disabled}
              title={user.strava ? translate("sync") : connectTitle}
            />
            {config.strava && user.strava && user.strava.synced && (
              <span>
                {" "}
                ({translate("lastSync")}{" "}
                {new Date(user.strava.synced).toLocaleDateString(locale)}){" "}
              </span>
            )}
          </div>

          <TextField
            value={name}
            field="name"
            onChange={this.handleChange}
            disabled={disabled}
          />
          <TextField
            value={bio}
            field="bio"
            onChange={this.handleChange}
            disabled={disabled}
            rows={3}
            multiline
          />

          <p className={styles.heading}>Doruovacie daje</p>
          <TextField
            type="email"
            value={email.value}
            field="email.value"
            onChange={this.handleChange}
            disabled={disabled || email.isLoading}
            onBlur={this.validEmail}
            errorText={email.errorText}
            required
          />
          <TextField
            value={tel.value}
            field="tel.value"
            onChange={this.handleChange}
            onBlur={this.validTelephone}
            disabled={disabled}
            errorText={tel.errorText}
          />
          <TextField
            value={country}
            field="country"
            type="country"
            onChange={this.handleChange}
            disabled={disabled}
          />
          <TextField
            value={city}
            field="city"
            type="city"
            onChange={this.handleChange}
            disabled={disabled}
          />
          <TextField
            value={address}
            field="address"
            type="address"
            onChange={this.handleChange}
            disabled={disabled}
          />
          <TextField
            value={postCode}
            field="postCode"
            type="postCode"
            onChange={this.handleChange}
            disabled={disabled}
          />
          <div className={classes.row}>
            <div className={classes.col}>
              <FormLabel component="legend">
                {translate("notifications")}
              </FormLabel>
              <FormGroup>
                <FormControlLabel
                  control={
                    <Checkbox
                      value={notifyAboutMe}
                      checked={notifyAboutMe}
                      onChange={this.handleChangeCheckBox}
                      name="notifyAboutMe"
                      disabled={!notifications || disabled}
                    />
                  }
                  label={translate("notifyAboutMe")}
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      value={notifyAboutFollowers}
                      checked={notifyAboutFollowers}
                      onChange={this.handleChangeCheckBox}
                      name="notifyAboutFollowers"
                      disabled={!notifications || disabled}
                    />
                  }
                  label={translate("notifyAboutFollowers")}
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      value={notifyNews}
                      onChange={this.handleChangeCheckBox}
                      name="notifyNews"
                      checked={notifyNews}
                      disabled={!notifications || disabled}
                    />
                  }
                  label={translate("notifyNews")}
                />
              </FormGroup>
            </div>
            <div className={classes.col}>
              <div>
                <Switch
                  value={notifications}
                  field="notifications"
                  onChange={this.handleChange}
                  disabled={disabled}
                />
              </div>
            </div>
          </div>
          {config["2fa"] && (
            <div className={classes.row}>
              <div className={classes.col}>
                <div>
                  <Switch
                    value={otp}
                    field="otp"
                    onChange={this.handleChange}
                    disabled={disabled}
                  />
                  <Button
                    color="primary"
                    disabled={disabled || !this.state.otp}
                    onClick={this.generateSecret}
                  >
                    {translate("generateSecret")}
                  </Button>
                </div>
              </div>
            </div>
          )}
        </Form>
        <Dialog
          title={this.props.translate("secret")}
          isOpen={this.state.isOpenSecretDialog}
          onConfirm={this.handleCloseSecretDialog}
        >
          <div className={classes.center}>
            {this.props.user.secret && (
              <img
                src={"data:image/jpeg;base64," + this.props.user.secret.qrCode}
                alt="secret"
                width={320}
                height={320}
              />
            )}
          </div>
        </Dialog>
      </>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  disabled: PropTypes.bool.isRequired,
};

export default compose(
  withStyles(styles),
  withStore,
  withUser,
  withUtils,
  withLocalization
)(FormWrapper);
