import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../style';
import { getStyles } from '../shared/style';
import classNames from 'classnames';

import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import VizSensor from 'react-visibility-sensor';

const styles = theme => ({
    ...getSharedStyles(theme),
    ...getStyles(theme),

    root: {
        justifyContent: 'center'
    },
    itemCard: {
        boxShadow: 'none',
        backgroundColor: 'transparent'
    },
    media: {
        height: 140,
        width: '100%',
        backgroundColor: '#ededed',
    },
    price: {
        fontWeight: 'bold',
        float: 'left',
        display: 'inline-block'
    },
    source: {
        float: 'right',
        display: 'inline-block'
    },
    membersOnlySpacer: {
        height: 10,
        width: '100%'
    },
    membersOnly: {
        height: 25,
        backgroundColor: '#4c4c4c',
        width: '90%',
        color: 'white',
        display: 'flex',
        alignItems: 'center',
        textAlign: 'center',
        paddingLeft: 5
    }
});

function ItemCard(props) {
    const [img, setImg] = useState(null);
    const onViz = async (isVisible) => {
        try {
            if(props.Id) {
                const data = await props.onViz(props.Id);
                var reader = new FileReader();
                reader.onloadend = () => {
                    setImg(reader.result);
                }
                reader.readAsDataURL(data);
            }
        } catch(e) {
            console.error(e);
        }
    }

    const { classes } = props;
    return (
        <VizSensor
            partialVisibility
            onChange={onViz}
        >
            <Card
                className={classNames(classes.itemCard, classes.w100)}
                onClick={props.onClick}
            >
                <CardActionArea>
                    <CardMedia
                        className={classes.media}
                        image={img || '/img/logo.png'}
                        title={props.name}
                    >
                        {props.membersOnly ? (
                            <div className={styles.center}>
                                <div className={classes.membersOnlySpacer}></div>
                                <div className={classNames(classes.membersOnly, styles.center)}>Pre astnkov vzvy</div>
                            </div>
                        ) : null
                        }
                    </CardMedia>
                    <CardContent className={classNames(classes.p0, classes.mt3)}>
                        <Typography>{props.name}</Typography>
                        <Typography component="p" className={classes.price}>
                            <small>{props.price}{props.curr}</small>
                        </Typography>
                        <Typography component="p" className={classes.source}>
                            {props.source}
                        </Typography>
                    </CardContent>
                </CardActionArea>
            </Card>
        </VizSensor>
    );
}

ItemCard.propTypes = {
    classes: PropTypes.object.isRequired,
    onClick: PropTypes.func.isRequired,
    name: PropTypes.string.isRequired,
    price: PropTypes.number.isRequired,
    curr: PropTypes.string.isRequired,
    source: PropTypes.string.isRequired,
    onViz: PropTypes.func.isRequired,
    Id: PropTypes.string,
    membersOnly: PropTypes.bool
};

export default withStyles(styles)(ItemCard);