import React from "react";
import { withRouter } from "react-router";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import { withLocalization } from "../shared/localization";
import compose from "../../compose";
import getSharedStyles from "../../style";
import classNames from "classnames";
import withUser from "../../consumers/withUser";
import withDoby from "../../consumers/withDoby";
import withStore from "../../consumers/withStore";
import config from "../../config";

import ArrowLeftIcon from "@material-ui/icons/ArrowLeft";
import Button from "../dobyComponents/form/Button";
import FollowCard from "../dobyComponents/FollowCard";
import Autocomplete from "../shared/Autocomplete";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  profileImg: {
    width: "120px",
    borderRadius: "100px",
  },
  medalItem: {
    width: "120px",
    height: "160px",
    position: "relative",
  },
  medalItemBg: {
    background: "#F5F5F5",
    //background: 'red',
    borderRadius: "10px",
    width: "100%",
    height: "120px",
  },
  medalItemImg: {
    width: "95px",
    position: "absolute",
    top: "10px",
    left: 0,
    right: 0,
    margin: "0 auto",
  },
  medalCarousel: {
    "& .flickity-viewport": {
      //padding: '7px'
    },
    "& .carousel-cell:first-child": {
      paddingLeft: "1.25rem",
    },
    "& .carousel-cell:last-child": {
      //paddingRight: '0.5rem',
    },
  },
  challengesCarousel: {
    "& .flickity-viewport": {
      //padding: '7px'
    },
    "& .carousel-cell:first-child": {
      paddingLeft: "1.25rem",
    },
    "& .carousel-cell:last-child": {
      //paddingRight: '0.5rem',
    },
  },
});

// const flickityOptions = {
//   cellAlign: "left",
//   contain: true,
//   prevNextButtons: false,
//   pageDots: false,
// };

class Follow extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      following: [],
      followers: [],
      renderFollowers: true,
      foundFriend: null,
    };
  }

  goToProfile = () => this.props.history.push(`/profile`);
  goToProfileFriend = (id) => this.props.history.push(`/profile-friend/${id}`);

  buildFilter = (ids) => {
    return ids.map((id) => ({ field: "_id", value: id.id }));
  };

  showFollowers = () => {
    this.setState({ renderFollowers: true });
  };

  showFollowing = () => {
    this.setState({ renderFollowers: false });
  };

  sendMail = async (id, name) => {
    const data = {
      subject: this.props.translate("emial_follow_subject"),
      from: config.mail.address,
      fromName: config.mail.name,
      recipientRef: {
        id,
      },
      templateId: config.mail.userEmailNotificationTemplate,
      params: [name],
    };

    await this.props.user.sendMail(data);
  };

  // Follow/Unfollow user
  handleFollowUser = async (id) => {
    this.props.doby.followUser(id);

    const { followers, following } = this.state;
    const user = followers.find((f) => f._id === id);

    following.push(user);

    this.setState({ following });
    this.sendMail(id, user.name);
  };
  handleUnfollowUser = async (id) => {
    this.props.doby.unfollowUser(id);

    let { following } = this.state;
    following = this.state.following.filter((f) => f._id !== id);

    this.setState({ following });
  };

  renderFollowing = () => {
    const { classes } = this.props;

    return this.state.following.map((f) => {
      return (
        <div className={classNames("carousel-cell", classes.py2)}>
          <FollowCard
            className={classNames(classes.mr5)}
            buttonLabel="following"
            onClick={this.handleUnfollowUser}
            name={f.name}
            medalCount={0}
            userID={f._id}
          />
        </div>
      );
    });
  };

  renderFollowers = () => {
    const { classes } = this.props;
    const { following, followers } = this.state;

    return followers.map((follower) => {
      const alreadyFollowHim =
        following.find((following) => following._id === follower._id) !==
        undefined;

      return (
        <div className={classNames("carousel-cell", classes.py2)}>
          <FollowCard
            className={classNames(classes.mr5)}
            buttonLabel={alreadyFollowHim ? "following" : "follow"}
            onClick={
              alreadyFollowHim ? this.handleUnfollowUser : this.handleFollowUser
            }
            name={follower.name}
            medalCount={1}
            userID={follower._id}
          />
        </div>
      );
    });
  };

  setFollowingFollowers = async (followingRef, followersRef) => {
    const { getUsers } = this.props.user;

    let { following, followers } = this.state;

    if (followingRef && followingRef.length !== 0) {
      if (followingRef.length === 0) following = [];
      else {
        const filterFollowing = this.buildFilter(followingRef);
        following = (await getUsers(filterFollowing)).data;
      }
    }

    if (followersRef && followersRef.length !== 0) {
      if (followersRef.length === 0) followers = [];
      else {
        const filterFollowers = this.buildFilter(followersRef);
        followers = (await getUsers(filterFollowers)).data;
      }
    }

    this.setState({ following, followers });
  };

  handleOnChangeSearchFriends = (name, friend) => {
    this.goToProfileFriend(friend._id);
  };

  handleOnFilterChangeSearchFriends = async (filter) => {
    await this.props.user.getUsers(filter);
  };

  async componentDidMount() {
    const { user, getUsers } = this.props.user;

    const dobyUser = await this.props.doby.getUser(user._id);
    if (dobyUser) {
      const { followingRef, followersRef } = dobyUser;
      this.setFollowingFollowers(followingRef, followersRef);
    }

    await getUsers([]);
  }

  render() {
    const { classes } = this.props;
    const { users } = this.props.user;
    const { following, followers, renderFollowers, foundFriend } = this.state;

    return (
      <>
        <div>
          <Grid container direction="row" justify="center" alignItems="center">
            {/* HEADER NAVIGATION */}
            <Grid item xs={12} className={classNames(classes.mt5)}>
              <div className={classNames(classes.container)}>
                <div
                  className={classNames(
                    classes.dFlex,
                    classes.w100,
                    classes.alignItemsCenter,
                    classes.justifyContentBetween
                  )}
                >
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      flexWrap: "wrap",
                    }}
                  >
                    <ArrowLeftIcon onClick={this.goToProfile} />
                    <span>Profil</span>
                  </div>
                </div>
              </div>
            </Grid>

            {/* FOLLOW ME/THEM BUTTONS */}
            <Grid item xs={12}>
              <div className={classNames(classes.container)}>
                <div
                  className={classNames(
                    classes.dFlex,
                    classes.alignItemsCenter,
                    classes.w100,
                    classes.mt4
                  )}
                >
                  <div className={classNames(classes.mt3, classes.w100)}>
                    <Button
                      onClick={this.showFollowing}
                      fullwidth
                    >{`Sledujem (${following.length})`}</Button>
                  </div>
                  <div className={classNames(classes.mt3, classes.w100)}>
                    <Button
                      onClick={this.showFollowers}
                      fullwidth
                    >{`Sleduj ma (${followers.length})`}</Button>
                  </div>
                </div>
              </div>
            </Grid>

            {/* FOLLOWERS/FOLLOWING CARDS */}
            <Grid item xs={12} className={classNames(classes.mt7)}>
              <div className={classNames(classes.container)}>
                {renderFollowers && this.renderFollowers()}
                {!renderFollowers && this.renderFollowing()}
              </div>
            </Grid>

            {/* SEARCH FRIENDS */}
            <Grid item xs={12} className={classNames(classes.mt7)}>
              <div className={classNames(classes.container)}>
                <Autocomplete
                  classes={classes}
                  onChange={this.handleOnChangeSearchFriends}
                  onFilterChange={this.handleOnFilterChangeSearchFriends}
                  data={users ? users.data : []}
                  field="foundFriend"
                  textField="username"
                  label="Njs priateov"
                  value={foundFriend}
                  fullWidth
                />
              </div>
            </Grid>
          </Grid>
        </div>
      </>
    );
  }
}

Follow.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default compose(
  withStyles(styles),
  withDoby,
  withUser,
  withStore,
  withLocalization,
  withRouter
)(Follow);
