import React from "react";
import FbIcon from "../icons/FbIcon";
import Button from '@material-ui/core/Button';

const GithubButton = props => {
  const handleClick = () => {
    const redirect = process.env.REACT_APP_HOMEPAGE;
    const url = `https://www.facebook.com/v9.0/dialog/oauth?scope=email,user_link&client_id=${props.config.id}&redirect_uri=${redirect}/?provider=5&state=1`;
    window.location.replace(url);
  };

  if(!props.config) {
    return null;
  }

  return (
    <Button fullWidth={props.fullWidth} color="primary" onClick={handleClick} disabled={props.disabled}>
      <FbIcon fontSize="large" /> <span style={props.fullWidth ? { width: 200} : null}>{props.title}</span>
    </Button>
  );
};

export default GithubButton;