import React from "react";
import PropTypes from "prop-types";
import Avatar from "@material-ui/core/Avatar";
import Divider from "@material-ui/core/Divider";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import FormControl from "@material-ui/core/FormControl";
import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import Typography from "@material-ui/core/Typography";
import withStyles from "@material-ui/core/styles/withStyles";
import { withRouter } from "react-router";
import { withLocalization } from "../shared/localization";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import classnames from "classnames";

import { Redirect } from "react-router-dom";
import withUser from "../../consumers/withUser";
import withStore from "../../consumers/withStore";
import compose from "../../compose";
import GithubButton from "../shared/login/GithubButton";
import GoogleButton from "../shared/login/GoogleButton";
import StravaButton from "../shared/login/StravaButton";
import FbButton from "../shared/login/FbButton";
import config from "../../config";

const styles = (theme) => ({
  root: {
    height: "100vh",
  },
  image: {
    backgroundImage: `url(/img/intro-bg-desktop.jpg)`,
    backgroundRepeat: "no-repeat",
    backgroundColor:
      theme.palette.type === "light"
        ? theme.palette.grey[50]
        : theme.palette.grey[900],
    backgroundSize: "cover",
    backgroundPosition: "center",
  },
  paper: {
    margin: theme.spacing.unit * 8,
    marginBottom: theme.spacing.unit * 4,
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing.unit,
    // backgroundColor: theme.palette.secondary.main,
    width: 128,
    height: 128,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing.unit,
  },
  submit: {
    marginTop: theme.spacing.unit * 3,
    marginBottom: theme.spacing.unit,
  },
  center: {
    display: "flex",
    alignItems: "center",
    flexDirection: "column",
  },
});

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright  "}
      <Link color="inherit" href={process.env.REACT_APP_HOMEPAGE}>
        {process.env.REACT_APP_NAME + " "}
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

class Login extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      username: "",
      password: "",
      token: "",
      disabled: false,
    };
  }

  handleSubmit = async (e) => {
    e.preventDefault();

    this.setState({ disabled: true });

    const { username, password, token } = this.state;
    try {
      const user = await this.props.user.auth(
        username.trim().toLowerCase(),
        password,
        token
      );
      if (user === null) {
        this.props.user.showMsg("Unauthorized");
      }
    } catch (error) {
      this.props.user.showError(error);
    }

    this.setState({ disabled: false });
  };

  handleChange = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };

  handleClickReset = async () => {
    const { username } = this.state;

    if (username) {
      try {
        await this.props.user.reset(username.trim().toLowerCase());
        this.props.user.showMsg("Mail sended.");
      } catch (error) {
        this.props.user.showError(error);
      }
    }
  };

  handleRegister = () => {
    this.props.history.push(`/register`);
  };

  render() {
    const { classes, user, translate } = this.props;
    const { username, password, disabled, oAuth, token } = this.state;

    const { from } = this.props.location.state || {
      from: { pathname: config.home },
    };

    if (oAuth) return null;
    if (user.user) return <Redirect to={from} />;

    return (
      <Grid
        container
        component="main"
        className={classnames(classes.root)}
      >
        <CssBaseline />
        <Grid item xs={false} sm={4} md={7} className={classes.image} />
        <Grid item xs={12} sm={8} md={5} component={"div"} elevation={6} square>
          <div className={classes.paper}>
            <img alt="logo" className={classes.avatar} src={config.logo} />
            {/* <Typography component="h1" variant="h5">
            Sign in
          </Typography> */}

            <GithubButton
              fullWidth
              disabled={disabled}
              title={translate("githubSignIn")}
              config={config.github}
            />
            <GoogleButton
              fullWidth
              disabled={disabled}
              title={translate("googleSignIn")}
              config={config.google}
            />
            <FbButton
              fullWidth
              disabled={disabled}
              title={translate("fbSignIn")}
              config={config.fb}
            />
            <StravaButton
              fullWidth
              disabled={disabled}
              title={translate("stravaSignIn")}
              config={config.strava}
            />

            <Typography variant="body1">{translate("useAccount")}</Typography>

            <form className={classes.form} onSubmit={this.handleSubmit}>
              <FormControl margin="normal" required fullWidth>
                <InputLabel htmlFor="username">
                  {translate("field.username")}
                </InputLabel>
                <Input
                  id="username"
                  name="username"
                  autoComplete="username"
                  autoFocus
                  value={username}
                  onChange={this.handleChange}
                />
              </FormControl>
              <FormControl margin="normal" required fullWidth>
                <InputLabel htmlFor="password">
                  {translate("field.password")}
                </InputLabel>
                <Input
                  name="password"
                  type="password"
                  id="password"
                  autoComplete="current-password"
                  value={password}
                  onChange={this.handleChange}
                />
              </FormControl>

              <Link color="textSecondary" onClick={this.handleClickReset}>
                {translate("qForgotPassword")}
              </Link>

              {config["2fa"] && (
                <FormControl margin="normal" fullWidth>
                  <InputLabel htmlFor="password">
                    {translate("field.token")}
                  </InputLabel>
                  <Input
                    name="token"
                    id="token"
                    value={token}
                    onChange={this.handleChange}
                  />
                </FormControl>
              )}
              {/* <FormControlLabel
              control={<Checkbox value="remember" color="primary" />}
              label="Remember me"
            /> */}
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                disabled={disabled}
              >
                {translate("signIn")}
              </Button>

              <Link color="textSecondary" onClick={this.handleRegister}>
                {translate("orRegister")}
              </Link>

              {/* <Grid container>
                <Grid item xs>
                  <Link href="#" variant="body2" onClick={this.handleClickReset}>
                    Forgot password?
                  </Link>
                </Grid>
                <Grid item>
                  <Link  variant="body2">
                    {"Don't have an account? Sign Up"}
                  </Link>
                </Grid>
              </Grid> */}
            </form>
          </div>
          <Copyright />
        </Grid>
      </Grid>
    );
  }
}

Login.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default compose(
  withStyles(styles),
  withUser,
  withStore,
  withLocalization,
  withRouter
)(Login);
