import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import TextInput from "../shared/TextInput";
import Form from "../shared/form/Form";
import NumInput from "../shared/NumInput";
import FileInput from "../shared/FileInput";
import Switch from "../shared/Switch";
import Editor from "../shared/Editor";
import Select from "../shared/Select";
import config from "../../config";
import TechParamForm from "./TechParamForm";

import withDoby from "../../consumers/withDoby";
import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";
import _ from "lodash";

const styles = (theme) => ({
  nameTextInput: {
    display: "flex",
    flex: 1,
  },
  margin: {
    margin: theme.spacing.unit,
  },
});

const INIT_STATE = {
  name: "",
  catalog_id: "",
  ean: "",
  short_description: "",
  long_description: "",
  price_without_tax: 0,
  disabled: false,
  main: false,
  category: [],
  productsRef: [],
  _img: null,
  _thumbnail: null,
  tech_param: {},
};

class FormWrapper extends React.Component {
  constructor(props) {
    super(props);
    this.state = { ...(props.data || INIT_STATE), tech_param_forms: [] };
  }

  async componentDidMount() {
    const { tech_param, tech_param_forms } = this.state;

    try {
      const { getEnums } = this.props.store;
      const { getSubordinateProducts } = this.props.doby;

      const filter = [
        { field: "enumTypeId", op: "gte", value: 200 },
        { field: "enumTypeId", op: "lt", value: 300 },
        { field: "disabled", value: false },
      ];
      await getEnums(filter);
      await getSubordinateProducts();

      if (!_.isEmpty(tech_param) && tech_param_forms.length === 0)
        this.setTechParamForms(tech_param);
    } catch (e) {
      console.error(e);
    }
  }

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleSubmit = async () => {
    const { addProduct, refreshProducts, editProduct } = this.props.doby;

    try {
      if (this.state._id) {
        await editProduct(this.state);
      } else {
        await addProduct(this.state);
      }
      refreshProducts();
      this.setState({ ...INIT_STATE, tech_param_forms: [] });
      if (this.props.onSubmit) this.props.onSubmit();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  handleChangeTechParamForm = (enumTypeId, enumValueID) => {
    const { tech_param } = this.state;

    this.setState({
      tech_param: { ...tech_param, [enumTypeId]: enumValueID },
    });
  };

  handleAddTechParam = (event) => {
    event.preventDefault();
    this.addTechParamForm();
  };

  removeTechParam = (enumTypeId, inOrder) => {
    const { tech_param, tech_param_forms } = this.state;
    delete tech_param[enumTypeId];

    this.setState({
      tech_param,
      tech_param_forms: tech_param_forms.filter((p) => p.inOrder !== inOrder),
    });
  };

  addTechParamForm = (enumTypeID = null, enumValueID = null) => {
    const { tech_param_forms } = this.state;
    const { enumTypes, enums } = this.props.store;

    const enumTypesFiltered = enumTypes.data.filter(
      (e) => e._id > 200 && e._id < 300
    );

    let data = null;
    if (enumTypeID !== null) {
      data = {
        enumType: Number.parseInt(enumTypeID),
        enumValueData: enums.data.filter(
          (e) => e.enumTypeId === Number.parseInt(enumTypeID)
        ),
        enumValue: enumValueID,
        disabled: true,
      };
    }

    const inOrder = tech_param_forms.length;
    this.setState({
      tech_param_forms: [
        ...tech_param_forms,
        {
          inOrder,
          element: (
            <TechParamForm
              classes={this.props.classes}
              onChange={this.handleChangeTechParamForm}
              onDelete={this.removeTechParam}
              enumTypeData={enumTypesFiltered}
              inOrder={inOrder}
              data={data}
            />
          ),
        },
      ],
    });
  };

  setTechParamForms = (tech_param) => {
    const entries = Object.entries(tech_param);
    for (const [enumTypeID, enumValueID] of entries)
      this.addTechParamForm(enumTypeID, enumValueID);
  };

  render() {
    const { classes, disabled } = this.props;
    const { enums, enumTypes } = this.props.store;
    const { subordinateProducts } = this.props.doby;
    const {
      name,
      price_without_tax,
      category,
      productsRef,
      _img,
      _thumbnail,
      tech_param_forms,
      catalog_id,
      ean,
      short_description,
      long_description,
    } = this.state;

    const enumTypesFiltered = enumTypes.data.filter(
      (e) => e._id > 200 && e._id < 300
    );
    const disabledAddTechParamForm =
      tech_param_forms.length === enumTypesFiltered.length;

    return (
      <>
        <Form onSubmit={this.handleSubmit} inline disabled={disabled}>
          <TextInput
            value={name}
            field="name"
            onChange={this.handleChange}
            required
            disabled={disabled}
          />
          <TextInput
            value={short_description}
            field="short_description"
            onChange={this.handleChange}
            disabled={disabled}
          />
          <TextInput
            value={catalog_id}
            field="catalog_id"
            onChange={this.handleChange}
            required
            disabled={disabled}
          />
          <TextInput
            value={ean}
            field="ean"
            onChange={this.handleChange}
            disabled={disabled}
          />
          <div className={classes.nameTextInput}>
            <Select
              value={category}
              field="category"
              onChange={this.handleChange}
              data={enums.data.filter((e) => e.enumTypeId === 200)}
              textField="name"
              multiple
              fullWidth
              disabled={disabled}
            />
          </div>
          <NumInput
            value={price_without_tax}
            field="price_without_tax"
            onChange={this.handleChange}
            min={0}
            required
          />
          <div className={classes.nameTextInput}>
            <Select
              value={productsRef}
              field="productsRef"
              onChange={this.handleChange}
              data={subordinateProducts.data}
              textField="name"
              multiple
              fullWidth
              disabled={disabled}
            />
          </div>
          <Switch
            label="Zruen"
            value={this.state.disabled}
            field="disabled"
            onChange={this.handleChange}
          />
          <Switch
            label="Na hlavnej strnke"
            value={this.state.main}
            field="main"
            onChange={this.handleChange}
          />
          <FileInput
            mimeType="image/jpeg"
            value={_img}
            field="_img"
            onChange={this.handleChange}
            disabled={this.state._id || this.state._id === "" || disabled}
            height={config.imageSize}
          />
          <FileInput
            mimeType="image/jpeg"
            value={_thumbnail}
            field="_thumbnail"
            onChange={this.handleChange}
            disabled={this.state._id || this.state._id === "" || disabled}
            height={config.thumbnailSize}
          />
        </Form>

        <Editor
          field="long_description"
          value={long_description}
          onChange={this.handleChange}
          height={config.imageSize}
          disabled={disabled}
        />

        {tech_param_forms.map((p) => p.element)}

        <Button
          disabled={disabledAddTechParamForm}
          onClick={this.handleAddTechParam}
          className={classes.button}
        >
          + Prida technicky parameter
        </Button>
      </>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object,
  onSubmit: PropTypes.func,
  date: PropTypes.instanceOf(Date),
  disabled: PropTypes.bool,
};

export default compose(
  withStyles(styles),
  withStore,
  withDoby,
  withUser
)(FormWrapper);
