import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import { withLocalization } from './localization';

const styles = theme => ({
  formControl: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    minWidth: 80
  },
});

function NumInputComponent(props) {
  const { classes } = props;
  const label = props.label || props.translate('field.' +props.field);

  return (
    <FormControl 
      className={classes.formControl}
      required={props.required}
      fullWidth={props.fullWidth} 
      style={{width: props.width}}
    >
      <InputLabel htmlFor={props.field}>{label}</InputLabel>
      <Input 
        id={props.field} 
        value={props.value ? props.value.toString() : '0'} 
        onChange={(e) => props.onChange(props.field, Number(e.target.value))} 
        type="number" 
        disabled={props.disabled}
        inputProps={{
          min: props.min,
          max: props.max,
          step: props.step || "any"
        }}       
      />
    </FormControl>
  );
}

NumInputComponent.propTypes = {
  classes: PropTypes.object.isRequired,
  label: PropTypes.string,
  field: PropTypes.string.isRequired,
  value: PropTypes.number,
  onChange: PropTypes.func.isRequired,
  placeholder: PropTypes.string,
  min: PropTypes.number,
  max: PropTypes.number,
  step: PropTypes.number,
  required: PropTypes.bool,
  disabled: PropTypes.bool,
  fullWidth: PropTypes.bool,
  width: PropTypes.number
};

export default withLocalization(withStyles(styles)(NumInputComponent));