import React from 'react';
import Table from '../shared/table/Table';
import FormDialog from '../shared/FormDialog';
import Form from './Form';

import compose from '../../compose';
import withStore from '../../consumers/withStore';
import withUser from '../../consumers/withUser';
import { withLocalization } from '../shared/localization'

const ROWS = [
  { field: 'name', disablePadding: true },
  { field: '_enumType.name', type: 'enum', data: 'enumTypes', idField: "enumTypeId", sortable: false },
  { field: 'key', type: 'num' },
  { field: 'disabled', type: 'bool' },
];

const FILTERS = [
  {
    label: 'Sub', field: 'sub', data: [
      { _id: 1, name: "shop attrs", filters: [{ field: 'enumTypeId', op: 'gt', value: 200}, { field: 'enumTypeId', op: 'lt', value: 300 }] },
    ]
  }
];

class TableWrapper extends React.Component {
  constructor(props) {
    super(props);
    
    this.state = {
      x: null,
      isOpen: false
    }
  }

  handleChangeClient = (name, value) => {
    this.setState({ clientId: value });
  }

  handleClose = () => {
    this.setState({ isOpen: false });
  }

  handleOpen = () => {
    this.setState({ isOpen: true });
  }

  handleChange = async (filters, page, sort, render) => {
    try {
      return await this.props.store.getEnums(filters, sort, page, render);
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  onEdit = async (x) => {
    this.setState({ x, isOpen: true })
  }

  isDisabled = x => {
    return false;
  }

  render() {
    const { deleteEnum } = this.props.store;
    const { translate } = this.props;

    return (
      <div>
        <Table
          rows={ROWS}
          data='enums'
          onChange={this.handleChange}
          onDelete={deleteEnum}
          title={translate('enums')}
          onEdit={this.onEdit}
          store={this.props.store}
          filters={FILTERS}
          isDisabled={this.isDisabled}
          filename='enums'
        />
        <FormDialog
          title={translate('enum')}
          onClose={this.handleClose}
          isOpen={this.state.isOpen}
        >
          <Form
            data={this.state.x}
            onSubmit={this.handleClose}
          />
        </FormDialog>
      </div>
    );
  }
}

export default compose(
  withStore,
  withUser,
  withLocalization
)(TableWrapper);