import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import TextInput from "../shared/TextInput";
import Form from "../shared/form/Form";
import FileInput from "../shared/FileInput";
import NumInput from "../shared/NumInput";
import Select from "../shared/Select";
import config from "../../config";

import withDoby from "../../consumers/withDoby";
import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";

const styles = (theme) => ({
  nameTextInput: {
    display: "flex",
    flex: 1,
  },
});

const INIT_STATE = {
  name: "",
  description: "",
  typeRef: null,
  key: 0,
  _logo: "",
};

class FormWrapper extends React.Component {
  constructor(props) {
    super(props);
    this.state = props.data || INIT_STATE;
  }

  async componentDidMount() {
    try {
      const { getEnums } = this.props.store;
      const filter = [
        { field: "enumTypeId", value: 3 },
        { field: "disabled", value: false },
      ];
      await getEnums(filter);
    } catch (e) {
      console.error(e);
    }
  }

  handleChangeTypeRef = (name, value) => {
    this.setState({ [name]: { id: value } });
  };

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleSubmit = async (content) => {
    const { addPartner, refreshPartners, editPartner } = this.props.doby;
    const data = { ...this.state, content };

    try {
      if (this.state._id) {
        await editPartner(data);
      } else {
        await addPartner(data);
      }

      refreshPartners();
      this.setState(INIT_STATE);

      if (this.props.onSubmit) this.props.onSubmit();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { classes, disabled } = this.props;
    const { name, description, typeRef, key, _logo } = this.state;
    const { enums } = this.props.store;

    return (
      <Form onSubmit={this.handleSubmit} inline disabled={disabled}>
        <TextInput
          value={name}
          field="name"
          onChange={this.handleChange}
          required
          disabled={disabled}
        />
        <div className={classes.nameTextInput}>
          <TextInput
            value={description}
            field="description"
            onChange={this.handleChange}
            required
            fullWidth
            disabled={disabled}
          />
        </div>
        <Select
          value={typeRef ? typeRef.id : null}
          field="typeRef"
          onChange={this.handleChangeTypeRef}
          data={enums.data}
          textField="name"
          required
          disabled={disabled}
        />
        <NumInput value={key} field="key" onChange={this.handleChange} />

        <FileInput
          mimeType="image/jpeg"
          value={_logo}
          field="_logo"
          onChange={this.handleChange}
          disabled={this.state._id || disabled}
          height={config.logoSize}
        />
      </Form>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object,
  onSubmit: PropTypes.func,
  date: PropTypes.instanceOf(Date),
  disabled: PropTypes.bool,
};

export default compose(
  withStyles(styles),
  withStore,
  withDoby,
  withUser
)(FormWrapper);
