import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import TextInput from "../shared/TextInput";
import Form from "../shared/form/Form";
import NumInput from "../shared/NumInput";

import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";

const styles = (theme) => ({
  nameTextInput: {
    display: "flex",
    flex: 1,
  },
});

const INIT_STATE = {
  name: "",
  content: "",
  key: 0,
};

class FormWrapper extends React.Component {
  constructor(props) {
    super(props);

    this.state = props.data || INIT_STATE;
  }

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleSubmit = async () => {
    const { addTemplate, refreshTemplates, editTemplate } = this.props.store;

    try {
      if (this.state._id) {
        await editTemplate(this.state);
      } else {
        await addTemplate(this.state);
      }

      refreshTemplates();
      this.setState(INIT_STATE);

      if (this.props.onSubmit) this.props.onSubmit();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { classes, disabled } = this.props;
    const { name, content, key } = this.state;

    return (
      <Form onSubmit={this.handleSubmit} inline disabled={disabled}>
        <div className={classes.nameTextInput}>
          <TextInput
            value={name}
            field="name"
            onChange={this.handleChange}
            fullWidth
            required
            disabled={disabled}
          />
        </div>
        <div className={classes.nameTextInput}>
          <TextInput
            value={content}
            field="content"
            onChange={this.handleChange}
            fullWidth
            required
            disabled={disabled}
          />
        </div>
        <NumInput
          value={key}
          field="key"
          onChange={this.handleChange}
          min={0}
          step={1}
          required
        />
      </Form>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object,
  onSubmit: PropTypes.func,
  date: PropTypes.instanceOf(Date),
  disabled: PropTypes.bool,
};

export default compose(withStyles(styles), withStore, withUser)(FormWrapper);
