import React from "react";
import StoreContext from "../contexts/StoreContext";
import { storage } from "kd-web";
import * as storeUtils from "../utils/storeUtils";

let _isInit;
class StoreProvider extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      enums: {
        total: 0,
        data: [],
        filters: [],
        page: {},
        isLoading: false,
      },
      enumTypes: {
        total: 6,
        data: [
          { _id: 1, name: "blog" },
          { _id: 3, name: "partner" },
          { _id: 5, name: "country" },
          { _id: 200, name: "shop" },
          { _id: 201, name: "size" },
          { _id: 202, name: "sex" },
        ],
      },
      templates: {
        total: 0,
        data: [],
        filters: [],
        page: null,
        isLoading: false,
      },
      transports: {
        total: 0,
        data: [],
        filters: [],
        page: null,
        isLoading: false,
      },
    };
  }

  getEnums = async (filters, sort, page, render = true) => {
    try {
      if (!render) {
        return await storeUtils.getEnums(
          this.state.enumTypes.data,
          filters,
          sort,
          page
        );
      }

      this.setState((state) => ({
        enums: { ...state.enums, isLoading: true },
      }));
      const enums = await storeUtils.getEnums(
        this.state.enumTypes.data,
        filters,
        sort,
        page
      );

      enums.isLoading = false;
      this.setState({ enums });
      return enums;
    } catch (error) {
      throw error;
    }
  };

  refreshEnums = async () => {
    try {
      this.setState((state) => ({
        enums: { ...state.enums, isLoading: true },
      }));
      const { filters, sort, page } = this.state.enums;
      const enums = await storeUtils.getEnums(
        this.state.enumTypes.data,
        filters,
        sort,
        page
      );
      enums.isLoading = false;
      this.setState({ enums });
    } catch (error) {
      throw error;
    }
  };

  getTemplates = async (filters, sort, page, render = true) => {
    try {
      if (!render) {
        return await storeUtils.getTemplates(filters, sort, page);
      }

      this.setState((state) => ({
        templates: { ...state.templates, isLoading: true },
      }));
      const templates = await storeUtils.getTemplates(filters, sort, page);

      templates.isLoading = false;
      this.setState({ templates });
      return templates;
    } catch (error) {
      throw error;
    }
  };

  refreshTemplates = async () => {
    try {
      this.setState((state) => ({
        templates: { ...state.templates, isLoading: true },
      }));
      const { filters, sort, page } = this.state.enums;
      const templates = await storeUtils.getTemplates(filters, sort, page);
      templates.isLoading = false;
      this.setState({ templates });
    } catch (error) {
      throw error;
    }
  };

  getTransports = async (filters, sort, page, render = true) => {
    try {
      if (!render) {
        return await storeUtils.getTransports(filters, sort, page);
      }

      this.setState((state) => ({
        transports: { ...state.transports, isLoading: true },
      }));
      const transports = await storeUtils.getTransports(filters, sort, page);

      transports.isLoading = false;
      this.setState({ transports });
      return transports;
    } catch (error) {
      throw error;
    }
  };

  refreshTransports = async () => {
    try {
      this.setState((state) => ({
        transports: { ...state.transports, isLoading: true },
      }));
      const { filters, sort, page } = this.state.transports;
      const transports = await storeUtils.getTransports(filters, sort, page);
      transports.isLoading = false;
      this.setState({ transports });
    } catch (error) {
      throw error;
    }
  };

  render() {
    const { children } = this.props;
    if (!_isInit && storage.getUser()) {
      _isInit = true;
    }

    return (
      <StoreContext.Provider
        value={{
          // ENUMS
          getEnums: this.getEnums,
          refreshEnums: this.refreshEnums,
          addEnum: storeUtils.addEnum,
          editEnum: storeUtils.editEnum,
          deleteEnum: storeUtils.deleteEnum,
          // TEMPLATES
          getTemplates: this.getTemplates,
          refreshTemplates: this.refreshTemplates,
          addTemplate: storeUtils.addTemplate,
          editTemplate: storeUtils.editTemplate,
          deleteTemplate: storeUtils.deleteTemplate,
          getFile: storeUtils.getFile,
          // TRANSPORTS
          getTransports: this.getTransports,
          refreshTransports: this.refreshTransports,
          addTransport: storeUtils.addTransport,
          editTransport: storeUtils.editTransport,
          deleteTransport: storeUtils.deleteTransport,

          ...this.state,
        }}
      >
        {children}
      </StoreContext.Provider>
    );
  }
}

export default StoreProvider;
