import React from "react";
import UserContext from "../contexts/UserContext";
import * as authUtils from "../utils/authUtils";
import * as mailUtils from "../utils/mailUtils";
import { storage } from "kd-web";

class UserProvider extends React.Component {
  state = {
    user: storage.getUser(),
    msg: null,
    users: {
      total: 0,
      data: [],
      filters: [],
      page: null,
      isLoading: false,
    },
    secret: null,
  };

  showMsg = (msg) => {
    this.setState({ msg });
  };

  showError = (error) => {
    this.setState({ msg: error.message });
  };

  hideMsg = () => {
    this.setState({ msg: null });
  };

  auth = async (username, password, token) => {
    try {
      const user = await authUtils.auth(username, password, token);
      this.setState({ user });

      return user;
    } catch (error) {
      throw error;
    }
  };

  registerUser = async (data) => {
    try {
      await authUtils.registerUser(data);
    } catch (error) {
      throw error;
    }
  };

  generateSecret = async () => {
    try {
      const secret = await authUtils.generateSecret();
      this.setState({ secret });
    } catch (error) {
      throw error;
    }
  };

  oAuth = async (provider, code) => {
    try {
      const user = await authUtils.oAuth(provider, code);
      this.setState({ user });
      return user;
    } catch (error) {
      throw error;
    }
  };

  updateOauth = async (provider, code) => {
    try {
      const user = await authUtils.updateOauth(provider, code);
      this.setState({ user });
    } catch (error) {
      throw error;
    }
  };

  updateUser = async (id, data) => {
    try {
      const user = await authUtils.updateUser(id, data);
      this.setState({ user });
      return user;
    } catch (error) {
      throw error;
    }
  };

  checkUser = async (username) => {
    try {
      return await authUtils.checkUser(username);
    } catch (error) {
      throw error;
    }
  };

  reset = async (username) => {
    try {
      await authUtils.reset(username);
    } catch (error) {
      throw error;
    }
  };

  getUsers = async (filters, sort, page, render = true) => {
    try {
      if (!render) {
        return await authUtils.getUsers(filters, sort, page);
      }

      this.setState((state) => ({
        users: { ...state.users, isLoading: true },
      }));
      const users = await authUtils.getUsers(filters, sort, page);
      users.isLoading = false;
      this.setState({ users });
      return users;
    } catch (error) {
      throw error;
    }
  };

  hasPermission = (permission) => {
    return this.state.user && (this.state.user.permissions & permission) > 0;
  };

  onNotification = () => {
    console.log("hub_notification");
  };

  initHub = () => {
    mailUtils.initHub(this.onNotification);

    setTimeout(async function () {
      //const res = await mailUtils.sendMock();
      // console.log(res);
    }, 2000);
  };

  render() {
    const { children } = this.props;

    return (
      <UserContext.Provider
        value={{
          auth: this.auth,
          registerUser: this.registerUser,
          hideMsg: this.hideMsg,
          showMsg: this.showMsg,
          showError: this.showError,
          generateSecret: this.generateSecret,
          oAuth: this.oAuth,
          updateOauth: this.updateOauth,
          getUsers: this.getUsers,
          getUser: authUtils.getUser,
          updateUser: this.updateUser,
          checkUser: this.checkUser,
          hasPermission: this.hasPermission,
          reset: this.reset,
          initHub: this.initHub,
          sendMail: mailUtils.send,
          ...this.state,
        }}
      >
        {children}
      </UserContext.Provider>
    );
  }
}

export default UserProvider;
