import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { withLocalization } from "../../shared/localization";
import getSharedStyles from "../../../style";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormControl from "@material-ui/core/FormControl";
import FilledInput from "@material-ui/core/FilledInput";
import InputLabel from "@material-ui/core/InputLabel";
import classNames from "classnames";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  textInput: {
    background: "white!important",
    border: "1px solid #E7E7E7",
    borderRadius: "6px",
    "&:before": {
      display: "none",
    },
    "&:after": {
      display: "none",
    },
  },
  inputLabel: {
    color: "rgba(0,0,0,0.3)!important",
    fontSize: "0.875rem",
  },
  inputRoot: {
    padding: "23px 12px 8px",
    fontSize: "0.875rem",
    color: "#333442",
  },
});

function TextFieldComponent(props) {
  const { classes } = props;
  const label = props.label || props.translate("field." + props.field);

  return (
    <FormControl
      className={classNames(props.className)}
      variant="filled"
      fullWidth={props.fullWidth}
      required={props.required}
      error={props.errorText && props.errorText !== ""}
    >
      <InputLabel
        htmlFor="component-filled"
        className={classNames(classes.inputLabel)}
      >
        {label}
      </InputLabel>
      <FilledInput
        id="outlined-name"
        label={label}
        value={props.value || ""}
        onChange={(e) => props.onChange(props.field, e.target.value)}
        margin="normal"
        variant="filled"
        className={classes.textInput}
        classes={{ input: classes.inputRoot }}
        rows={props.rows}
        multiline={props.multiline}
        onBlur={props.onBlur}
        type={props.type === "new-password" ? "password" : props.type}
        autoComplete={props.type === "new-password" ? "new-password" : null}
      />
      {props.errorText && <FormHelperText className={classNames(classes.textError)}>{props.errorText}</FormHelperText>}
    </FormControl>
  );
}

TextFieldComponent.propTypes = {
  classes: PropTypes.object.isRequired,
  className: PropTypes.string,
  label: PropTypes.string,
  field: PropTypes.string.isRequired,
  value: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  placeholder: PropTypes.string,
  fullWidth: PropTypes.bool,
  required: PropTypes.bool,
  type: PropTypes.string,
  disabled: PropTypes.bool,
  multiline: PropTypes.bool,
  rows: PropTypes.number,
  startAdornment: PropTypes.string,
  onBlur: PropTypes.func,
  errorText: PropTypes.string,
  width: PropTypes.number,
};

export default withLocalization(withStyles(styles)(TextFieldComponent));
