import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import withUser from "../../../consumers/withUser";
import { withLocalization } from "../../shared/localization";
import withStore from "../../../consumers/withStore";
import withUtils from "../../shared/utils/withUtils";
import compose from "../../../compose";
import getSharedStyles from "../../../style";
import classNames from "classnames";

import Button from "../../dobyComponents/form/Button";
import Switch from "../../dobyComponents/form/Switch";

import TextField from "../../dobyComponents/form/TextField";
import TelField from "../../dobyComponents/form/TelField";
import SelectField from "../../dobyComponents/form/SelectField";
import Checkbox from "../../dobyComponents/form/CheckboxField";

import FacebookConnection from "../../dobyComponents/socialConnections/FacebookConnection";
import StravaConnection from "../../dobyComponents/socialConnections/StravaConnection";

import _ from "lodash";
import config from "../../../config";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  profileImgHolder: {
    width: "120px",
    height: "120px",
    minWidth: "120px",
    overflow: "hidden",
    position: "relative",
  },
  profileImg: {
    // width: '120px',
    borderRadius: "100px",
    border: "3px solid white",
    width: "100%",
    height: "100%",
    position: "absolute",
    left: 0,
    right: 0,
    objectFit: "cover",
  },
  avatar: {},
  socialConnectCard: {
    background: "white",
  },
  socialIcon: {
    width: "30px",
    height: "30px",
  },
});

const INIT_STATE = {
  password: {
    value: "",
    errorText: "",
  },
  repeatedPassword: {
    value: "",
    errorText: "",
  },
  email: {
    value: "",
    isLoading: false,
    errorText: "",
  },
  name: "",
  username: {
    value: "",
    isLoading: false,
    errorText: "",
  },
  tel: {
    value: "",
    errorText: "",
  },
  address: "",
  city: "",
  postCode: "",
  country: "",
  otp: false,
  avatar: null,
  notifications: true,
  notifyAboutMe: false,
  notifyAboutFollowers: false,
  notifyNews: false,
  telPreffix: null,
};

const preffixData = [
  { preffix: "+421", _id: 1 },
  { preffix: "+43", _id: 2 },
];

const handleInputTelNumber = (tel) => {
  if (tel === undefined || tel === "") return { value: "", preffix: null };

  const preffix = tel.substring(0, 4);
  switch (preffix) {
    case "+421":
    case "+420":
      return {
        value: tel.substring(4),
        preffix: preffixData.find((p) => p.preffix === preffix)._id,
      };
    default:
      return {
        value: tel.substring(3),
        preffix: preffixData.find((p) => p.preffix === tel.substring(0, 3))._id,
      };
  }
};

const buildState = (user) => {
  const { value, preffix } = handleInputTelNumber(user.tel);

  return {
    ...INIT_STATE,
    ...user,
    email: { ...INIT_STATE.email, value: user.email },
    username: { ...INIT_STATE.username, value: user.username },
    tel: { ...INIT_STATE.tel, value },
    telPreffix: preffix,
    isOpenSecretDialog: false,
    avatarChanged: false,
  };
};

class ProfileSettings extends React.Component {
  constructor(props) {
    super(props);
    this.state = buildState(this.props.user.user);
  }

  componentDidMount() {
    this.fetchData();
  }

  fetchData = async () => {
    const { getEnums } = this.props.store;
    const filter = [
      { field: "enumTypeId", value: 5 },
      { field: "disabled", value: false },
    ];
    await getEnums(filter);
  };

  handleChange = (name, value) => {
    this.setState((state) => _.set(state, name, value));
  };

  handleChangeCheckBox = (event) => {
    const { name, checked } = event.target;
    this.setState((state) => _.set(state, name, checked));
  };

  handleChangeAvatar = (event) => {
    event.preventDefault();
    const reader = new FileReader();
    reader.onloadend = async () => {
      let data = await this.props.utils.resize(reader.result, 320);
      data = data.split(",").pop();
      this.setState({ avatar: data, avatarChanged: true });
    };
    reader.readAsDataURL(event.target.files[0]);

    // zabezpecuje volanie onChange aj pri uploadovani rovnakeho suboru
    // -> po ulozeni formularu nezavolalo onChange opat, v pripade nahrania
    // -> rovnakeho suboru ako predtym
    event.target.value = "";
  };

  handleChangePreffix = (name, value) => {
    if (value === "") this.handleChange("tel.value", "");
    this.handleChange(name, value);
  };

  handleRemoveAvatar = () => {
    this.setState({ avatar: null, avatarChanged: true });
  };

  validTelephone = () => {
    const { translate } = this.props;
    const { tel } = this.state;

    const telSuffix = tel.value.replace(/\s/g, "");
    const regex = new RegExp("^[0-9]{9}$");
    const isValid = telSuffix === "" || regex.test(telSuffix);

    const errorText = isValid ? "" : translate("field.telNotValid");

    this.setState((state) => ({
      tel: { ...state.tel, errorText },
    }));

    return isValid;
  };

  validPasswords = () => {
    const { password, repeatedPassword } = this.state;
    const { translate } = this.props;

    const regex = new RegExp(config.passwordPattern);
    const isValidPassword = !password.value || regex.test(password.value);
    const errorTextPassword = isValidPassword
      ? ""
      : translate("field.passwordWeak");

    const isValidRepeatedPassword = password.value === repeatedPassword.value;
    const errorTextRepeatedPassword = isValidRepeatedPassword
      ? ""
      : translate("field.passwordNoMatch");

    this.setState((state) => ({
      password: { ...state.password, errorText: errorTextPassword },
      repeatedPassword: {
        ...this.state.repeatedPassword,
        errorText: errorTextRepeatedPassword,
      },
    }));

    return isValidPassword && isValidRepeatedPassword;
  };

  validEmail = async () => {
    const { translate } = this.props;
    const { email } = this.state;

    this.setState((state) => ({
      email: {
        ...state.email,
        isLoading: true,
      },
    }));

    const isValid = !email.value || (await this.isValid(email.value));
    const errorText = isValid ? "" : translate("field.emailInUse");

    this.setState((state) => ({
      email: { ...state.email, errorText, isLoading: false },
    }));

    return isValid;
  };

  isValid = async (username) => {
    const { user } = this.props;

    const ids = await this.props.user.checkUser(username);
    return (
      ids.length === 0 || (ids.length === 1 && ids[0]._id === user.user._id)
    );
  };

  validUsername = async () => {
    const { translate } = this.props;
    const { username } = this.state;

    this.setState((state) => ({
      username: {
        ...state.username,
        isLoading: true,
      },
    }));

    const isValid = !username.value || (await this.isValid(username.value));
    const errorText = isValid ? "" : translate("field.usernameInUse");

    this.setState((state) => ({
      username: { ...state.username, errorText, isLoading: false },
    }));

    return isValid;
  };

  handleSubmit = async () => {
    const {
      password,
      name,
      username,
      email,
      tel,
      address,
      city,
      postCode,
      country,
      otp,
      avatar,
      avatarChanged,
      telPreffix,
    } = this.state;

    const isValid =
      this.validTelephone() &&
      this.validPasswords() &&
      (await this.validEmail()) &&
      (await this.validUsername());
    if (!isValid) {
      return;
    }

    const preffix = telPreffix
      ? preffixData.find((p) => p._id === telPreffix).preffix
      : "";
    try {
      await this.props.user.updateUser(this.state._id, {
        email: email.value,
        password: password.value,
        name,
        username: username.value,
        tel: preffix + tel.value.replace(/\s/g, ""),
        address,
        city,
        postCode,
        country,
        otp,
        avatar: avatarChanged ? avatar : null,
      });
      this.setState(buildState(this.props.user.user));
      this.props.user.showMsg("Ok.");
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  goToProfile = () => this.props.history.push(`/profile`);

  render() {
    const { classes, translate, locale, disabled } = this.props;
    const { user } = this.props.user;
    const connectTitle = translate("connect");
    const { enums } = this.props.store;
    const {
      email,
      tel,
      otp,
      address,
      city,
      postCode,
      country,
      avatar,
      name,
      bio,
      notifications,
      notifyAboutMe,
      notifyAboutFollowers,
      notifyNews,
      telPreffix,
    } = this.state;

    return (
      <>
        <div>
          <Grid container direction="row" justify="center" alignItems="center">
            <Grid item xs={12} className={classNames(classes.mt5)}>
              <div className={classNames(classes.container)}>
                <div
                  className={classNames(classes.textXs)}
                  onClick={this.goToProfile}
                >
                  {translate("back")}
                </div>
              </div>
            </Grid>

            <Grid item xs={12}>
              <div className={classNames(classes.container, classes.mt2)}>
                <div
                  className={classNames(classes.fontBold, classes.textMonoH4)}
                >
                  {translate("settings")}
                </div>
                <div
                  className={classNames(
                    classes.dFlex,
                    classes.alignItemsCenter,
                    classes.w100,
                    classes.mt3
                  )}
                >
                  {/* <img src="./img/user-img.jpg" className={classNames(classes.profileImg)} /> */}
                  <div className={classNames(classes.profileImgHolder)}>
                    <img
                      alt="avatarImg"
                      src={
                        avatar
                          ? "data:image/jpeg;base64," + avatar
                          : "./img/user-img.jpg"
                      }
                      className={classNames(classes.profileImg)}
                    />
                  </div>

                  <div
                    className={classNames(
                      classes.flexCol,
                      classes.flexGrow,
                      classes.ml5
                    )}
                  >
                    <div className={classNames(classes.textXs2)}>
                      {translate("descSettingsProfilePhoto")}
                    </div>
                    <div className={classNames(classes.mt3, classes.w100)}>
                      <Button onClick={() => {}} fullwidth>
                        <label for="avatar-upload">
                          {translate("changePhoto")}
                        </label>
                        <input
                          hidden
                          type="file"
                          accept="image/jpeg"
                          onChange={this.handleChangeAvatar}
                          id="avatar-upload"
                        />
                      </Button>
                    </div>
                    <div className={classNames(classes.mt3, classes.w100)}>
                      <Button
                        fullwidth
                        outline
                        onClick={this.handleRemoveAvatar}
                      >
                        {translate("removePhoto")}
                      </Button>
                    </div>
                  </div>
                </div>
              </div>
            </Grid>

            <Grid item xs={12} className={classNames(classes.mt6)}>
              <div className={classNames(classes.container)}>
                <FacebookConnection config={config.fb} />
                <StravaConnection
                  config={config.strava}
                  className={classNames(classes.mt3)}
                />
              </div>
            </Grid>

            <Grid item xs={12} className={classNames(classes.mt6)}>
              <div className={classNames(classes.container)}>
                <div
                  className={classNames(classes.fontBold, classes.textMonoH4)}
                >
                  {translate("personData")}
                </div>

                <Grid
                  container
                  direction="row"
                  justify="center"
                  alignItems="center"
                >
                  <Grid
                    item
                    xs={12}
                    className={classNames(classes.mt4, classes.pr1)}
                  >
                    <TextField
                      onChange={this.handleChange}
                      fullWidth
                      required
                      value={name}
                      field="name"
                      disabled={disabled}
                    />
                  </Grid>
                  {/* <Grid item xs={6} className={classNames(classes.mt4, classes.pl1)}>
                                        <TextField
                                            onChange={this.handleChange}
                                            fullWidth
                                            required
                                            value={this.state.surname}
                                            field="surname"
                                        />
                                    </Grid> */}
                  <Grid item xs={12} className={classNames(classes.mt4)}>
                    <TextField
                      onChange={this.handleChange}
                      fullWidth
                      required
                      value={bio}
                      field="bio"
                      rows={3}
                      multiline
                      disabled={disabled}
                    />
                  </Grid>
                </Grid>

                <div
                  className={classNames(
                    classes.fontBold,
                    classes.textMonoH4,
                    classes.mt6
                  )}
                >
                  {translate("delivery_details")}
                </div>

                <Grid
                  container
                  direction="row"
                  justify="center"
                  alignItems="center"
                >
                  <Grid item xs={12} className={classNames(classes.mt4)}>
                    <TextField
                      onChange={this.handleChange}
                      fullWidth
                      required
                      value={email.value}
                      field="email.value"
                      type="email"
                      disabled={disabled || email.isLoading}
                      onBlur={this.validEmail}
                      errorText={email.errorText}
                    />
                  </Grid>
                  {/* <Grid item xs={12} className={classNames(classes.mt4)}>
                                        <TextField
                                            fullWidth
                                            value={tel.value}
                                            field="tel.value"
                                            onChange={this.handleChange}
                                            onBlur={this.validTelephone}
                                            disabled={disabled}
                                            errorText={tel.errorText}
                                        />
                                    </Grid> */}

                  <Grid item xs={4} className={classNames(classes.mt2)}>
                    <SelectField
                      onChange={this.handleChangePreffix}
                      fullWidth
                      data={preffixData}
                      value={telPreffix}
                      field="telPreffix"
                      textField="preffix"
                      disabled={false}
                    />
                  </Grid>

                  <Grid item xs={8} className={classNames(classes.mt2)}>
                    <TelField
                      value={tel.value}
                      fullWidth
                      field="tel.value"
                      onChange={this.handleChange}
                      onBlur={this.validTelephone}
                      errorText={tel.errorText}
                      disabled={disabled}
                    />
                  </Grid>

                  <Grid item xs={12} className={classNames(classes.mt4)}>
                    <SelectField
                      onChange={this.handleChange}
                      fullWidth
                      data={enums.data}
                      value={country}
                      field="country"
                      textField="name"
                      disabled={disabled}
                    />
                  </Grid>
                  <Grid
                    item
                    xs={8}
                    className={classNames(classes.mt4, classes.pr2)}
                  >
                    <TextField
                      fullWidth
                      value={city}
                      field="city"
                      type="city"
                      onChange={this.handleChange}
                      disabled={disabled}
                    />
                  </Grid>
                  <Grid item xs={4} className={classNames(classes.mt4)}>
                    <TextField
                      fullWidth
                      value={postCode}
                      field="postCode"
                      type="postCode"
                      onChange={this.handleChange}
                      disabled={disabled}
                    />
                  </Grid>
                  <Grid item xs={12} className={classNames(classes.mt4)}>
                    <TextField
                      fullWidth
                      value={address}
                      field="address"
                      type="address"
                      onChange={this.handleChange}
                      disabled={disabled}
                    />
                  </Grid>
                </Grid>
              </div>
            </Grid>

            <Grid item xs={12} className={classNames(classes.mt6)}>
              <div className={classNames(classes.container)}>
                <div
                  className={classNames(
                    classes.dFlex,
                    classes.w100,
                    classes.alignItemsCenter,
                    classes.justifyContentBetween
                  )}
                >
                  <div className={classNames(classes.fontBold)}>
                    {translate("notifications")}
                  </div>
                  <div>
                    <Switch
                      value={notifications}
                      field="notifications"
                      onChange={this.handleChange}
                      //disabled={disabled}
                    />
                  </div>
                </div>

                <div className={classNames(classes.dBlock)}>
                  <Checkbox
                    onChange={this.handleChange}
                    fullWidth
                    required
                    value={notifyAboutMe}
                    field="notifyAboutMe"
                    label={translate("notifyAboutMe")}
                    disabled={!notifications || disabled}
                  />
                </div>

                <div className={classNames(classes.dBlock)}>
                  <Checkbox
                    onChange={this.handleChange}
                    fullWidth
                    required
                    value={notifyAboutFollowers}
                    field="notifyAboutFollowers"
                    label={translate("notifyAboutFollowers")}
                    disabled={!notifications || disabled}
                  />
                </div>

                <div className={classNames(classes.dBlock)}>
                  <Checkbox
                    onChange={this.handleChange}
                    fullWidth
                    required
                    value={notifyNews}
                    field="notifyNews"
                    label={translate("notifyNews")}
                    disabled={!notifications || disabled}
                  />
                </div>
              </div>
            </Grid>

            <Grid item xs={12} className={classNames(classes.mt3, classes.mb5)}>
              <div className={classNames(classes.container)}>
                <Button fullwidth onClick={this.handleSubmit}>
                  {translate("saveSettings")}
                </Button>
              </div>
            </Grid>
          </Grid>
        </div>
      </>
    );
  }
}

ProfileSettings.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default compose(
  withStyles(styles),
  withUser,
  withLocalization,
  withStore,
  withUtils
)(ProfileSettings);
