import React from "react";
import StravaIcon from "../icons/StravaIcon";
import Button from '@material-ui/core/Button';

const GithubButton = props => {
  const handleClick = () => {
    const redirect = process.env.REACT_APP_HOMEPAGE;
    // const redirect = 'http://localhost:3000';
    const url = `https://www.strava.com/oauth/authorize?client_id=${props.config.id}&response_type=code&scope=read,activity:read&redirect_uri=${redirect}?provider=4`;
    window.location.replace(url);
  };

  if(!props.config) {
    return null;
  }

  return (
    <Button fullWidth={props.fullWidth} variant={props.variant} color="primary" onClick={handleClick} disabled={props.disabled}>
      <StravaIcon fontSize="large" /> <span style={props.fullWidth ? { width: 200} : null}>{props.title}</span>
    </Button>
  );
};

export default GithubButton;
