import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../style';
import classNames from 'classnames';
import Grid from '@material-ui/core/Grid';
import compose from '../../compose';
import withUser from '../../consumers/withUser';
import Flickity from "react-flickity-component";
import Button from '../dobyComponents/form/Button';

const styles = theme => ({
    ...getSharedStyles(theme),
    introSection: {
        minHeight: 'calc(100vh - 60px)',
        height: 'calc(100vh - 60px)',
        width: '100%',
        background: 'url("/img/intro-bg.jpg")',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        position: 'relative',
        '&:after': {
            content: "''",
            display: 'block',
            width: '100%',
            height: '100%',
            position: 'absolute',
            top: 0,
            left: 0,
            background: 'linear-gradient(0deg, rgba(0,0,0,0.35) 0%, rgba(0,0,0,0) 100%)'
        }
    },
    challengeLogo: {
        width: '100px',
        height: 'auto',
        zIndex: 4,
    },
    challengeIntroContent: {
        paddingBottom: '120px',
        zIndex: 4,
    },
    summitsDetail: {
        minHeight: 'calc(100vh - 60px)',
        height: 'calc(100vh - 60px)',
        width: '100%',
        background: 'url("/img/challenge-card.jpg")',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        position: 'relative',
        '&:after': {
            content: "''",
            display: 'block',
            width: '100%',
            height: '100%',
            position: 'absolute',
            top: 0,
            left: 0,
            background: 'linear-gradient(0deg, rgba(0,0,0,0.35) 0%, rgba(0,0,0,0) 100%)'
        }
    },
    summitsDetailContent: {
        zIndex: 4,
        position: 'relative',        
    },  
    summitsCarousel: {
        '& .flickity-viewport': {
            //padding: '7px'
        },
        '& .carousel-cell:first-child': {
            paddingLeft: '1.25rem'
        },
        '& .carousel-cell:last-child': {
            //paddingRight: '0.5rem',
        }
    },
    summitCarouselItem: {
        background: 'rgba(255,255,255,0.5)',
        whiteSpace: 'nowrap',
        minWidth: '160px',
    },
    summitCarouselItemActive: {
        background: 'rgba(255,255,255,1)', 
    }
});

const flickityOptions = {
    cellAlign: 'center',
    contain: true,
    prevNextButtons: false,
    pageDots: false
}


class ChallengeDetail extends React.Component {
    constructor(props) {
        super(props);       
        this.state = {
            
        }
    }

    renderSummits = () => {
        const { classes } = this.props;
        let summits = [
            {id: 0, name: 'Vychodna vysoka', altitude: 2495},
            {id: 1, name: 'Koprovsky stit', altitude: 2495},
            {id: 2, name: 'Rysy', altitude: 2495},
            {id: 3, name: 'Krivan', altitude: 2495, active: true},
            {id: 4, name: 'Slavkosky stit', altitude: 2495},
            {id: 5, name: 'Jahnaci stit', altitude: 2495},
        ]

        return summits.map(summit=>{
            return (
                <div className={classNames("carousel-cell")}>
                    <div className={classNames(classes.summitCarouselItem, summit.active && classes.summitCarouselItemActive, classes.flexCol, classes.alignItemsCenter, classes.roundedMd, classes.px5, classes.py2, classes.mr5)}>
                        <div className={classNames(classes.fontBold)}>
                            {summit.name}
                        </div>
                        <div className={classNames()}>
                            {summit.altitude}
                        </div>
                    </div>
                </div>              
            )
        })
    }

    render() {
        const {classes} = this.props;
        return (
            <>
                <div className={classNames(classes.introSection)}>
                    <div className={classNames(classes.container, classes.pt5, classes.h100)}>
                        <div className={classNames(classes.flexCol, classes.h100, classes.w100, classes.justifyContentBetween)}>
                            <div className={classNames(classes.challengeLogoHolder)}>
                                <img src={"./img/dobylogo.png"} className={classNames(classes.challengeLogo)}/>
                            </div>                          
                            <div className={classNames(classes.flexCol, classes.challengeIntroContent)}>
                                <h3 className={classNames(classes.fontBold, classes.textWhite)}>
                                    Doby Tatry 2021
                                </h3>
                                <div className={classNames(classes.textWhite, classes.mt2)}>
                                    6 najkrajch vrcholov z Vysokch Tatier. Vysok Tatry s povaovan za najkrajie pohorie na Slovensku a s touto vzvou 
                                    spozn to najlepie z nich. 
                                </div>
                                <div className={classNames(classes.fontBold, classes.textWhite, classes.mt4)}>
                                    Termin
                                </div>
                                <div className={classNames(classes.textWhite, classes.mt1)}>
                                    Od 1.7 do 30.9.2021
                                </div>
                                <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.mt2,)}>
                                    <div className={classNames(classes.flexCol)}>
                                        <div className={classNames(classes.fontBold, classes.textWhite, classes.mt4)}>
                                            Obtiaznost
                                        </div>
                                        <div className={classNames(classes.textWhite, classes.mt1)}>
                                            Narocna
                                        </div>  
                                    </div>
                                    <div className={classNames(classes.flexCol, classes.ml6)}>
                                        <div className={classNames(classes.fontBold, classes.textWhite, classes.mt4)}>
                                            Casova narocnost
                                        </div>
                                        <div className={classNames(classes.textWhite, classes.mt1)}>
                                            6 celodennych tur
                                        </div>  
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className={classNames(classes.summitsDetail)}>
                    <div className={classNames(classes.flexCol, classes.h100, classes.justifyContentEnd, classes.summitsDetailContent)}>
                        <div className={classNames(classes.container)}>
                            <Grid
                                container
                                direction="row"
                                justify="center"
                                alignItems="center"
                            >
                                <Grid item xs={12} className={classNames(classes.mt4)}>
                                    <div className={classNames(classes.flexCol)}>
                                        <div className={classNames(classes.fontBold, classes.textWhite, classes.mt4)}>
                                            Obtiaznost
                                        </div>
                                        <div className={classNames(classes.textWhite, classes.mt1)}>
                                            Narocna
                                        </div>  
                                    </div>
                                </Grid>

                                <Grid item xs={12} className={classNames(classes.mt4)}>
                                    <div className={classNames(classes.flexCol)}>
                                        <div className={classNames(classes.fontBold, classes.textWhite, classes.mt4)}>
                                            Najkratsia trasa
                                        </div>
                                        <div className={classNames(classes.textWhite, classes.mt1)}>
                                            <span>7:06h</span><span className={classNames(classes.ml5)}>11,4km</span><span className={classNames(classes.ml5)}>+1 355m</span>
                                        </div>                                      
                                    </div>
                                </Grid>

                                <Grid item xs={12} className={classNames(classes.mt4)}>
                                    <div className={classNames(classes.textWhite, classes.mt4)}>
                                        Ikona Slovenska a mnohmi povaovan 
                                        za najkraj vrchol v Tatrch. Z jeho vrcholu je 
                                        ndhern vhad na cel Tatry a na Zpadn Tatry. Hovor sa, e kad Slovk, by ho mal aspo raz v ivote zdola.
                                    </div>
                                </Grid>                           
                            </Grid>
                        </div>

                        <div className={classNames(classes.summitsCarouselHolder, classes.mt5, classes.mb8)}>
                            <Flickity
                                className={classNames(classes.summitsCarousel)}
                                options={flickityOptions}
                                reloadOnUpdate={true}
                            >
                                {this.renderSummits()}
                            </Flickity>
                        </div>
                    </div>
                </div>

                <div className={classNames(classes.container)} style={{paddingBottom: '120px'}}>
                    <Grid
                        container
                        direction="row"
                        justify="center"
                        alignItems="center"
                    >
                        <Grid item xs={12} className={classNames(classes.mt4)}>
                            <Button fullwidth onClick={()=>this.props.history.push('challenge-checkout')}>Prijat vyzvu od 22</Button>
                        </Grid>
                    </Grid>
                </div>
            </>
        )
    }
} 



export default compose(
  withStyles(styles),
  withUser
)(ChallengeDetail);