import React from "react";
import PropTypes from "prop-types";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DatePicker from "../shared/DatePicker";
import NumInput from "../shared/NumInput";
import DialogActions from "@material-ui/core/DialogActions";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextInput from "./TextInput";
import Select from "./Select";
import moment from "moment";

export const buildFilters = (rows, f, state) => {
  const filters = [];

  if (state) {
    let value, field;

    f.forEach((x) => {
      field = x.field;
      value = state[field];
      if (value) {
        filters.push(...x.data.find((y) => y._id === value).filters);
      }
    });

    rows.forEach((x) => {
      switch (x.type) {
        case "num":
          field = x.field;
          value = state[field];
          if (value) {
            filters.push({ field, op: "eq", value });
          }
          break;

        case "bool":
          field = x.field;
          value = state[field];
          if (value) {
            filters.push({
              field,
              op: "eq",
              value: value === 1 ? true : false,
            });
          }
          break;

        case "date":
        case "datetime":
          field = x.field;
          value = state[field];
          if (value) {
            filters.push({
              field,
              op: "lte",
              value: moment(value).endOf("day").toDate(),
            });
            filters.push({
              field,
              op: "gte",
              value: moment(value).startOf("day").toDate(),
            });
          }
          break;

        case "enum":
        case "array":
          field = x.idField;
          value = state[field];
          if (value && value.length > 0) {
            filters.push({ field, op: "in", value });
          }
          break;

        default:
          field = x.field;
          value = state[field];
          if (value) {
            filters.push({ field, op: "regex", value });
          }
          break;
      }
    });
  }

  return filters;
};

class FilterDialog extends React.Component {
  constructor(props) {
    super(props);

    this.state = props.defaultState || {};
  }

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleClear = () => {
    const state = {};
    this.props.filters.forEach((x) => {
      state[x.field] = null;
    });

    this.props.rows.forEach((x) => {
      const field =
        x.type === "enum" || x.type === "array" ? x.idField : x.field;
      state[field] = null;
    });
    this.props.onConfirm(null);
    this.setState(state);
  };

  handleSubmit = () => {
    this.props.onConfirm(this.state);
  };

  buildFilter = (x) => {
    if (x.filterable === false) {
      return null;
    }

    switch (x.type) {
      case "enum":
      case "array":
        const textField = x.textField || x.field.split(".").pop();
        const data = this.props.store[x.data].data;
        return (
          <Select
            key={x.field}
            label={x.label}
            value={this.state[x.idField] || []}
            field={x.idField}
            onChange={this.handleChange}
            data={x.getData ? x.getData(data) : data}
            textField={textField}
            multiple
          />
        );
      case "bool":
        return (
          <Select
            key={x.field}
            label={x.label}
            value={this.state[x.field]}
            field={x.field}
            onChange={this.handleChange}
            data={[
              { _id: 1, name: "T" },
              { _id: -1, name: "F" },
            ]}
            textField={"name"}
          />
        );
      case "date":
      case "datetime":
        return (
          <DatePicker
            key={x.field}
            label={x.label}
            value={this.state[x.field]}
            field={x.field}
            onChange={this.handleChange}
          />
        );
      case "num":
        return (
          <NumInput
            key={x.field}
            label={x.label}
            value={this.state[x.field]}
            field={x.field}
            onChange={this.handleChange}
            min={0}
          />
        );
      default:
        return (
          <TextInput
            key={x.field}
            label={x.label}
            value={this.state[x.field]}
            field={x.field}
            onChange={this.handleChange}
          />
        );
    }
  };

  render() {
    const props = this.props;

    return (
      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        open={props.isOpen}
        onClose={props.onClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        fullWidth
      >
        <DialogTitle id="alert-dialog-title">Filters</DialogTitle>
        {this.props.filters.map((f) => (
          <Select
            value={this.state[f.field]}
            field={f.field}
            label={f.label}
            onChange={this.handleChange}
            data={f.data}
            textField="name"
          />
        ))}
        {props.rows.map((x) => this.buildFilter(x))}
        <DialogActions>
          <Button onClick={this.handleClear} color="primary">
            Clear
          </Button>
          <Button onClick={this.handleSubmit} color="primary" autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>
    );
  }
}

FilterDialog.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onConfirm: PropTypes.func.isRequired,
  rows: PropTypes.array.isRequired,
  store: PropTypes.object.isRequired,
  filters: PropTypes.array.isRequired,
  defaultState: PropTypes.object,
};

export default FilterDialog;
