import React from "react";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { DatePicker } from "material-ui-pickers";
import { withLocalization } from './localization';

const styles = theme => ({
  formControl: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    minWidth: 160
  },
});

const getMaskAndFormat = (locale) => {
  switch(locale) {
    case 'sk-SK': {
      return {
        format: "DD.M.YYYY",
        mask:  [/\d/, /\d/, ".", /\d/, ".", /\d/, /\d/, /\d/, /\d/]
      };
    }
  
    case 'en-US':
    default: {
      return {
        format: "MM/DD/YYYY",
        mask:  [/\d/, /\d/, "/", /\d/, /\d/, "/", /\d/, /\d/, /\d/, /\d/]
      };
    }
  }
}


function DatePickerComponent(props) {
  const { classes } = props;
  const { format, mask } = getMaskAndFormat(props.locale);

  const label = props.label || props.translate('field.' +props.field);
  return (
    <DatePicker
      className={classes.formControl}
      keyboard
      label={label}
      format={format}
      clearable={!props.required}
      mask={value =>
        // handle clearing outside if value can be changed outside of the component
        value ? mask : []
      }
      value={props.value || null}
      onChange={(value) => props.onChange(props.field, value ? value.toDate() : null)}
      disableOpenOnEnter
      animateYearScrolling={false}
      style={{flex: 1, display: 'flex'}}
      disabled={props.disabled}
      okLabel={props.translate('dialog.ok')}
      cancelLabel={props.translate('dialog.cancel')}
    />
  );
}

DatePickerComponent.propTypes = {
  classes: PropTypes.object.isRequired,
  label: PropTypes.string,
  field: PropTypes.string.isRequired,
  value: PropTypes.instanceOf(Date),
  onChange: PropTypes.func.isRequired,
  placeholder: PropTypes.string,
  required: PropTypes.bool,
  disabled: PropTypes.bool
};

export default withLocalization(withStyles(styles)(DatePickerComponent));