import React from "react";
import GoogleIcon from "../icons/GoogleIcon";
import Button from '@material-ui/core/Button';

const GithubButton = props => {
  const handleClick = () => {
    const url = `https://accounts.google.com/o/oauth2/v2/auth?scope=https://www.googleapis.com/auth/userinfo.profile https://www.googleapis.com/auth/userinfo.email&redirect_uri=${process.env.REACT_APP_HOMEPAGE}?provider=2&response_type=code&client_id=${props.config.id}`;
    window.location.replace(url);
  };

  if(!props.config) {
    return null;
  }

  return (
    <Button fullWidth={props.fullWidth} color="primary" onClick={handleClick} disabled={props.disabled}>
      <GoogleIcon fontSize="large" /> <span style={props.fullWidth ? { width: 200} : null}>{props.title}</span>
    </Button>
  );
};

export default GithubButton;