import React from "react";
import Table from "../shared/table/Table";
import FormDialog from "../shared/FormDialog";
import Form from "./Form";

import compose from "../../compose";
import withStore from "../../consumers/withStore";
import withDoby from "../../consumers/withDoby";
import withUser from "../../consumers/withUser";
import { withLocalization } from "../shared/localization";

const ROWS = [
  { field: "title", disablePadding: true },
  { field: "description" },
  {
    field: "type.name",
    type: "enum",
    data: "enums",
    idField: "typeRef.id",
    sortable: false,
  },
  { field: "_id" },
];

class TableWrapper extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      x: null,
      isOpen: false,
    };
  }

  handleChangeClient = (name, value) => {
    this.setState({ clientId: value });
  };

  handleClose = () => {
    this.setState({ isOpen: false });
  };

  handleOpen = () => {
    this.setState({ isOpen: true });
  };

  handleChange = async (filters, page, sort, render) => {
    try {
      return await this.props.doby.getPosts(filters, sort, page, render);
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  onEdit = async (x) => {
    this.setState({ x, isOpen: true });
  };

  isDisabled = (x) => {
    return false;
  };

  render() {
    const { deletePost } = this.props.doby;
    const { translate } = this.props;

    return (
      <div>
        <Table
          rows={ROWS}
          data="posts"
          onChange={this.handleChange}
          onDelete={deletePost}
          title={translate("posts")}
          onEdit={this.onEdit}
          store={{ ...this.props.doby, ...this.props.store }}
          filters={[]}
          isDisabled={this.isDisabled}
          filename='posts'
        />
        <FormDialog
          title={translate("post")}
          onClose={this.handleClose}
          isOpen={this.state.isOpen}
        >
          <Form data={this.state.x} onSubmit={this.handleClose} />
        </FormDialog>
      </div>
    );
  }
}

export default compose(
  withStore,
  withDoby,
  withUser,
  withLocalization
)(TableWrapper);
