import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import TextInput from "../shared/TextInput";
import Form from "../shared/form/Form";
import Select from "../shared/Select";
import FileInput from "../shared/FileInput";
import Editor from "../shared/Editor";
import config from "../../config";

import withDoby from "../../consumers/withDoby";
import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";

const styles = (theme) => ({
  nameTextInput: {
    display: "flex",
    flex: 1,
  },
});

const INIT_STATE = {
  title: "",
  description: "",
  typeRef: null,
  content: "",
  _img: null,
  _thumbnail: null,
};

class FormWrapper extends React.Component {
  constructor(props) {
    super(props);
    this.state = props.data || INIT_STATE;
  }

  async componentDidMount() {
    try {
      const { getEnums } = this.props.store;
      const filter = [
        { field: "enumTypeId", value: 1 },
        { field: "disabled", value: false },
      ];
      await getEnums(filter);
    } catch (e) {
      console.error(e);
    }
  }

  handleChangeTypeRef = (name, value) => {
    this.setState({ [name]: { id: value } });
  };

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleSubmit = async () => {
    const { addPost, refreshPosts, editPost } = this.props.doby;

    try {
      if (this.state._id) {
        await editPost(this.state);
      } else {
        await addPost(this.state);
      }

      refreshPosts();
      this.setState(INIT_STATE);

      if (this.props.onSubmit) this.props.onSubmit();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { classes, disabled } = this.props;
    const {
      title,
      description,
      typeRef,
      content,
      _img,
      _thumbnail,
    } = this.state;
    const { enums } = this.props.store;

    return (
      <>
        <Form onSubmit={this.handleSubmit} inline disabled={disabled}>
          <TextInput
            value={title}
            field="title"
            onChange={this.handleChange}
            required
            disabled={disabled}
          />
          <div className={classes.nameTextInput}>
            <TextInput
              value={description}
              field="description"
              onChange={this.handleChange}
              required
              fullWidth
              disabled={disabled}
            />
          </div>
          <Select
            value={typeRef ? typeRef.id : null}
            field="typeRef"
            onChange={this.handleChangeTypeRef}
            data={enums.data}
            textField="name"
            required
            disabled={disabled}
          />
          <FileInput
            mimeType="image/jpeg"
            value={_img}
            field="_img"
            onChange={this.handleChange}
            disabled={this.state._id || this.state._id === "" || disabled}
            height={config.imageSize}
          />
          <FileInput
            mimeType="image/jpeg"
            value={_thumbnail}
            field="_thumbnail"
            onChange={this.handleChange}
            disabled={this.state._id || this.state._id === "" || disabled}
            height={config.thumbnailSize}
          />
        </Form>
        <Editor
          field="content"
          value={content}
          onChange={this.handleChange}
          height={config.imageSize}
          disabled={disabled}
        />
      </>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object,
  onSubmit: PropTypes.func,
  date: PropTypes.instanceOf(Date),
  disabled: PropTypes.bool,
};

export default compose(
  withStyles(styles),
  withStore,
  withDoby,
  withUser
)(FormWrapper);
