import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Toolbar from '@material-ui/core/Toolbar';
import classNames from 'classnames';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@material-ui/core/Tooltip';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import CloudDownloadIcon from '@material-ui/icons/CloudDownload';
import FilterListIcon from '@material-ui/icons/FilterList';
import RefreshIcon from '@material-ui/icons/Refresh';
import { lighten } from '@material-ui/core/styles/colorManipulator';
import { withLocalization } from '../localization';

const toolbarStyles = theme => ({
  root: {
    paddingRight: theme.spacing.unit,
  },
  highlight:
    theme.palette.type === 'light'
      ? {
        color: theme.palette.secondary.main,
        backgroundColor: lighten(theme.palette.secondary.light, 0.85),
      }
      : {
        color: theme.palette.text.primary,
        backgroundColor: theme.palette.secondary.dark,
      },
  spacer: {
    flex: '1 1 auto',
  },
  actions: {
    color: theme.palette.text.secondary,
  },
  title: {
    flex: '0 0 auto',
  },
});

let EnhancedTableToolbar = props => {
  const { numSelected, classes, onDelete, onFilter, title, onExport, isFilterSet, actions, onRefresh, exportDisabled, translate } = props;

  return (
    <Toolbar
      className={classNames(classes.root, {
        [classes.highlight]: numSelected > 0,
      })}
    >
      <div className={classes.title}>
        {numSelected > 0 ? (
          <Typography color="inherit" variant="subtitle1">
            {numSelected} {translate('table.selected')}
          </Typography>
        ) : (
            <Typography variant="h6" id="tableTitle">
              {title}
            </Typography>
          )}
      </div>
      <div className={classes.spacer} />
      <div className={classes.actions}>
        {actions}
        {numSelected > 0 ? (
          <Tooltip title={translate('table.delete')}>
            <IconButton aria-label={translate('table.delete')} onClick={onDelete}>
              <DeleteIcon />
            </IconButton>
          </Tooltip>
        ) : (
            <Tooltip title={translate('table.filter')}>
              <IconButton aria-label={translate('table.filter')} color={isFilterSet ? "primary" : "default"} onClick={onFilter}>
                <FilterListIcon />
              </IconButton>
            </Tooltip>
          )}
        <Tooltip title={translate('table.export')}>
          <IconButton aria-label={translate('table.export')} onClick={onExport} disabled={exportDisabled}>
            <CloudDownloadIcon />
          </IconButton>
        </Tooltip>
        <Tooltip title={translate('table.refresh')}>
          <IconButton aria-label={translate('table.refresh')} onClick={onRefresh}>
            <RefreshIcon />
          </IconButton>
        </Tooltip>
      </div>
    </Toolbar>
  );
};

EnhancedTableToolbar.propTypes = {
  classes: PropTypes.object.isRequired,
  numSelected: PropTypes.number.isRequired,
  onDelete: PropTypes.func.isRequired,
  onFilter: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  onExport: PropTypes.func.isRequired,
  isFilterSet: PropTypes.bool.isRequired,
  actions: PropTypes.element,
  exportDisabled: PropTypes.bool
};

export default withLocalization(withStyles(toolbarStyles)(EnhancedTableToolbar));