import React, {useState} from 'react';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../../style';
import classNames from 'classnames';
import { withLocalization } from '../../shared/localization'
import compose from '../../../compose';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

const styles = theme => ({
    ...getSharedStyles(theme),
    tabsRootClasses: {
        background: '#E4EAF3',       
        minHeight: '37px', 
    },
    tabsInicator: {
        height: '100%',
        background: theme.palette.secondary.main,
    },
    tabSelected: {
        zIndex: 3,
        color: 'white',
        border: 'none!important'
    },
    label: {
       letterSpacing: '0.5px'
    },
    labelContainer: {
        padding: '0px 30px'
    },
    tabRoot: {
        minHeight: '37px',
        border: '2px solid #C8D2E1'
    } 
});

function TabSwitch(props) {
    const [value, setValue] = useState(0)
    const { classes } = props;

    const handleChange = (event, newValue) => {
        setValue(newValue);
        props.onChange(newValue)
    }

    return (
        <>
            <Tabs 
                value={value}
                onChange={handleChange} 
                variant="fullWidth"
                classes={{root: classes.tabsRootClasses, indicator: classes.tabsInicator}}
            >
                <Tab 
                    label={props.label1}
                    classes={{root: classes.tabRoot, selected: classes.tabSelected, labelContainer: classes.labelContainer, label: classNames(classes.label, classes.textMono, classes.textXs2, classes.fontBold, classes.uppercase)}}
                />
                <Tab 
                    label={props.label2}
                    classes={{root: classes.tabRoot, selected: classes.tabSelected, labelContainer: classes.labelContainer, label: classNames(classes.label, classes.textMono, classes.textXs2, classes.fontBold, classes.uppercase)}}
                />              
            </Tabs>
        </>
    )
}

export default compose(
    withStyles(styles),
    withLocalization
)(TabSwitch);