import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { withRouter } from 'react-router';
import withStore from '../../consumers/withStore';
import withDoby from '../../consumers/withDoby';
import getSharedStyles from '../../style';
import classNames from 'classnames';
import Grid from '@material-ui/core/Grid';
import compose from '../../compose';
import withUser from '../../consumers/withUser';
import HillIcon from '@material-ui/icons/TerrainOutlined';

import Button from '../dobyComponents/form/Button';
import Radio from '@material-ui/core/Radio';
import ProductSmallCard from '../dobyComponents/eshop/ProductSmallCard'
import { withLocalization } from '../shared/localization';
import { formatPrice } from '../../utils/helpers'

const styles = theme => ({
    ...getSharedStyles(theme),
    checkoutStep: {
        width: '25%',
        height: '60px',
        background: 'red',
    },
    checkoutStepActive: {
        background: 'green'
    },
    logoHolder: {
        width: '80px',
        height: '80px',
        background: 'white',
        borderRadius: '10px',
        textAlign: 'center',
    },
    logolImg: {
        maxWidth: '70px'
    },
    challengeName: {
        color: '#47556A',
        fontSize: '20px',
    },
    stepNumber: {
        height: '40px',
        width: '40px',
        borderRadius: 50,
        textAlign: 'center',
        lineHeight: '40px',
        background: '#333442',
        color: 'white',
    },

    //package
    challengePackageCard: {
        background: 'white',
        border: '1px solid #C8D2E1'
    },
    challengePackageCardSelected: {
        background: '#333442',
        border: '1px solid #333442',
        color: 'white'
    },
    btnWhite: {
        background: 'white',
        color: '#333442'
    },
    lightText: {
        color: 'rgba(255,255,255,0.4)'
    },
});


class ChallengeCheckout extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedPackage: null,
            selectedGift: null,
            challenge: null,
            challengeLogo: null,

            //starterPackLowcost: null,
            starterPackStandard: null
        }
    }

    componentDidMount() {
        this.fetchData();
    }

    fetchStarterpacks = async () => {
        //NOTE: get all products with category of starterpack
        const filterStarterpack = [
            { field: "catalog_id", value: "pack" }
        ];

        //TODO: use find 
        const packs = await this.props.doby.getProducts(filterStarterpack);
        await this.setState({ starterPackStandard: packs.data[0]});
    }

    fetchData = async () => {
        const { _id } = this.props.match.params;
        await this.fetchStarterpacks();

        const filter = [ { field: "main", value: true } ];

        await this.props.doby.getProducts(filter);
        await this.props.doby.getChallenges();

        const challenge = this.props.doby.challenges.data.find(x => x._id === _id);
        if (challenge) {
            this.setState({
                challenge
            });

            this.getChallangeLogo(challenge.logoId)
        }
    }

    getChallangeLogo = async (logoId) => {
        if (!logoId)
            return;

        const data = await this.props.store.getFile(logoId)
        var reader = new FileReader();
        reader.onloadend = () => {
            this.setState({ challengeLogo: reader.result });
        }
        reader.readAsDataURL(data);
    }

    packageChanged = (field, value) => {
        this.setState({
            [field]: value._id
        })

        if (value._id === this.state.starterPackStandard._id) {
            //this.props.doby.cartRemoveProduct(this.state.starterPackLowcost._id);
            //console.log(this.state.starterPackStandard);
            this.props.doby.cartAddProduct({
                ...this.state.starterPackStandard,
                challengeRef: {id: this.state.challenge._id}
            });
        }
        // if (value._id === this.state.starterPackLowcost._id) {
        //     this.props.doby.cartRemoveProduct(this.state.starterPackStandard._id);
        //     this.props.doby.cartAddProduct({
        //         ...this.state.starterPackLowcost,
        //         challengeRef: {id: this.state.challenge._id}
        //     });
        // }
        //more 
    }

    handleFieldChange = (field, value) => {
        this.setState({
            [field]: value
        })
    }

    renderProducts = () => {
        const { products } = this.props.doby;
        return products.data.map((product, index) => {
            const { classes } = this.props;
            return (
                <Grid item xs={12} sm={6} className={classNames((index & 1) ? classes.pl2 : classes.pr2, classes.mb4)}>
                    <ProductSmallCard
                        key={product._id}
                        title={product.name}
                        price={product.price_with_tax}
                        thumbnailId={product.thumbnailId}
                        catalogId={product.catalog_id}
                    />
                </Grid>
            )
        })
    }

    goToCart = () => {
        this.props.history.push(`/eshop/cart`);
    }

    render() {
        const { classes } = this.props;
        return (
            <>
                <div className={classNames(classes.dFlex, classes.w100)}>
                    <div className={classNames(classes.checkoutStep)}>

                    </div>
                    <div className={classNames(classes.checkoutStep)}>

                    </div>
                    <div className={classNames(classes.checkoutStep, classes.checkoutStepActive)}>

                    </div>
                    <div className={classNames(classes.checkoutStep)}>

                    </div>
                </div>

                <div className={classNames(classes.container, classes.mt5)}>
                    <Grid
                        container
                        direction="row"
                        justify="flex-start"
                    >
                        <Grid item xs={12} className={classNames()}>
                            <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter)}>
                                <div className={classNames(classes.logoHolder, classes.dFlex, classes.alignItemsCenter, classes.justifyContentCenter)}>
                                    {this.state.challengeLogo && <img src={this.state.challengeLogo} className={classNames(classes.logolImg)} alt={''} />}
                                </div>
                                <div className={classNames(classes.challengeName, classes.ml4)}>
                                    {this.state.challenge && this.state.challenge.name}
                                </div>
                            </div>
                        </Grid>

                        <Grid item xs={12} className={classNames(classes.mt5, classes.mb6)}>
                            <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter)}>
                                <div className={classNames(classes.stepNumber, classes.textMonoH4, classes.fontSemibold)}>
                                    1
                                </div>
                                <div className={classNames(classes.fontBold, classes.textMonoH4, classes.ml4)}>
                                    {this.props.translate('choose_your_package')}
                                </div>
                            </div>
                        </Grid>

                        {/* package lowcost */}
                        <Grid item xs={12} className={classNames(classes.mb3)}>
                            { this.state.starterPackLowcost && <div className={classNames(classes.roundedMd, classes.p5, this.state.selectedPackage === this.state.starterPackLowcost._id ? classes.challengePackageCardSelected : classes.challengePackageCard)} style={{ borderBottomLeftRadius: 0, borderBottomRightRadius: 0 }}>
                                <div className={classNames(classes.flexCol)}>
                                    <div className={classNames(classes.dFlex, classes.w100, classes.justifyContentBetween, classes.alignItemsCenter)}>
                                        <div className={classNames(classes.fontSemibold)}>
                                            Low Cost
                                        </div>
                                        <div className={classNames(classes.dFlex, classes.alignItemsCenter)}>
                                            <div className={classNames(classes.fontBold, classes.textBase)}>
                                                { formatPrice(this.state.starterPackLowcost.price_with_tax)}
                                            </div>
                                            <div className={classNames()}>
                                                <Radio
                                                    onChange={(e) => this.packageChanged("selectedPackage", this.state.starterPackLowcost)}
                                                    value={0}
                                                    checked={this.state.selectedPackage === this.state.starterPackLowcost._id}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                    <div className={classNames(classes.row, classes.mt3)}>
                                        {/* <div className={classNames(classes.dFlex)}>
                                            <div className={classNames()}>
                                                icon
                                            </div>
                                            <div className={classNames()}>
                                                registracia vo vyzve
                                            </div>
                                        </div> */}
                                    </div>

                                </div>
                            </div> }
                            { this.state.starterPackStandard && <div className={classNames(classes.roundedMd, classes.p5, this.state.selectedPackage === this.state.starterPackStandard._id ? classes.challengePackageCardSelected : classes.challengePackageCard)} style={{ borderTopLeftRadius: 0, borderTopRightRadius: 0 }}>
                                <div className={classNames(classes.flexCol)}>
                                    <div className={classNames(classes.dFlex, classes.w100, classes.justifyContentBetween, classes.alignItemsCenter)}>
                                        <div className={classNames(classes.fontSemibold)}>
                                            Standard
                                        </div>
                                        <div className={classNames(classes.dFlex, classes.alignItemsCenter)}>
                                            <div className={classNames(classes.fontBold, classes.textBase)}>
                                                {formatPrice(this.state.starterPackStandard.price_with_tax)}
                                            </div>
                                            <div className={classNames()}>
                                                <Radio
                                                    onChange={(e) => this.packageChanged("selectedPackage", this.state.starterPackStandard)}
                                                    value={0}
                                                    checked={this.state.selectedPackage === this.state.starterPackStandard._id}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                    <div className={classNames(classes.mt2)}>
                                        <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter, classes.roundedMd, classes.bg300, classes.py2, classes.px3)}>
                                            <div className={classNames(classes.fontBold, classes.textXs2)}>
                                                Vsetko z balicku low cost
                                            </div>
                                            <div className={classNames(classes.dFlex, classes.ml2)}>
                                                <HillIcon />
                                                <HillIcon />
                                                <HillIcon />
                                                <HillIcon />
                                            </div>
                                        </div>

                                        <div className={classNames(classes.mt3)}>
                                            <div className={classNames(classes.dFlex, classes.w100)}>
                                                <div className={classNames(classes.w50, classes.pr1)}>
                                                    <div className={classNames(classes.ml2)}>
                                                        <div className={classNames()}>
                                                            <HillIcon />
                                                        </div>
                                                        <div className={classNames(classes.textXs, classes.fontMedium)}>
                                                            Zlavovy kupon 22 
                                                        </div>
                                                        <div className={classNames(classes.textXs2, classes.mt1)}>
                                                            Plat na veci z e-shopu niie. Kupn me vyui aj neskr (poleme ti ho na e-mail).
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className={classNames(classes.w50, classes.pl1)}>
                                                    <div className={classNames(classes.ml2)}>
                                                        <div className={classNames()}>
                                                            <HillIcon />
                                                        </div>
                                                        <div className={classNames(classes.textXs, classes.fontMedium)}>
                                                            Zlavovy kupon 22 
                                                        </div>
                                                        <div className={classNames(classes.textXs2, classes.mt1)}>
                                                            Plat na veci z e-shopu niie. Kupn me vyui aj neskr (poleme ti ho na e-mail).
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div> }
                        </Grid>

                        <Grid item xs={12} className={classNames(classes.mt7, classes.mb6)}>
                            <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter)}>
                                <div className={classNames(classes.stepNumber, classes.textMonoH4, classes.fontSemibold)}>
                                    2
                                </div>
                                <div className={classNames(classes.fontBold, classes.textMonoH4, classes.ml4)}>
                                    Vbava
                                </div>
                            </div>
                        </Grid>

                        {this.renderProducts()}

                        <Button
                            className={classNames(classes.w100, classes.roundedMd, classes.mt2, classes.mb4)}
                            onClick={this.goToCart}
                        >
                            {this.props.translate('continue')}
                        </Button>

                    </Grid>
                </div>
            </>
        )
    }
}



export default compose(
    withStyles(styles),
    withUser,
    withRouter,
    withDoby,
    withStore,
    withLocalization
)(ChallengeCheckout);