import React from "react";
import PropTypes from "prop-types";
import Avatar from "@material-ui/core/Avatar";
import CssBaseline from "@material-ui/core/CssBaseline";
// import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import withStyles from "@material-ui/core/styles/withStyles";

import { Redirect } from "react-router-dom";
import withUser from "../../consumers/withUser";
import { withLocalization } from '../shared/localization';
import withStore from "../../consumers/withStore";
import compose from "../../compose";
import config from '../../config';

const styles = theme => ({
  main: {
    width: "100%",
    display: "block", // Fix IE 11 issue.
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
      width: 400,
      marginLeft: "auto",
      marginRight: "auto"
    }
  },
  paper: {
    marginTop: theme.spacing.unit * 8,
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme
      .spacing.unit * 3}px`
  },
  avatar: {
    margin: theme.spacing.unit,
    // backgroundColor: theme.palette.secondary.main,
    width: 128,
    height: 128
  },
  loader: {
    marginTop: theme.spacing.unit,
  }
});

let _code, _provider, _mutex;
class OAuthComponent extends React.Component {
  constructor(props) {
    super(props);

    const urlParams = new URLSearchParams(this.props.location.search);
    _provider = urlParams.get("provider");
    _code = urlParams.get("code");
  }

  async componentDidMount() {
    if (_provider && !_mutex) {
      _mutex = true;
      const { user } = this.props;
      try {
        if (user.user) {
          await user.updateOauth(_provider, _code);
          this.props.history.replace('/account');
        }
        else {
          if(!await user.oAuth(_provider, _code)) {
            user.showMsg('Unauthorized');
          }
          this.props.history.replace(config.home);
        }
      }
      catch (error) {
        this.props.user.showError(error);
        this.props.history.replace('/login');
      }
      _mutex = false;
    }
  }

  render() {
    const { classes, translate } = this.props;

    if (!_provider) return <Redirect to={{ pathname: config.home }} />;
    return (
      <main className={classes.main}>
        <CssBaseline />
        <Paper className={classes.paper}>
          <Avatar className={classes.avatar} src={config.logo} />
          <div className={classes.loader}>
            <Typography component="h1" variant="h5">
              {translate('loading')}
            </Typography>
          </div>
        </Paper>
      </main>
    );
  }
}

OAuthComponent.propTypes = {
  classes: PropTypes.object.isRequired
};

export default compose(withStyles(styles), withUser, withStore, withLocalization)(OAuthComponent);
