import React, { useState } from 'react';
import { withStyles } from '@material-ui/core/styles';
import compose from '../../../compose';

import classNames from 'classnames';
import getSharedStyles from '../../../style';

import DeliveryPayment from './DeliveryPayment';
import CheckoutSummary from './CheckoutSummary'

const styles = theme => ({
    ...getSharedStyles(theme),
    checkoutStep: {
        width: '25%',
        height: '60px',
        background: 'red',
    },
    checkoutStepActive: {
        background: 'green'
    }
});

function Checkout(props) {
    const [currentStep, setCurrentStep] = useState(3);
    const { classes } = props;

    const changeStep = (step) => {
        setCurrentStep(step)
    }
    return (
        <>
            <div className={classNames(classes.dFlex, classes.w100)}>
                <div className={classNames(classes.checkoutStep)}>

                </div>
                <div className={classNames(classes.checkoutStep)}>

                </div>
                <div className={classNames(classes.checkoutStep, classes.checkoutStepActive)}>

                </div>
                <div className={classNames(classes.checkoutStep)}>

                </div>
            </div>

            <div>
                {currentStep === 3 && <DeliveryPayment changeStep={changeStep}/>}
                {currentStep === 4 && <CheckoutSummary changeStep={changeStep}/>}               
            </div>
        </>
    );
}

export default compose(
    withStyles(styles),
)(Checkout);