import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { getStyles } from "./style";
import AttachFileIcon from "@material-ui/icons/AttachFile";
import InputLabel from "@material-ui/core/InputLabel";
import IconButton from "./IconButton";
import withUtils from "./utils/withUtils";
import { withLocalization } from "./localization";

const styles = (theme) => ({
  ...getStyles(theme),
  button: {
    margin: theme.spacing.unit,
  },
  tooltip: {
    "max-width": "150px",
    "max-height": "150px",
  },
});

class ImageAvatars extends React.Component {
  constructor(props) {
    super(props);

    this.inputRef = React.createRef();
    this.filePath = "";
  }

  onChange = async (event) => {
    event.preventDefault();

    let file = event.target.files[0];
    this.filePath = URL.createObjectURL(file);

    // zabezpecuje volanie onChange aj pri uploadovani rovnakeho suboru
    // -> po ulozeni formularu nezavolalo onChange opat, v pripade nahrania
    // -> rovnakeho suboru ako predtym
    event.target.value = "";

    if (this.props.mimeType === "image/jpeg") {
      var urlCreator = window.URL || window.webkitURL;
      var src = urlCreator.createObjectURL(file);
      file = await this.props.utils.resize(src, this.props.height, file.name);
    }

    this.props.onChange(this.props.field, file);
  };

  onClick = () => {
    if (!this.props.disabled) {
      this.inputRef.current.click();
    }
  };

  render() {
    const { classes, value, disabled, mimeType, field, translate } = this.props;
    const label = this.props.label || translate("field." + field);

    return (
      <div>
        <IconButton
          className={classes.button}
          tooltip={
            <img
              src={this.filePath}
              alt="Attach file"
              className={classes.tooltip}
            />
          }
          color={value ? "primary" : "default"}
          onClick={this.onClick}
          disabled={disabled}
        >
          <AttachFileIcon />
        </IconButton>
        <InputLabel htmlFor={field}>{label}</InputLabel>
        <input
          ref={this.inputRef}
          style={{ display: "none" }}
          type="file"
          accept={mimeType}
          onChange={this.onChange}
        />
      </div>
    );
  }
}

ImageAvatars.propTypes = {
  classes: PropTypes.object.isRequired,
  field: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  value: PropTypes.string.isRequired,
  height: PropTypes.number.isRequired,
  label: PropTypes.string,
  disabled: PropTypes.bool,
  mimeType: PropTypes.string,
};

export default withLocalization(withUtils(withStyles(styles)(ImageAvatars)));
