import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../style';
import { getStyles } from '../shared/style';
import { withLocalization } from '../shared/localization'

import compose from '../../compose';
import classNames from 'classnames';
import Drawer from "@material-ui/core/Drawer";


const styles = theme => ({
    ...getSharedStyles(theme),
    ...getStyles(theme),

    root: {
        justifyContent: 'center'
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
    drawer: {
        borderTopLeftRadius: '20px',
        borderTopRightRadius: '20px'
    }
});

function RollerModal(props) {
    const { classes } = props;
    return (
        <Drawer
            anchor="bottom"
            open={props.opened}
            onClose={props.onClose}
            //className={classes.drawer}
            classes={{ paper: classes.drawer }}
        >
            <div className={classNames(classes.container, classes.mt5)}>
                <h5 className={classNames(classes.fontBold, classes.mt7)}>Detaily</h5>
                <p>
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Fermentum leo vel orci porta non. Morbi tristique senectus et netus. Porta non pulvinar neque laoreet suspendisse interdum consectetur libero.
                </p>

                <h5 className={classNames(classes.fontBold, classes.mt7)}>Detaily</h5>
                <p>
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Fermentum leo vel orci porta non. Morbi tristique senectus et netus. Porta non pulvinar neque laoreet suspendisse interdum consectetur libero.
                </p>
            </div>
        </Drawer>
    );
}

RollerModal.propTypes = {
    onClose: PropTypes.func.isRequired,
};

export default compose(
    withStyles(styles),
    withLocalization
)(RollerModal);