import React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
// import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import MenuIcon from '@material-ui/icons/Menu';
// import Toolbar from '@material-ui/core/Toolbar';
// import Typography from '@material-ui/core/Typography';
// import AccountCircle from '@material-ui/icons/AccountCircle';
// import MenuItem from '@material-ui/core/MenuItem';
// import Menu from '@material-ui/core/Menu';

import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import { withStyles } from '@material-ui/core/styles';
import withWidth, { isWidthDown } from '@material-ui/core/withWidth';
import { withRouter } from "react-router";
import compose from '../../compose';
import { withLocalization } from './localization';
import classNames from 'classnames';
import getSharedStyles from '../../style';

const drawerWidth = 240;

const styles = theme => ({
    ...getSharedStyles(theme),
    root: {
        display: 'flex',
    },
    grow: {
        flexGrow: 1,
    },
    drawer: {
        // [theme.breakpoints.up('lg')]: {
        //     width: drawerWidth,
        //     flexShrink: 0,
        // },
    },
    appBar: {
        background: '#fff',
        height: '60px',
        //marginLeft: drawerWidth,
        // [theme.breakpoints.up('lg')]: {
        //     width: `calc(100% - ${drawerWidth}px)`,
        // },
        boxShadow: "0px 0px 17px 1px rgba(0,0,0,0.08)"

    },
    appBarMenuIcon: {
        fill: theme.palette.secondary.dark,
        height: '1.8rem',
        width: '1.8rem',
    },
    appBarLogoImg: {
        width: '140px',
    },
    appBarCol: {
        width: 'calc((100% - 100px) / 2)',
        color: 'red',
    },
    menuButton: {
        marginRight: 20,
        [theme.breakpoints.up('lg')]: {
            display: 'none',
        },
    },
    toolbar: theme.mixins.toolbar,
    drawerPaper: {
        width: '80%',
        maxWidth: '340px',
    },
    content: {
        flexGrow: 1,
        //padding: theme.spacing.unit * 2,
        overflow: 'auto',
        paddingTop: '60px'
    },
});

class ResponsiveDrawer extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            mobileOpen: false,
            anchorEl: null
        };
    }

    handleMenu = event => {
        this.setState({ anchorEl: event.currentTarget });
    };

    handleClose = () => {
        this.setState({ anchorEl: null });
    };

    handleClickAccount = () => {
        this.props.history.push('/account');
        this.handleClose();
    }

    handleDrawerToggle = () => {
        this.setState(state => ({ mobileOpen: !state.mobileOpen }));
    };

    redirect = (route) => {
        if (isWidthDown('md', this.props.width)) {
            this.handleDrawerToggle();
        }

        this.props.history.push(route.path);
    }

    render() {
        const { classes, theme, routes, user, translate } = this.props;
        // const { anchorEl } = this.state;
        // const open = Boolean(anchorEl);
        const path = this.props.history.location.pathname;
        const route = routes.find(x => x.path === path);

        const drawer = (
            <div>
                <div className={classes.toolbar} />
                <Divider />
                <List>
                    {routes.map(route => {
                        if (route.exact && !route.hidden && (!route.permission || user.hasPermission(route.permission))) return (
                            <ListItem button key={route.name} onClick={() => this.redirect(route)}>
                                <ListItemText primary={translate('route.' + route.name) || route.name} />
                            </ListItem>
                        );
                        else return null;
                    })}
                </List>
            </div>
        );

        if (!route || !route.anonymous || route.name === 'home') {
            return (
                <div className={classes.root}>
                    <CssBaseline />
                    <AppBar position="fixed" className={classes.appBar} color='secondary'>
                        <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.h100, classes.ml5, classes.mr5)} >
                            {this.props.user.user ?
                                <>
                                    <div onClick={this.handleDrawerToggle} className={classes.appBarCol}>
                                        <MenuIcon className={classes.appBarMenuIcon} />
                                    </div>
                                    <img src={this.props.logo} className={classes.appBarLogoImg} alt={''} />
                                    <div className={classNames(classes.appBarCol, classes.dFlex, classes.alignItemsCenter, classes.justifyContentEnd)}>
                                        <ShoppingCartIcon
                                            style={{ color: theme.palette.secondary.dark }}
                                            onClick={() => this.props.history.push('/eshop/cart')}
                                        />
                                    </div>
                                </>
                                :
                                <>
                                    <div className={classes.appBarCol}></div>
                                    <img src={this.props.logo} className={classes.appBarLogoImg} alt={''} onClick={() => this.props.history.push('/login')} />
                                </>
                            }
                        </div>
                    </AppBar>
                    <nav className={classes.drawer}>
                        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
                        <Hidden lgUp implementation="css">
                            <Drawer
                                variant="temporary"
                                anchor={theme.direction === 'rtl' ? 'right' : 'left'}
                                open={this.state.mobileOpen}
                                onClose={this.handleDrawerToggle}
                                classes={{
                                    paper: classes.drawerPaper,
                                }}
                            >
                                {drawer}
                            </Drawer>
                        </Hidden>
                        {/* <Hidden mdDown implementation="css">
                            <Drawer
                                classes={{
                                    paper: classes.drawerPaper,
                                }}
                                variant="permanent"
                                open
                            >
                                {drawer}
                            </Drawer>
                        </Hidden> */}
                    </nav>
                    <main className={classes.content}>                       
                        {this.props.children}
                    </main>
                </div>
            );
        }
        else {
            return (
                <div className={classes.root}>
                    {this.props.children}
                </div>
            )
        }
    }
}

ResponsiveDrawer.propTypes = {
    classes: PropTypes.object.isRequired,
    theme: PropTypes.object.isRequired,
    children: PropTypes.arrayOf(PropTypes.element),
    user: PropTypes.object.isRequired,
    routes: PropTypes.array.isRequired,
};

export default compose(
    withWidth(),
    withStyles(styles, { withTheme: true }),
    withRouter,
    withLocalization
)(ResponsiveDrawer);