import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Redirect,
} from "react-router-dom";

import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import withStyles from '@material-ui/core/styles/withStyles';

import BottomNavigationBar from './components/dobyComponents/BottomNavigationBar'

import compose from './compose';
import withUser from './consumers/withUser';
import Container from "./components/shared/Container";
import routes from './routes';
import config from './config'

const styles = theme => ({
  close: {
    padding: theme.spacing.unit / 2,
  },
});

let _basename = '/';
let _init = false;
class App extends React.Component {
  constructor(props) {
    super(props);

    if(process.env.NODE_ENV === "production") {
      const url = new URL(process.env.REACT_APP_HOMEPAGE);
      _basename = url.pathname;
    }
  }

  render() {
    const { user, classes } = this.props;

    if(!_init && user.user) {
      _init = true;
      user.initHub();
    }

    return (
      <React.Fragment>
        <Router basename={_basename}>
          <Container
            user={user}
            routes={routes}
            logo={config.logo}
          >
            {routes.map(x => {
              if (x.anonymous) return (
                <Route key={x.name} path={x.path} exact={x.exact} component={x.component} />
              )
              else return (
                <PrivateRoute exact={x.exact} key={x.name} path={x.path} component={x.component} user={user} permission={x.permission} />
              )
            })}
          </Container>

          {/* <BottomNavigationBar/> */}
        </Router>        

        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'center',
          }}
          open={user.msg}
          autoHideDuration={6000}
          onClose={user.hideMsg}
          ContentProps={{
            'aria-describedby': 'message-id',
          }}
          message={<span id="message-id">{user.msg}</span>}
          action={[
            <IconButton
              key="close"
              aria-label="Close"
              color="inherit"
              className={classes.close}
              onClick={user.hideMsg}
            >
              <CloseIcon />
            </IconButton>,
          ]}
        />
      </React.Fragment>
    );
  }
}

function PrivateRoute({ component: Component, user, permission, ...rest }) {
  return (
    <Route
      {...rest}
      render={props => {
        if(!user.user) {
          return <Redirect
            to={{
              pathname: "/login",
              state: { from: props.location }
            }}
          />
        }
        else if(permission && !user.hasPermission(permission)) {
          return <Redirect
            to={{
              pathname: config.home,
              state: { from: props.location }
            }}
          />
        }
        else {
          return <Component {...props} />;
        }
      }}
    />
  );
}

export default compose(
  withStyles(styles),
  withUser
)(App);