import React from 'react';
import { withRouter } from 'react-router';
import withStore from '../../consumers/withStore';
import withDoby from '../../consumers/withDoby';
import withUser from '../../consumers/withUser';

//import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { withLocalization } from '../shared/localization'

import classNames from 'classnames';
import compose from '../../compose';
import getSharedStyles from '../../style';


import Select from '../dobyComponents/form/SelectField';
import Button from '../dobyComponents/form/Button';
import TextField from '../dobyComponents/form/TextField';
import Switch from '../dobyComponents/form/Switch';

const styles = theme => ({
    ...getSharedStyles(theme),
    image: {
        width: '100%'
    }
});

class Push extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            challenges: [],
            summits: [],
            selectedChallengeRef: null,
            selectedSummitRef: null,
            summitDesc: '',
            summitIsPrivate: false,
        };
    }

    componentDidMount() {
        this.getData();
    }

    //TODO: get our possible summits
    getData = async () => {
        const { user } = this.props.user;
        const { challenges } = await this.props.doby.getUser(user._id);
        this.setState({ challenges });
    }

    uploadPicture = () => {

    }

    onChangeChallenge = async (field, selectedChallengeRef) => {
        const challenge = this.state.challenges.find( x => x._id === selectedChallengeRef );
        if(challenge && this.state.selectedChallengeRef !== selectedChallengeRef) {
            const summitFilter = [
                {
                    field: "_id",
                    op: "in",
                    value: challenge.summitsRef.map(x => x.id)
                },
            ];

            this.setState({selectedChallengeRef});

            const summits = await this.props.doby.getSummits(summitFilter);
            this.setState({summits: summits.data});
        }
    }

    onChangeSummit = (field, selectedSummitRef) => this.setState({selectedSummitRef});

    render() {
        const { classes } = this.props;
        return (
            <div className={classNames(classes.container, classes.mt3)}>
                <h2>{this.props.translate('summit.add')}</h2>

                <img
                    className={classNames(classes.image, classes.mb3)}
                    alt=""
                    onClick={this.uploadPicture}
                    src="https://www.google.com/logos/doodles/2021/st-patricks-day-2021-6753651837108887.2-l.png" />

                <h3>{this.props.translate('summit.image_title')}</h3>
                <p>{this.props.translate('summit.image_desc')}</p>


                <div>
                    <Select
                        value={this.state.selectedChallengeRef ? this.state.selectedChallengeRef.toString() : ''}
                        field={'challenge'}
                        onChange={this.onChangeChallenge}
                        data={this.state.challenges}
                        textField={"name"}
                        fullWidth
                        required
                    />
                    <Select
                        value={this.state.selectedSummitRef ? this.state.selectedSummitRef.toString() : ''}
                        field={'summit'}
                        onChange={this.onChangeSummit}
                        data={this.state.summits}
                        textField={"name"}
                        fullWidth
                        required
                    />
                    <TextField
                        value={this.state.summitDesc}
                        field={'summitDesc'}
                        onChange={(field, summitDesc)=> { this.setState({summitDesc})}}
                        fullWidth
                        multiline
                    />

                    <div className={classNames(classes.dFlex, classes.w100, classes.justifyContentBetween, classes.alignItemsCenter)}>
                        <h3 className={classNames(classes.mt4)}>
                            {this.props.translate('summit.private')}
                        </h3>
                        <Switch
                            field={'private'}
                            value={this.state.summitIsPrivate}
                            onChange={(field, summitIsPrivate)=> { this.setState({summitIsPrivate})}}
                        />
                    </div>

                    <p>{this.props.translate('summit.private_desc')}</p>

                    <Button
                        className={classNames(classes.mt5, classes.mb3)}
                        onClick={this.showAllProductDetails}
                        fullwidth
                        disabled={!this.state.selectedChallengeRef || !this.state.selectedSummitRef}
                    >
                        {this.props.translate('summit.send')}
                    </Button>
                </div>
            </div>
        );
    }
}

export default compose(
    withRouter,
    withStore,
    withStyles(styles),
    withDoby,
    withUser,
    withLocalization
)(Push);
