import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import TextInput from "../../shared/TextInput";
import Select from "../../shared/Select";
import Form from "../../shared/form/Form";
import DatePicker from "../../shared/DatePicker";
import FileInput from "../../shared/FileInput";
import NumInput from "../../shared/NumInput";
import config from "../../../config";

import withDoby from "../../../consumers/withDoby";
import withUser from "../../../consumers/withUser";
import withStore from "../../../consumers/withStore";
import compose from "../../../compose";

const styles = (theme) => ({
  nameTextInput: {
    display: "flex",
    flex: 1,
  },
});

const INIT_STATE = {
  name: "",
  description: "",
  summitsRef: [],
  serieRef: null,
  difficulty: 1,
  start: new Date(),
  end: new Date(),
  _img: null,
  _thumbnail: null,
  _map: null,
  _logo: null,
  _badge: null,
};

class FormWrapper extends React.Component {
  constructor(props) {
    super(props);
    this.state = props.data || INIT_STATE;
  }

  async componentDidMount() {
    try {
      const { getSummits, getSeries } = this.props.doby;

      await getSummits();
      await getSeries();
    } catch (e) {
      console.error(e);
    }
  }

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleChangeSummitsRef = (name, value) => {
    const v = value.map((x) => ({ id: x }));
    this.setState({ [name]: v });
  };

  handleChangeSerieRef = (name, value) => {
    this.setState({ [name]: { id: value } });
  };

  handleSubmit = async () => {
    const { addChallenge, refreshChallenges, editChallenge } = this.props.doby;

    try {
      if (this.state._id) {
        await editChallenge(this.state);
      } else {
        await addChallenge(this.state);
      }

      refreshChallenges();
      this.setState(INIT_STATE);

      if (this.props.onSubmit) this.props.onSubmit();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { classes, disabled } = this.props;
    const {
      name,
      description,
      start,
      end,
      summitsRef,
      serieRef,
      difficulty,
      _img,
      _map,
      _thumbnail,
      _logo,
      _badge,
    } = this.state;
    const { summits, series } = this.props.doby;

    return (
      <Form onSubmit={this.handleSubmit} inline disabled={disabled}>
        <TextInput
          value={name}
          field="name"
          onChange={this.handleChange}
          required
          disabled={disabled}
        />
        <div className={classes.nameTextInput}>
          <TextInput
            value={description}
            field="description"
            onChange={this.handleChange}
            required
            fullWidth
            disabled={disabled}
          />
        </div>
        <Select
          value={serieRef ? serieRef.id : null}
          field="serieRef"
          onChange={this.handleChangeSerieRef}
          data={series.data}
          textField="name"
          required
          idField="id"
          disabled={disabled}
        />

        <NumInput
          value={difficulty}
          field="difficulty"
          onChange={this.handleChange}
          min={0}
          max={5}
          step={1}
          required
        />

        <DatePicker
          value={start}
          field="start"
          onChange={this.handleChange}
          required
        />

        <DatePicker
          value={end}
          field="end"
          onChange={this.handleChange}
          required
        />

        <div className={classes.nameTextInput}>
          <Select
            value={summitsRef.map((x) => x.id)}
            field="summitsRef"
            onChange={this.handleChangeSummitsRef}
            fullWidth
            data={summits.data}
            textField="name"
            required
            multiple
            idField="id"
            disabled={disabled}
          />
        </div>

        <FileInput
          mimeType="image/jpeg"
          value={_img}
          field="_img"
          onChange={this.handleChange}
          disabled={this.state._id || disabled}
          height={config.imageSize}
        />

        <FileInput
          mimeType="image/jpeg"
          value={_thumbnail}
          field="_thumbnail"
          onChange={this.handleChange}
          disabled={this.state._id || disabled}
          height={config.thumbnailSize}
        />

        <FileInput
          mimeType="image/png"
          value={_logo}
          field="_logo"
          onChange={this.handleChange}
          disabled={this.state._id || disabled}
        />

        <FileInput
          mimeType="image/png"
          value={_map}
          field="_map"
          onChange={this.handleChange}
          disabled={this.state._id || disabled}
        />

        <FileInput
          mimeType="image/jpeg"
          value={_badge}
          field="_badge"
          onChange={this.handleChange}
          disabled={this.state._id || disabled}
          height={config.badgeSize}
        />
      </Form>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object,
  onSubmit: PropTypes.func,
  date: PropTypes.instanceOf(Date),
  disabled: PropTypes.bool,
};

export default compose(
  withStyles(styles),
  withDoby,
  withUser,
  withStore
)(FormWrapper);
