import React, {useState} from 'react';
import { withStyles } from '@material-ui/core/styles';
import { withRouter } from 'react-router';
import compose from '../../compose';

import classNames from 'classnames';
import getSharedStyles from '../../style';

import AddIcon from '@material-ui/icons/Add';
import GroupIcon from '@material-ui/icons/GroupOutlined';
import AccountIcon from '@material-ui/icons/AccountCircleOutlined';
import HillIcon from '@material-ui/icons/TerrainOutlined';
import DotsIcon from '@material-ui/icons/MoreHoriz';

const styles = theme => ({
    ...getSharedStyles(theme),
    bottomBar: {
        position: 'fixed',
        height: '120px',
        width: '100%',
        bottom: '-60px',
        left: '0',
        background: 'rgba(255,255,255,0.95)',
        borderTopLeftRadius: '10px',
        borderTopRightRadius: '10px',
        borderTop: '1px solid rgba(0, 0, 0, 0.1)',
        backdropFilter: 'blur(9px)',
        transition: 'all 0.3s ease',
        zIndex: 800,
    },
    expandedBar: {
        bottom: '0',
    },
    itemsHolder: {
        width: 'calc((100% - 70px) / 2)',
        paddingTop: '0.5rem'
        //background: 'red',
    },
    addPostHolder: {
        width: '70px',
    },
    addPostBtn: {
        width: '56px',
        height: '56px',
        borderRadius: '50px',
        background: '#3044C0',
        boxShadow: '0px 2px 15px rgba(48, 68, 192, 0.75)',
        outline: 'none!important',
        border: '0!important',
        color: 'white',
        marginTop: '-10px',
        padding: '0',        
    },
    navItem: {
        width: '50%',
        textAlign: 'center'
    }
});

function BottomNavigationBar(props) {   
    const { classes, fullwidth, outline} = props;  
    const [expanded, setExpanded] = useState(false);

    const expandBar = () => {
        setExpanded(!expanded)
    }

    const goToLink = (name) => {
        props.history.push(name)
    }

    return (
        <>
            <div className={classNames(classes.bottomBar, expanded && classes.expandedBar)}>
                <div className={classNames(classes.flexCol)}>
                    <div className={classNames(classes.dFlex)}>
                        <div className={classNames(classes.itemsHolder, classes.pl5)}>
                            <div className={classNames(classes.dFlex, classes.justifyContentCenter)}>
                                <div className={classNames(classes.flexCol, classes.navItem, classes.alignItemsCenter)}>
                                    <GroupIcon/>
                                    <div className={classNames(classes.textXs2)}>
                                        Komunita
                                    </div>
                                </div>
                                <div className={classNames(classes.flexCol, classes.navItem, classes.alignItemsCenter)}>
                                    <AccountIcon/>
                                    <div className={classNames(classes.textXs2)}>
                                        Profil
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className={classNames(classes.addPostHolder, classes.textCenter)}>
                            <button className={classNames(classes.addPostBtn)}>
                                <AddIcon style={{fontSize: '30px'}}/>
                            </button>
                        </div>
                        <div className={classNames(classes.itemsHolder, classes.pr5)}>
                            <div className={classNames(classes.dFlex, classes.justifyContentCenter)}>
                                <div className={classNames(classes.flexCol, classes.navItem, classes.alignItemsCenter)} onClick={()=>goToLink('challenges')}>
                                    <HillIcon/>
                                    <div className={classNames(classes.textXs2)}>
                                        Vyzvy
                                    </div>
                                </div>
                                <div className={classNames(classes.flexCol, classes.navItem, classes.alignItemsCenter)} onClick={expandBar}>
                                    <DotsIcon/>
                                    <div className={classNames(classes.textXs2)}>
                                        {expanded ? "Menej" : "Viac"}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className={classNames(classes.dFlex, classes.mt3, classes.justifyContentCenter)}>
                        <div className={classNames(classes.flexCol, classes.alignItemsCenter, classes.px3)}>
                            <GroupIcon/>
                            <div className={classNames(classes.textXs2)}>
                                Sprievodca
                            </div>
                        </div>
                        <div className={classNames(classes.flexCol, classes.alignItemsCenter, classes.px3)}>
                            <GroupIcon/>
                            <div className={classNames(classes.textXs2)}>
                                Blog
                            </div>
                        </div>
                        <div className={classNames(classes.flexCol, classes.alignItemsCenter, classes.px3)}>
                            <GroupIcon/>
                            <div className={classNames(classes.textXs2)}>
                                Kontakt
                            </div>
                        </div>
                        <div className={classNames(classes.flexCol, classes.alignItemsCenter, classes.px3)}>
                            <GroupIcon/>
                            <div className={classNames(classes.textXs2)}>
                                Batoh
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default compose(
    withStyles(styles),
    withRouter,
)(BottomNavigationBar);