import React, { useState,useEffect  } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../style';
import { getStyles } from '../shared/style';
import classNames from 'classnames';

const styles = theme => ({
    ...getSharedStyles(theme),
    ...getStyles(theme),
    postImgWrapper: {
        width: '100%',
        height: '180px',
        background: 'rgba(0,0,0,0.1)',
        overflow: 'hidden',
        position: 'relative',
    },
    postImg: {
        width: '100%',
        height: '100%',
        position: 'absolute',
        left: 0,
        right: 0,
        objectFit: 'cover',
    },
    blogPostCard: {
        width: 250,
        height: 'auto'
    }
   
});

function BlogPost(props) {
    const { classes} = props;
    const [img, setImg] = useState(null);

    useEffect(() => {       
        if (!img) {
            getImg();
        }
      }, []);

    const getImg = async () =>{
        try {       
            if(props.thumbnailId) {
                const data = await props.getFile(props.thumbnailId);
                var reader = new FileReader();
                reader.onloadend = () => {
                    setImg(reader.result);
                }
                reader.readAsDataURL(data);
            }
        } catch(e) {
            console.error(e);
        }
    }  

    return (
        <div className={classNames(classes.blogPostCard, classes.flexCol, classes.mr5)}>           
            <div className={classNames(classes.postImgWrapper)}>
                <img src={img} className={classes.postImg} alt={props.title}/>
            </div>
            <h5 className={classNames(classes.textBase, classes.fontBold, classes.mt2)}>{props.title}</h5> 
            <p className={classNames(classes.mt2, classes.textBase)}>
                {props.content}
            </p>


        </div>
    );
}

BlogPost.propTypes = {
    title: PropTypes.string.isRequired,
    content: PropTypes.string.isRequired,
    created: PropTypes.string,    
    thumbnailId: PropTypes.string.isRequired,
    getFile: PropTypes.func.isRequired   
};

export default withStyles(styles)(BlogPost);