import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { withLocalization } from "../shared/localization";
import getSharedStyles from "../../style";
import compose from "../../compose";
import classNames from "classnames";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  profileImgHolder: {
    width: "40px",
    height: "40px",
    borderRadius: "50px",
    overflow: "hidden",
    position: "relative",
  },
  profileImg: {
    width: "100%",
    height: "100%",
    position: "absolute",
    top: "0",
    left: "0",
    objectFit: "cover",
  },
  postImgWrapper: {
    width: "100%",
    height: "220px",
    background: "rgba(0,0,0,0.1)",
    overflow: "hidden",
    position: "relative",
  },
  postImg: {
    width: "100%",
    height: "100%",
    position: "absolute",
    left: 0,
    right: 0,
    objectFit: "cover",
  },
  postCard: {
    background: "#ffffff",
  },
});

const convertDate = (date) => {
  return `${date.getDate()}.${date.getMonth() + 1}.${date.getFullYear()}`;
};

function ActivityPost(props) {
  const { classes } = props;
  const climbCreated = new Date(props.climbCreated);

  return (
    <div className={classNames(props.className, classes.postCard, classes.py5)}>
      <div className={classNames(classes.flexCol, classes.w100)}>
        <div className={classNames(classes.container)}>
          <div
            className={classNames(
              classes.dFlex,
              classes.w100,
              classes.alignItemsCenter,
              classes.justifyContentBetween
            )}
          >
            <div
              className={classNames(
                classes.dFlex,
                classes.alignItemsCenter,
                classes.justifyContentBetween
              )}
            >
              <div className={classNames(classes.profileImgHolder)}>
                <img
                  alt="avatarImg"
                  src={
                    props.avatar
                      ? "data:image/jpeg;base64," + props.avatar
                      : "./img/user-img.jpg"
                  }
                  className={classNames(classes.profileImg)}
                />
              </div>
              <div className={classNames(classes.flexCol, classes.ml3)}>
                <div className={classNames(classes.textXs)}>
                  {props.userName}
                </div>
                <div className={classNames(classes.textXs2, classes.text200)}>
                  {props.userAddress}
                </div>
                <div className={classNames()}></div>
              </div>
            </div>
            <div>...</div>
          </div>
        </div>

        <div className={classNames(classes.postImgWrapper, classes.mt5)}>
          <img
            alt="summitImg"
            src={
              props.image
                ? "data:image/jpeg;base64," + props.image
                : "./img/user-img.jpg"
            }
            className={classes.postImg}
          />
        </div>

        <div className={classNames(classes.container, classes.mt5)}>
          <div className={classNames(classes.flexCol, classes.w100)}>
            <div
              className={classNames(
                classes.dFlex,
                classes.alignItemsCenter,
                classes.justifyContentBetween
              )}
            >
              <div
                className={classNames(
                  classes.dFlex,
                  classes.alignItemsCenter
                )}
              >
                <div>Ikona</div>
                <div className={classNames(classes.FlexCol, classes.ml3)}>
                  <div className={classNames(classes.textXs)}>
                    {props.summitName}
                  </div>
                  <div className={classNames(classes.textXs2, classes.text200)}>
                    {`${props.summitElevation}m | Pridan ${convertDate(
                      climbCreated
                    )}`}
                  </div>
                </div>
              </div>
              <div
                className={classNames(
                  classes.flexRCol,
                  classes.alignItemsCenter
                )}
              >
                <div className={classNames(classes.textXs)}>
                  {props.challengeName}
                </div>
                <div
                  className={classNames(
                    classes.textXs2,
                    classes.text200,
                    classes.textRight
                  )}
                >{`${props.challengeFinishedSummits}/${props.challengeAllSummits}`}</div>
              </div>
            </div>
            <div className={classNames(classes.textXs2, classes.mt3)}>
              {props.description}
            </div>
            <div
              className={classNames(
                classes.w100,
                classes.dFlex,
                classes.alignItemsCenter,
                classes.justifyContentBetween,
                classes.mt3
              )}
            >
              <div className={classNames()}>Likes {props.likesCount}</div>
              <div className={classNames()}>Comments</div>
              <div className={classNames()}>Share</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

ActivityPost.propTypes = {
  className: PropTypes.object.isRequired,
  description: PropTypes.string.isRequired,
  userName: PropTypes.string.isRequired,
  userAddress: PropTypes.string.isRequired,
  avatar: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
  summitName: PropTypes.string.isRequired,
  summitElevation: PropTypes.string.isRequired,
  climbCreated: PropTypes.string.isRequired,
  challengeName: PropTypes.string.isRequired,
  challengeAllSummits: PropTypes.number.isRequired,
  challengeFinishedSummits: PropTypes.number.isRequired,
  likesCount: PropTypes.number.isRequired,
};

export default compose(withStyles(styles), withLocalization)(ActivityPost);
