import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import compose from '../../../compose';

import classNames from 'classnames';
import getSharedStyles from '../../../style';


const styles = theme => ({
    ...getSharedStyles(theme),
    socialConnectCard: {
        background: 'white',
    },
    socialIcon: {
        width: '30px',
        height: '30px',
    }
});

function StravaConnection(props) {
    const { classes } = props;

    const handleClick = () => {
        const redirect = process.env.REACT_APP_HOMEPAGE;
        // const redirect = 'http://localhost:3000';
        const url = `https://www.strava.com/oauth/authorize?client_id=${props.config.id}&response_type=code&scope=read,activity:read&redirect_uri=${redirect}?provider=4`;
        window.location.replace(url);
    };

    if (!props.config) {
        return null;
    }

    return (
        <>
            <div className={classNames(classes.p3, classes.socialConnectCard, classes.roundedMd, classes.dBlock, props.className)} onClick={handleClick}>
                <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter)}>
                    <img src={"./img/strava-logo.png"} className={classNames(classes.socialIcon)} />
                    <div className={classNames(classes.flexCol, classes.ml3)}>
                        <div className={classNames()}>
                            Prida a prepoji Strava
                        </div>
                        <div className={classNames(classes.textXs2, classes.text300)}>
                            Njs priateov a prida link na profil
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default compose(
    withStyles(styles),
)(StravaConnection);