import React, { useState }  from 'react';
import PropTypes from 'prop-types';
import withDoby from '../../../consumers/withDoby';
import withUser from '../../../consumers/withUser';
import withStore from '../../../consumers/withStore';
import compose from '../../../compose';

import { withStyles } from '@material-ui/core/styles';
import { withLocalization } from '../../shared/localization'
import { formatPrice, getCurrencySymbol } from '../../../utils/helpers';

import getSharedStyles from '../../../style';
import classNames from 'classnames';
import config from '../../../config';
//import ClearIcon from '@material-ui/icons/Clear';

const styles = theme => ({
    ...getSharedStyles(theme),
    productThumbnail: {
        width: '70px'
    },
    itemCountBtn: {
        backgroundColor: '#ececec',
        width: '24px',
        height: '24px',
        lineHeight: '21px',
        textAlign: 'center',
        fontSize: '19px',
        borderRadius: '4px',
        cursor: 'pointer'
    },
    itemCount: {
        width: '60px',
        textAlign: 'center'
    }
});

function CheckouCartItem(props) {
    const { classes } = props;
    const renderVariants = () => {
        return (
            <div className={classNames(classes.textXs2)}>Variant</div>
        );
    }
   
    const [img, setImg] = useState(null);
    const getImage = async () => {
        try {
            if(props.thumbnailId) {
                const data = await props.store.getFile(props.thumbnailId);
                var reader = new FileReader();
                reader.onloadend = () => {
                    setImg(reader.result);
                }
                reader.readAsDataURL(data);
            }
        } catch(e) {
            console.error(e);
        }
    }
    getImage();

    return (         
        <>
            <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter, classes.justifyContentBetween)}> 
                <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter)}>
                    <div>
                        <img
                            className={classes.productThumbnail} 
                            src={ img || ''} 
                            alt=""
                        />
                    </div>
                    <div className={classNames(classes.flexCol, classes.ml3)}>
                        <div className={classNames(classes.textXs)}>{props.productName}</div>
                        <div>{renderVariants()}</div>
                    </div>
                </div>   
                <div className={classNames(classes.flexCol)}>
                    <div className={classNames(classes.fontBold)}>{formatPrice(props.priceWithTax * props.productCount)}</div>
                    <div className={classNames(classes.textXs2, classes.textRight)}>{props.translate('with_tax')}</div> 
                </div>            
            </div> 
            <hr className={classNames(classes.w100, classes.mt4, classes.mb4, classes.hrLine)}/>
        </>         
    );
}

CheckouCartItem.propTypes = {
    productName: PropTypes.string.isRequired, 
    techParams: PropTypes.array, 
    priceWithTax: PropTypes.number.isRequired,
    thumbnailId: PropTypes.string,
    productId: PropTypes.string.isRequired,
    productCount: PropTypes.number.isRequired,
    countInStock: PropTypes.number.isRequired,
};

export default compose(
    withStyles(styles),
    withDoby,
    withUser,
    withLocalization,
    withStore
)(CheckouCartItem);