import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { withLocalization } from '../shared/localization';
import getSharedStyles from '../../style';
import compose from '../../compose';
import classNames from 'classnames';
import Button from './form/Button';

const styles = theme => ({
    ...getSharedStyles(theme),

    card: {
        minWidth: 300,   
    },   
});

function ChallengeCard(props) {
    const { classes } = props;
    return (
        <div className={classNames(classes.card, classes.roundedMd, classes.p5, classes.cardShadow, props.className)}>
            <div className={classNames()}>
                <div className={classNames(classes.textMd, classes.fontBold)}>
                    {props.title}
                </div>
                <div className={classNames(classes.textXs, classes.text200)}>
                    {props.description}
                </div>
            </div>
            <div className={classNames(classes.mt4, classes.dFlex, classes.w100, classes.justifyContentBetween)}>
                <Button 
                    fullwidth
                    className={classNames(classes.mr1)}
                    onClick={props.onClickMoreInfo}
                >
                   {props.translate('more_info')}
                </Button>
                <Button 
                    fullwidth
                    className={classNames(classes.ml1)}
                    onClick={props.onClickParticipate}
                >
                    {props.translate('participate')}
                </Button>               
            </div>
        </div>
    );
}

ChallengeCard.propTypes = {
    className: PropTypes.string,
    onClickMoreInfo: PropTypes.func,
    onClickParticipate: PropTypes.func,
    title: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
};

export default compose(
    withStyles(styles),
    withLocalization
)(ChallengeCard);