import React from "react";
import PropTypes from "prop-types";
import ReactMde from "react-mde";
import Markdown from "./Markdown";
import withUtils from "./utils/withUtils";
import "react-mde/lib/styles/css/react-mde-all.css";

const URL = "https://mailer.poprad.space";
function Editor(props) {
  const save = async function* (data) {
    var blob = new Blob([data], { type: "image/jpeg" });
    var urlCreator = window.URL || window.webkitURL;
    var src = urlCreator.createObjectURL(blob);
    const d = await props.utils.resize(src, this.props.height, "img.jpg");

    try {
      const file = await props.utils.postFile(
        URL,
        d,
        "dGVzdDozPGdEdGc2PkQnOXR9XCFE"
      );
      yield `${URL}/uploads/${file._id}`;
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  };

  const [selectedTab, setSelectedTab] = React.useState("write");
  return (
    <ReactMde
      value={props.value}
      onChange={(value) => props.onChange(props.field, value)}
      selectedTab={selectedTab}
      readOnly={props.disabled}
      onTabChange={setSelectedTab}
      generateMarkdownPreview={(content) =>
        Promise.resolve(<Markdown>{content}</Markdown>)
      }
      paste={{
        saveImage: save,
      }}
    />
  );
}

Editor.propTypes = {
  field: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  height: PropTypes.number.isRequired,
  disabled: PropTypes.bool,
};

export default withUtils(Editor);
