import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { withLocalization } from "../shared/localization";
import getSharedStyles from "../../style";
import compose from "../../compose";
import classNames from "classnames";
import Button from "./form/Button";

const styles = (theme) => ({
  ...getSharedStyles(theme),

  profileImgHolder: {
    width: "60px",
    height: "60px",
    minWidth: "60px",
    overflow: "hidden",
    position: "relative",
  },
  profileImg: {
    // width: '120px',
    borderRadius: "100px",
    border: "3px solid white",
    width: "100%",
    height: "100%",
    position: "absolute",
    left: 0,
    right: 0,
    objectFit: "cover",
  },
  medalImgHolder: {
    width: "30px",
    height: "30px",
    minWidth: "30px",
    overflow: "hidden",
    position: "relative",
  },
  medalImg: {
    // width: '120px',
    borderRadius: "100px",
    border: "3px solid white",
    width: "100%",
    height: "100%",
    position: "absolute",
    left: 0,
    right: 0,
    objectFit: "cover",
  },
  card: {
    minWidth: 300,
  },
});

function FollowCard(props) {
  const { classes } = props;

  const handleOnClick = () => {
    props.onClick(props.userID);
  };

  return (
    <div
      className={classNames(
        classes.card,
        classes.roundedMd,
        classes.p5,
        classes.cardShadow,
        props.className
      )}
    >
      <div
        className={classNames(
          classes.mt4,
          classes.dFlex,
          classes.w100,
          classes.justifyContentBetween
        )}
      >
        <div className={classNames(classes.profileImgHolder)}>
          <img
            alt="avatarImg"
            src={"./img/user-img.jpg"}
            className={classNames(classes.profileImg)}
          />
        </div>
        <div className={classNames()}>
          <div className={classNames(classes.textMonoH4, classes.fontBold)}>
            {props.name}
          </div>
          {props.medalCount !== 0 && (
            <div className={classNames(classes.textXs, classes.text200)}>
              <div className={classNames(classes.medalImgHolder)}>
                <img
                  alt="avatarImg"
                  src={"./img/user-img.jpg"}
                  className={classNames(classes.medalImg)}
                />
                {`+${props.medalCount}`}
              </div>
            </div>
          )}
        </div>
        <Button
          fullwidth
          className={classNames(classes.mt3, classes.w100)}
          onClick={handleOnClick}
        >
          {props.translate(props.buttonLabel || "follow")}
        </Button>
      </div>
    </div>
  );
}

FollowCard.propTypes = {
  className: PropTypes.object.isRequired,
  buttonLabel: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
  name: PropTypes.string.isRequired,
  medalCount: PropTypes.string.isRequired,
  userID: PropTypes.string,
};

export default compose(withStyles(styles), withLocalization)(FollowCard);
