import React from "react";
import GithubIcon from "../icons/GithubIcon";
import Button from '@material-ui/core/Button';

const GithubButton = props => {
  const handleClick = () => {
    const url = `https://github.com/login/oauth/authorize?client_id=${props.config.id}&scope=user&redirect_uri=${process.env.REACT_APP_HOMEPAGE}?provider=1`;
    window.location.replace(url);
  };

  if(!props.config) {
    return null;
  }

  return (
    <Button fullWidth={props.fullWidth} color="primary" tooltip="Github" onClick={handleClick} disabled={props.disabled}>
      <GithubIcon fontSize="large" /> <span style={props.fullWidth ? { width: 200} : null}>{props.title}</span>
    </Button>
  );
};

export default GithubButton;
