import { store } from "kd-web";

export const postFile = async (file) => {
  try {
    return await store.postFile(file);
  } catch (error) {
    throw error;
  }
};

export const getFile = async (id) => {
  try {
    return await store.getFile(id);
  } catch (error) {
    throw error;
  }
};

export const delFile = async (id) => {
  try {
    return await store.delFile(id);
  } catch (error) {
    throw error;
  }
};

const ENUMS_COLLECTION = "enums";
export const addEnum = async (data) => {
  try {
    await store.post(ENUMS_COLLECTION, data);
  } catch (error) {
    throw error;
  }
};

export const editEnum = async (data) => {
  try {
    await store.put(ENUMS_COLLECTION, data, data._id);
  } catch (error) {
    throw error;
  }
};

export const getEnums = async (enumTypes, filters, sort, page) => {
  try {
    const enums = await store.get(ENUMS_COLLECTION, filters, sort, page);
    for (const x of enums.data) {
      x._enumType = enumTypes.find((e) => e._id === x.enumTypeId) || {
        name: null,
      };
    }
    return enums;
  } catch (error) {
    throw error;
  }
};

export const deleteEnum = async (id) => {
  try {
    await store.delById(ENUMS_COLLECTION, id);
  } catch (error) {
    throw error;
  }
};

