import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import ClearIcon from "@material-ui/icons/Clear";
import IconButton from "@material-ui/core/IconButton";
import Form from "../shared/form/Form";
import Select from "../shared/Select";

import withDoby from "../../consumers/withDoby";
import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";

const styles = (theme) => ({
  nameTextInput: {
    display: "flex",
    flex: 1,
  },
  margin: {
    margin: theme.spacing.unit,
  },
});

const INIT_STATE = {
  enumTypeData: [],
  enumType: "",
  enumValueData: [],
  enumValue: "",
  disabled: false,
};

class TechParamForm extends React.Component {
  constructor(props) {
    super(props);

    this.state = props.data || INIT_STATE;
  }

  handleChange = (name, value) => {
    if (name === "enumType") {
      const { enums } = this.props.store;

      const enumValueData = enums.data.filter((e) => e.enumTypeId === value);
      this.setState({ enumValueData });
    }

    this.setState({ [name]: value }, this.callBack);
  };

  callBack = () => {
    const { enumType, enumValue } = this.state;
    if (enumType !== "" && enumValue !== "")
      this.props.onChange(enumType, enumValue);
  };

  handleSubmit = async () => {};

  handleDelete = () => {
    this.props.onDelete(this.state.enumType, this.props.inOrder);
    this.setState({ disabled: false });
  };

  render() {
    const { classes, enumTypeData } = this.props;
    const { enumType, enumValueData, enumValue, disabled } = this.state;

    return (
      <Form onSubmit={this.handleSubmit} inline disabled={true}>
        <div className={classes.nameTextInput}>
          <Select
            value={enumType}
            field="enumType"
            onChange={this.handleChange}
            data={enumTypeData}
            textField="name"
            fullWidth
            required
            disabled={disabled}
          />
        </div>
        <div className={classes.nameTextInput}>
          <Select
            value={enumValue}
            field="enumValue"
            onChange={this.handleChange}
            data={enumValueData}
            textField="name"
            fullWidth
            required
            disabled={enumValueData.length === 0 || disabled}
          />
        </div>
        <IconButton
          aria-label="Clear"
          className={classes.margin}
          onClick={this.handleDelete}
        >
          <ClearIcon fontSize="small" />
        </IconButton>
      </Form>
    );
  }
}

TechParamForm.propTypes = {
  classes: PropTypes.object.isRequired,
  onDelete: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  enumTypeData: PropTypes.array.isRequired,
  data: PropTypes.object.isRequired,
  inOrder: PropTypes.number,
};

export default compose(
  withStyles(styles),
  withStore,
  withDoby,
  withUser
)(TechParamForm);
