import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import Checkbox from "@material-ui/core/Checkbox";
import EnhancedTableHead from "./TableHead";
import EnhancedTableToolbar from "./TableToolbar";
import AlertDialog from "../AlertDialog";
import FilterDialog, { buildFilters } from "../FilterDialog";
import ActionsMenu from "../ActionsMenu";
import Progress from "../Progress";
import { CSVLink } from "react-csv";
import _ from "lodash";
import { withLocalization } from "../localization";

const styles = (theme) => ({
  root: {
    width: "100%",
  },
  table: {
    // minWidth: 1020,
  },
  tableWrapper: {
    overflowX: "auto",
    position: "relative",
  },
});

let _headers = [];
class EnhancedTable extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      selected: [],
      page: 0,
      rowsPerPage: 5,
      filters: buildFilters(props.rows, props.filters, props.defaultFilters),
      sort: props.defaultSort || { field: "_id", dir: "asc" },
      isOpenDelete: false,
      isOpenFilter: false,
      filter: null,
      exported: [],
    };

    this.exportRef = React.createRef();
  }

  handleRequestSort = (event, property) => {
    const field = property;
    let dir = "desc";

    if (this.state.sort.field === property && this.state.sort.dir === "desc") {
      dir = "asc";
    }

    const sort = { field, dir };
    const { filters, page, rowsPerPage } = this.state;
    this.onChange(filters, page, rowsPerPage, sort);
    this.setState({ sort });
  };

  handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const data = this.props.store[this.props.data].data;
      this.setState((state) => ({
        selected: data
          .filter((n) => this.props.isDisabled(n) === false)
          .map((n) => n._id),
      }));
      return;
    }
    this.setState({ selected: [] });
  };

  handleClick = (id) => {
    const { selected } = this.state;
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1)
      );
    }

    this.setState({ selected: newSelected });
  };

  handleChangePage = (event, page) => {
    const { filters, rowsPerPage, sort } = this.state;
    this.onChange(filters, page, rowsPerPage, sort);
    this.setState({ page });
  };

  handleChangeRowsPerPage = (event) => {
    const rowsPerPage = event.target.value;
    const { filters, page, sort } = this.state;
    this.onChange(filters, page, rowsPerPage, sort);
    this.setState({ rowsPerPage });
  };

  isSelected = (id) => this.state.selected.indexOf(id) !== -1;

  onChange = (filters, page, rowsPerPage, sort) => {
    this.props.onChange(
      filters,
      { limit: rowsPerPage, skip: rowsPerPage * page },
      sort,
      true
    );
  };

  componentDidMount = () => {
    const { filters, page, rowsPerPage, sort } = this.state;
    this.onChange(filters, page, rowsPerPage, sort);
  };

  handleOpenFilter = () => {
    this.setState({ isOpenFilter: true });
  };

  handleCloseFilter = () => {
    this.setState({ isOpenFilter: false });
  };

  handleConfirmFilter = (state) => {
    const filters = buildFilters(this.props.rows, this.props.filters, state);
    const page = 0;
    const { rowsPerPage, sort } = this.state;
    this.onChange(filters, page, rowsPerPage, sort);
    this.setState({ filters, page, isOpenFilter: false });
  };

  onDeleteMultiple = async () => {
    const { selected, filters, page, rowsPerPage, sort } = this.state;
    await Promise.all(selected.map((id) => this.props.onDelete(id)));
    this.onChange(filters, page, rowsPerPage, sort);
    this.setState({ selected: [] });
    this.handleCloseDelete();
  };

  strToDate = (str) => {
    const date = new Date(str);
    return date.toLocaleDateString(this.props.locale);
  };

  strToDateTime = (str) => {
    const date = new Date(str);
    return date.toLocaleString(this.props.locale, {
      hour12: false,
      year: "numeric",
      month: "numeric",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  };

  getValue = (n, row) => {
    const value = _.get(n, row.field);
    if (row.cell) {
      return row.cell(n);
    }

    switch (row.type) {
      case "date":
        return this.strToDate(value);
      case "datetime":
        return this.strToDateTime(value);
      case "bool":
        return value ? "T" : "F";
      case "array":
        return value.map((s) => s[row.textField]).join(" | ");
      default:
        return value;
    }
  };

  onDeleteSingle = async (id) => {
    const { filters, page, rowsPerPage, sort } = this.state;
    await this.props.onDelete(id);
    this.onChange(filters, page, rowsPerPage, sort);
  };

  handleCloseDelete = () => {
    this.setState({ isOpenDelete: false });
  };

  handleOpenDelete = () => {
    this.setState({ isOpenDelete: true });
  };

  onExport = async () => {
    const { filters, sort } = this.state;
    _headers = [];
    this.props.rows.forEach((x) => {
      if (x.exportable !== false) {
        const field = x.idField || x.field;
        _headers.push({
          label: this.props.translate("field." + field),
          key: x.field,
        });
      }
    });
    const exported = await this.props.onChange(filters, undefined, sort, false);
    if (exported) {
      this.setState({ exported: exported.data });
      this.exportRef.current.link.click();
    }
  };

  handleRefresh = () => {
    const { filters, page, rowsPerPage, sort } = this.state;
    this.onChange(filters, page, rowsPerPage, sort, true);
  };

  render() {
    const { classes, rows, sortable, title, translate } = this.props;
    const {
      selected,
      rowsPerPage,
      page,
      sort,
      isOpenDelete,
      isOpenFilter,
    } = this.state;
    const data = this.props.store[this.props.data];
    const emptyRows =
      rowsPerPage - Math.min(rowsPerPage, data.total - page * rowsPerPage);

    return (
      <Paper className={classes.root}>
        <EnhancedTableToolbar
          numSelected={selected.length}
          onDelete={this.handleOpenDelete}
          onExport={this.onExport}
          onFilter={this.handleOpenFilter}
          title={title}
          isFilterSet={this.state.filters.length > 0}
          actions={this.props.actions}
          onRefresh={this.handleRefresh}
          exportDisabled={!this.props.filename}
        />
        <div className={classes.tableWrapper}>
          <Progress isLoading={data.isLoading} />
          <Table
            className={classes.table}
            aria-labelledby="tableTitle"
            size="small"
          >
            <EnhancedTableHead
              numSelected={selected.length}
              sort={sort}
              onSelectAllClick={this.handleSelectAllClick}
              onRequestSort={this.handleRequestSort}
              rowCount={data.total}
              rows={rows}
              sortable={sortable}
            />
            <TableBody>
              {data.data.map((n) => {
                const isSelected = this.isSelected(n._id);
                const disabled = this.props.isDisabled(n);
                return (
                  <TableRow
                    hover
                    // onClick={event => this.handleClick(n._id)}
                    role="checkbox"
                    aria-checked={isSelected}
                    tabIndex={-1}
                    key={n._id}
                    selected={isSelected}
                  >
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={isSelected}
                        onClick={(event) => this.handleClick(n._id)}
                        disabled={disabled}
                      />
                    </TableCell>

                    {rows.map((row) => (
                      <TableCell
                        key={row.field}
                        align={row.type === "num" ? "right" : "left"}
                        padding={row.disablePadding ? "none" : "default"}
                      >
                        {this.getValue(n, row)}
                      </TableCell>
                    ))}
                    <TableCell>
                      <ActionsMenu
                        onDelete={() => this.onDeleteSingle(n._id)}
                        onEdit={() => this.props.onEdit(n)}
                        onClone={
                          this.props.onClone
                            ? () => this.props.onClone(n)
                            : undefined
                        }
                        disabled={disabled}
                      />
                    </TableCell>
                  </TableRow>
                );
              })}
              {emptyRows > 0 && (
                <TableRow style={{ height: 49 * emptyRows }}>
                  <TableCell colSpan={6} />
                </TableRow>
              )}
            </TableBody>
          </Table>
        </div>
        <TablePagination
          labelRowsPerPage={translate("table.rowsPerPage")}
          labelDisplayedRows={({ from, to, count }) =>
            `${from}-${to} ${translate("table.of")} ${count}`
          }
          rowsPerPageOptions={[5, 10, 25]}
          component="div"
          count={data.total}
          rowsPerPage={rowsPerPage}
          page={page}
          backIconButtonProps={{
            "aria-label": translate("table.previousPage"),
          }}
          nextIconButtonProps={{
            "aria-label": translate("table.nextPage"),
          }}
          onChangePage={this.handleChangePage}
          onChangeRowsPerPage={this.handleChangeRowsPerPage}
        />
        <AlertDialog
          isOpen={isOpenDelete}
          onClose={this.handleCloseDelete}
          onConfirm={this.onDeleteMultiple}
          title={translate("table.delete")}
          description={translate("table.sure")}
        />
        <FilterDialog
          isOpen={isOpenFilter}
          onClose={this.handleCloseFilter}
          onConfirm={this.handleConfirmFilter}
          store={this.props.store}
          rows={this.props.rows}
          filters={this.props.filters}
          defaultState={this.props.defaultFilters}
        />
        <CSVLink
          data={this.state.exported}
          headers={_headers}
          filename={this.props.filename + ".csv"}
          style={{ display: "none" }}
          ref={this.exportRef}
          target="_blank"
        />
      </Paper>
    );
  }
}

EnhancedTable.propTypes = {
  classes: PropTypes.object.isRequired,
  rows: PropTypes.array.isRequired,
  data: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  sortable: PropTypes.bool,
  onDelete: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  onEdit: PropTypes.func.isRequired,
  store: PropTypes.object.isRequired,
  onClone: PropTypes.func,
  filters: PropTypes.array,
  isDisabled: PropTypes.func.isRequired,
  filename: PropTypes.string,
  actions: PropTypes.element,
  defaultState: PropTypes.object,
  defaultSort: PropTypes.object,
};

export default withLocalization(withStyles(styles)(EnhancedTable));
