import React from 'react';
import Table from '../shared/table/Table';
import FormDialog from '../shared/FormDialog';
import Form from './Form';

import compose from '../../compose';
import withDoby from '../../consumers/withDoby';
import withUser from '../../consumers/withUser';
import { withLocalization } from '../shared/localization'

const ROWS = [
  { field: 'name', disablePadding: true },
  { field: '_id' }
];

class TableWrapper extends React.Component {
  constructor(props) {
    super(props);  
    this.state = {
      x: null,
      isOpen: false
    }
  }

  handleChangeClient = (name, value) => {
    this.setState({ clientId: value });
  }

  handleClose = () => {
    this.setState({ isOpen: false });
  }

  handleOpen = () => {
    this.setState({ isOpen: true });
  }

  handleChange = async (filters, page, sort, render) => {
    try {
      return await this.props.doby.getSeries(filters, sort, page, render);
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  onEdit = async (x) => {
    this.setState({ x, isOpen: true })
  }

  isDisabled = x => {
    return false;
  }

  render() {
    const { deleteSerie } = this.props.doby;
    const { translate } = this.props;

    return (
      <div>
        <Table
          rows={ROWS}
          data='series'
          onChange={this.handleChange}
          onDelete={deleteSerie}
          title={translate('series')}
          onEdit={this.onEdit}
          store={this.props.doby}
          filters={[]}
          isDisabled={this.isDisabled}
          filename='series'
        />
        <FormDialog
          title={translate('serie')}
          onClose={this.handleClose}
          isOpen={this.state.isOpen}
        >
          <Form
            data={this.state.x}
            onSubmit={this.handleClose}
          />
        </FormDialog>
      </div>
    );
  }
}

export default compose(
  withDoby,
  withUser,
  withLocalization
)(TableWrapper);