import React, { useState, useEffect } from 'react';
import { withStyles } from '@material-ui/core/styles';
import withStore from '../../../consumers/withStore';
import withDoby from '../../../consumers/withDoby';
import getSharedStyles from '../../../style';
import classNames from 'classnames';
import { withLocalization } from '../../shared/localization'
import Select from '../../shared/Select';
import compose from '../../../compose';
import Button from '../form/Button';
import Modal from '../Modal';

const styles = theme => ({
    ...getSharedStyles(theme),
    productImg: {
        maxWidth: '100%'
    },
    thumbnailHolder: {
        height: '110px',
        textAlign: 'center'
    }
});

class ProductSmallCard extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            img: null,
            addToCartModal: false,
            selectedProduct: null,
            detailImage: null,
            selectedVariants: {},
        }
    }

    componentDidMount() {
        this.getImg();
    }

    getImg = async () => {
        try {
            if (this.props.thumbnailId) {
                const data = await this.props.store.getFile(this.props.thumbnailId);
                var reader = new FileReader();
                reader.onloadend = () => {
                    this.setState({ img: reader.result })
                }
                reader.readAsDataURL(data);
            }
        } catch (e) {
            console.error(e);
        }
    }

    openAddToCartModal = async () => {
        await this.props.doby.getProductsByCatalogId(this.props.catalogId);
        const catalogProducts = this.props.doby.catalog.data;
        if (catalogProducts.length > 0) {
            this.setState({ selectedProduct: catalogProducts[0] }, () => {
                this.fetchDetailImage();
            });
        }
        this.setState({ addToCartModal: true })
    }

    handleChangeDropdown = (name, value) => {
        this.setState((state) => {
            return {
                ...state,
                selectedVariants: {
                    ...state.selectedVariants,
                    [name]: value
                }
            };
        }, () => {
            const product = this.getProductByDropdownSelection();
            this.setState({ selectedProduct: product }, () => this.fetchDetailImage());
        });
    }

    fetchDetailImage = async () => {
        const { imageId } = this.state.selectedProduct;
        if (imageId) {
            const data = await this.props.store.getFile(imageId);
            let reader = new FileReader();
            reader.onloadend = () => this.setState({ detailImage: reader.result });
            reader.readAsDataURL(data);
        }
    }

    getProductByDropdownSelection = () => {
        //NOTE: musme vybrat tolko dropdownov kolko mame unikatnych parametrov pre produkt variant
        if (Object.keys(this.state.selectedVariants).length !== Object.keys(this.props.doby.productVariants.data).length)
            return null;

        for (let variant of this.props.doby.catalog.data) {
            let hasAllRequirements = false;
            for (let variantKey in this.state.selectedVariants) {
                if (variant.tech_param && variant.tech_param[variantKey] === this.state.selectedVariants[variantKey]) {
                    hasAllRequirements = true;
                    break;
                }
            }

            if (hasAllRequirements)
                return variant
        }
        return null;
    }

    addToCart = () => {
        if (this.state.selectedProduct) {
            this.props.doby.cartAddProduct(this.state.selectedProduct);
            //this.setState({showSnackbar: true});
        }
    }

    renderDropdowns = () => {
        let dropdowns = [];
        for (let variantKey in this.props.doby.productVariants.data) {
            dropdowns.push(
                <Select
                    key={variantKey}
                    value={this.state.selectedVariants[variantKey] ? this.state.selectedVariants[variantKey].toString() : ''}
                    field={variantKey}
                    onChange={this.handleChangeDropdown}
                    data={this.props.doby.productVariants.data[variantKey].data}
                    textField={"name"}
                    fullWidth
                    required
                />
            );
        }
        return (
            <>
                { dropdowns}
            </>
        );
    }

    render() {
        const { classes, title, price } = this.props;

        return (
            <>
                <div className={classNames(classes.roundedMd, classes.bgWhite, classes.p4)}>
                    <div className={classNames(classes.flexCol)}>
                        <div className={classNames(classes.thumbnailHolder)}>
                            <img src={this.state.img} className={classNames(classes.productImg)} alt="" />
                        </div>
                        <div className={classNames(classes.textXs, classes.fontSemibold, classes.mt3)}>
                            {title}
                        </div>
                        <div className={classNames(classes.fontBold)}>
                            {price}  <span className={classNames(classes.textXs2, classes.text200, classes.fontRegular)}>s DPH</span>
                        </div>
                        <Button
                            className={classNames(classes.mt4, classes.textXs2)}
                            onClick={this.openAddToCartModal}
                            fullwidth
                        >
                            {this.props.translate('add_to_cart')}
                        </Button>
                        <Button
                            className={classNames(classes.mt2, classes.textXs2)}
                            outline={true}
                            onClick={this.props.onClickViewDetails}
                            fullwidth
                        >
                            {this.props.translate('product_details')}
                        </Button>
                    </div>
                </div>

                <Modal
                    isOpen={this.state.addToCartModal}
                    onClose={() => this.setState({ addToCartModal: false })}
                >
                    {!this.props.doby.catalog.isLoading && <>   
                        <img
                            className={classes.productImg}
                            src={this.state.detailImage ? this.state.detailImage : ''}
                            alt={''}
                        />
                        { this.renderDropdowns() }
                        <Button
                            className={classNames(classes.mt2, classes.textXs2)}
                            outline={true}
                            fullwidth
                            onClick={this.addToCart}
                            disabled={this.state.selectedProduct && this.state.selectedProduct.disabled}
                        >
                            {this.props.translate('add_to_cart')}
                        </Button>
                    </> }
                </Modal>
            </>
        );
    }
}

export default compose(
    withStyles(styles),
    withLocalization,
    withStore,
    withDoby
)(ProductSmallCard);