import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function DropdownFaqClosedIcon(props) {
    const { classes } = props;
    return (
        <>
            <svg width="100%" height="100%" viewBox="0 0 32 32" fill="none" xmlns="http://www.w3.org/2000/svg" className={classNames(classes.icon)} style={{height: `${props.size}px`}}>
                <path d="M26 11L16 21L6 11" fill="#F5333F"/>
            </svg>         
        </>
    )
}

export default compose(
    withStyles(styles)
)(DropdownFaqClosedIcon);