import React from "react";
import DobyContext from "../contexts/DobyContext";
import * as dobyUtils from "../utils/dobyUtils";
import * as storeUtils from "../utils/storeUtils";

class DobyProvider extends React.Component {
  state = {
    summits: {
      total: 0,
      data: [],
      filters: [],
      page: null,
      isLoading: false,
    },
    challenges: {
      total: 0,
      data: [],
      filters: [],
      page: null,
      isLoading: false,
    },
    products: {
      total: 0,
      data: [],
      filters: [],
      page: null,
      isLoading: false,
    },
    subordinateProducts: {
      total: 0,
      data: [],
      filters: [],
      page: null,
      isLoading: false,
    },
    catalog: {
      data: [],
      isLoading: false,
    },
    productVariants: {
      data: {},
    },
    cart: {
      data: {
        products: [],
      },
    },
    checkout: {
      data: {
        state: 0,
        orderData: {},
        coupon: {},
        deliveryData: {
          nameSurname: "",
          email: "",
          country: "",
          city: "",
          street: "",
          streetNumber: "",
          psc: "",
          tel: "",

          invoiceData: false,
          invoiceNameSurname: "",
          invoiceCountry: "",
          invoiceCity: "",
          invoiceStreet: "",
          invoiceStreetNumber: "",
          invoicePsc: "",

          companyOrder: false,
          ico: "",
          dic: "",
          icdph: "",
          bic: "",
          iban: "",
          accountOwner: "",

          deliveryType: null,
          paymentMethod: null,

          //TODO: localization
          //alebo ciselnik
          paymentMethods: [
            {
              id: 5,
              name: "Kartou",
              price: 0.0,
            },
            {
              id: 8,
              name: "Na dobierku",
              price: 1.9,
            },
          ],
        },
      },
      isLoading: false,
    },
    posts: {
      total: 0,
      data: [],
      filters: [],
      page: null,
      isLoading: false,
    },
    series: {
      total: 0,
      data: [],
      filters: [],
      page: null,
      isLoading: false,
    },
    partners: {
      total: 0,
      data: [],
      filters: [],
      page: null,
      isLoading: false,
    },
    users: {
      total: 0,
      data: [],
      filters: [],
      page: null,
      isLoading: false,
    },
    climbs: {
      total: 0,
      data: [],
      filters: [],
      page: null,
      isLoading: false,
    },
  };

  //-- [Summits] --
  getSummits = async (filters, sort, page, render = true) => {
    try {
      if (!render) {
        return await dobyUtils.getSummits(filters, sort, page);
      }

      this.setState((state) => ({
        summits: { ...state.summits, isLoading: true },
      }));
      const summits = await dobyUtils.getSummits(filters, sort, page);
      summits.isLoading = false;
      this.setState({ summits });
      return summits;
    } catch (error) {
      throw error;
    }
  };

  refreshSummits = async () => {
    try {
      this.setState((state) => ({
        summits: { ...state.summits, isLoading: true },
      }));
      const { filters, sort, page } = this.state.summits;
      const summits = await dobyUtils.getSummits(filters, sort, page);
      summits.isLoading = false;
      this.setState({ summits });
    } catch (error) {
      throw error;
    }
  };

  //-- [Chellenges] --
  getChallenges = async (filters, sort, page, render = true) => {
    try {
      if (!render) {
        return await dobyUtils.getChallenges(filters, sort, page);
      }

      this.setState((state) => ({
        challenges: { ...state.challenges, isLoading: true },
      }));
      const challenges = await dobyUtils.getChallenges(filters, sort, page);
      challenges.isLoading = false;
      this.setState({ challenges });
      return challenges;
    } catch (error) {
      throw error;
    }
  };

  refreshChallenges = async () => {
    try {
      this.setState((state) => ({
        challenges: { ...state.challenges, isLoading: true },
      }));
      const { filters, sort, page } = this.state.challenges;
      const challenges = await dobyUtils.getChallenges(filters, sort, page);
      challenges.isLoading = false;
      this.setState({ challenges });
    } catch (error) {
      throw error;
    }
  };

  //-- [Subordinate products] --
  getSubordinateProducts = async (filters, sort, page, render = true) => {
    try {
      if (!render) {
        return await dobyUtils.getProducts(filters, sort, page);
      }

      this.setState((state) => ({
        subordinateProducts: { ...state.subordinateProducts, isLoading: true },
      }));

      const subordinateProducts = await dobyUtils.getProducts(
        filters,
        sort,
        page
      );
      subordinateProducts.isLoading = false;
      this.setState({ subordinateProducts });
      return subordinateProducts;
    } catch (error) {
      throw error;
    }
  };

  //-- [Products] --
  getProducts = async (filters, sort, page, render = true) => {
    try {
      if (!render) {
        return await dobyUtils.getProducts(filters, sort, page);
      }

      this.setState((state) => ({
        products: { ...state.products, isLoading: true },
      }));

      const products = await dobyUtils.getProducts(filters, sort, page);
      products.isLoading = false;
      this.setState({ products });
      return products;
    } catch (error) {
      throw error;
    }
  };

  buildDropdowns = async (catalog) => {
    let dropdowns = {};
    for (let variant of catalog) {
      const paramKeys = Object.keys(variant.tech_param);
      if (paramKeys.length <= 0) continue;

      const paramKey = paramKeys[0];
      if (!dropdowns.hasOwnProperty(paramKey)) {
        dropdowns[paramKey] = [];
      }
      dropdowns[paramKey].push(variant.tech_param[paramKey]);
    }

    for (let property in dropdowns) {
      const filter = [{ field: "_id", op: "in", value: dropdowns[property] }];
      dropdowns[property] = await storeUtils.getEnums([], filter);
    }

    return dropdowns;
  };

  getProductsByCatalogId = async (id) => {
    try {
      this.setState((state) => ({
        catalog: { ...state.catalog, isLoading: true },
      }));
      const productsArray = await dobyUtils.getProductsByCatalogId(id);    
      const dropdowns = await this.buildDropdowns(productsArray);
      this.setState({ catalog: {data: [...productsArray], isLoading: false}, productVariants: { data: dropdowns } });
      return productsArray;
    } catch (error) {
      throw error;
    }
  };

  getProduct = async (id) => {
    try {
      this.setState((state) => ({
        product: { ...state.product, isLoading: true },
      }));
      const product = await dobyUtils.getProduct(id);
      product.isLoading = false;
      this.setState({ product });
      return product;
    } catch (error) {
      throw error;
    }
  };

  refreshProducts = async () => {
    try {
      this.setState((state) => ({
        products: { ...state.products, isLoading: true },
      }));
      const { filters, sort, page } = this.state.products;
      const products = await dobyUtils.getProducts(filters, sort, page);
      products.isLoading = false;
      this.setState({ products });
    } catch (error) {
      throw error;
    }
  };

  deleteProduct = async (id) => {
    this.setState((state) => ({
      products: { ...state.products, isLoading: true },
    }));

    const data = this.state.products.data.find((p) => p._id === id);
    if (data) await dobyUtils.editProduct({ ...data, disabled: true });

    this.setState((state) => ({
      products: { ...state.products, isLoading: false },
    }));
  };

  //-- [Cart] --
  cartGetProducts = () => {
    const savedProducts = localStorage.getItem("cart");
    if (savedProducts) {
      const cart = JSON.parse(savedProducts);
      this.setState({ cart });
    }
  };

  cartAddProduct = (product, count = 1) => {
    const existingProduct = this.state.cart.data.products.find(
      (item) => item.product._id === product._id
    );
    if (existingProduct) {
      if (existingProduct.product.catalog_id !== "pack")
        this.cartIncreaseCount(product._id, count);
      return;
    }

    this.setState(
      (state) => ({
        cart: {
          data: {
            products: [...state.cart.data.products, { count, product }],
          },
        },
      }),
      () => {
        const serializedCart = JSON.stringify(this.state.cart);
        localStorage.setItem("cart", serializedCart);
      }
    );
  };

  cartIncreaseCount = (productId, increase) => {
    const productIndex = this.state.cart.data.products.findIndex(
      (item) => item.product._id === productId
    );
    if (productIndex === -1) return;

    this.setState(
      (state) => ({
        cart: {
          data: {
            products: state.cart.data.products.map((item, i) => {
              if (i === productIndex) {
                return {
                  count: item.count + increase < 1 ? 1 : item.count + increase,
                  product: item.product,
                };
              }
              return item;
            }),
          },
        },
      }),
      () => {
        const serializedCart = JSON.stringify(this.state.cart);
        localStorage.setItem("cart", serializedCart);
      }
    );
  };

  cartRemoveProduct = (productId) => {
    this.setState(
      (state) => ({
        cart: {
          data: {
            products: state.cart.data.products.filter(
              (item) => item.product._id !== productId
            ),
          },
        },
      }),
      () => {
        const serializedCart = JSON.stringify(this.state.cart);
        localStorage.setItem("cart", serializedCart);
      }
    );
  };

  cartRemoveProducts = () => {
    this.setState(
      {
        cart: {
          data: {
            products: [],
          },
        },
      },
      () => {
        const serializedCart = JSON.stringify(this.state.cart);
        localStorage.setItem("cart", serializedCart);
      }
    );
  };

  //-- [Order] --
  deliveryOrderChanged = (name, value) => {
    this.setState((state) => ({
      checkout: {
        data: {
          ...state.checkout.data,
          deliveryData: {
            ...state.checkout.data.deliveryData,
            [name]: value,
          },
        },
      },
    }));
  };

  deliveryOrderFill = (obj) => {
    this.setState((state) => ({
      checkout: {
        data: {
          ...state.checkout.data,
          deliveryData: {
            ...state.checkout.data.deliveryData,
            ...obj,
          },
        },
      },
    }));
  };

  redeeemDiscountCode = async (code) => {
    try {
      this.setState((state) => ({
        checkout: { ...state.checkout, isLoading: true },
      }));

      const coupon = await dobyUtils.redeeemDiscountCode(code);
      this.setState((state) => ({
        checkout: {
          data: { ...state.checkout.data, coupon: { ...coupon } },
          isLoading: false,
        },
      }));

      return coupon;
    } catch (error) {
      throw error;
    }
  };

  createOrder = async (orderData) => {
    try {
      this.setState((state) => ({
        checkout: { ...state.checkout, isLoading: true, orderData },
      }));

      const order = await dobyUtils.addOrder(orderData);
      this.setState((state) => ({
        checkout: { ...state.checkout, isLoading: false, orderData: order },
      }));

      return order;
    } catch (error) {
      throw error;
    }
  };

  verifyOrder = async (paymentId) => {
    try {
      this.setState((state) => ({
        checkout: { ...state.checkout, isLoading: true },
      }));

      const order = await dobyUtils.verifyOrder(paymentId);
      this.setState((state) => ({
        checkout: { ...state.checkout, isLoading: false, orderData: order },
      }));

      return order;
    } catch (error) {
      throw error;
    }
  };

  //-- [Posts] --
  getPosts = async (filters, sort, page, render = true) => {
    try {
      if (!render) {
        return await dobyUtils.getPosts(filters, sort, page);
      }

      this.setState((state) => ({
        posts: { ...state.posts, isLoading: true },
      }));
      const posts = await dobyUtils.getPosts(filters, sort, page);
      posts.isLoading = false;
      this.setState({ posts });
      return posts;
    } catch (error) {
      throw error;
    }
  };

  refreshPosts = async () => {
    try {
      this.setState((state) => ({
        posts: { ...state.posts, isLoading: true },
      }));
      const { filters, sort, page } = this.state.posts;
      const posts = await dobyUtils.getPosts(filters, sort, page);
      posts.isLoading = false;
      this.setState({ posts });
    } catch (error) {
      throw error;
    }
  };

  //-- [Series] --
  getSeries = async (filters, sort, page, render = true) => {
    try {
      if (!render) {
        return await dobyUtils.getSeries(filters, sort, page);
      }

      this.setState((state) => ({
        series: { ...state.series, isLoading: true },
      }));
      const series = await dobyUtils.getSeries(filters, sort, page);
      series.isLoading = false;
      this.setState({ series });
      return series;
    } catch (error) {
      throw error;
    }
  };
  refreshSeries = async () => {
    try {
      this.setState((state) => ({
        series: { ...state.series, isLoading: true },
      }));
      const { filters, sort, page } = this.state.series;
      const series = await dobyUtils.getSeries(filters, sort, page);
      series.isLoading = false;
      this.setState({ series });
    } catch (error) {
      throw error;
    }
  };

  //-- [Partners] --
  getPartners = async (filters, sort, page, render = true) => {
    try {
      if (!render) {
        return await dobyUtils.getPartners(filters, sort, page);
      }

      this.setState((state) => ({
        partners: { ...state.partners, isLoading: true },
      }));
      const partners = await dobyUtils.getPartners(filters, sort, page);
      partners.isLoading = false;
      this.setState({ partners });
      return partners;
    } catch (error) {
      throw error;
    }
  };

  refreshPartners = async () => {
    try {
      this.setState((state) => ({
        partners: { ...state.partners, isLoading: true },
      }));
      const { filters, sort, page } = this.state.partners;
      const partners = await dobyUtils.getPartners(filters, sort, page);
      partners.isLoading = false;
      this.setState({ partners });
    } catch (error) {
      throw error;
    }
  };

  //-- [User] --
  getUsers = async (filters, sort, page, render = true) => {
    try {
      if (!render) {
        return await dobyUtils.getUsers(filters, sort, page);
      }

      this.setState((state) => ({
        users: { ...state.users, isLoading: true },
      }));
      const users = await dobyUtils.getUsers(filters, sort, page);
      users.isLoading = false;
      this.setState({ users });
      return users;
    } catch (error) {
      throw error;
    }
  };

  //-- [Climbs] --
  getClimbs = async (filters, sort, page, render = true) => {
    try {
      if (!render) {
        return await dobyUtils.getClimbs(filters, sort, page);
      }

      this.setState((state) => ({
        climbs: { ...state.climbs, isLoading: true },
      }));
      const climbs = await dobyUtils.getClimbs(filters, sort, page);
      climbs.isLoading = false;
      this.setState({ climbs });
      return climbs;
    } catch (error) {
      throw error;
    }
  };

  render() {
    const { children } = this.props;

    return (
      <DobyContext.Provider
        value={{
          //-- [Summits] --
          getSummits: this.getSummits,
          refreshSummits: this.refreshSummits,
          addSummit: dobyUtils.addSummit,
          editSummit: dobyUtils.editSummit,
          deleteSummit: dobyUtils.deleteSummit,

          //-- [Challenges] --
          getChallenges: this.getChallenges,
          refreshChallenges: this.refreshChallenges,
          addChallenge: dobyUtils.addChallenge,
          editChallenge: dobyUtils.editChallenge,
          deleteChallenge: dobyUtils.deleteChallenge,

          //-- [Subordinate products] --
          getSubordinateProducts: this.getSubordinateProducts,

          //-- [Products] --
          getProducts: this.getProducts,
          getProductsByCatalogId: this.getProductsByCatalogId,
          getProduct: this.getProduct,
          refreshProducts: this.refreshProducts,
          addProduct: dobyUtils.addProduct,
          editProduct: dobyUtils.editProduct,
          // deleteProduct: dobyUtils.deleteProduct,
          deleteProduct: this.deleteProduct,

          //-- [Order] --
          deliveryOrderChanged: this.deliveryOrderChanged,
          deliveryOrderFill: this.deliveryOrderFill,
          redeeemDiscountCode: this.redeeemDiscountCode,
          createOrder: this.createOrder,
          verifyOrder: this.verifyOrder,

          //-- [Cart] --
          cartGetProducts: this.cartGetProducts,
          cartAddProduct: this.cartAddProduct,
          cartIncreaseCount: this.cartIncreaseCount,
          cartRemoveProduct: this.cartRemoveProduct,
          cartRemoveProducts: this.cartRemoveProducts,

          //-- [Posts] --
          getPosts: this.getPosts,
          refreshPosts: this.refreshPosts,
          addPost: dobyUtils.addPost,
          editPost: dobyUtils.editPost,
          deletePost: dobyUtils.deletePost,

          //-- [Series] --
          getSeries: this.getSeries,
          refreshSeries: this.refreshSeries,
          addSerie: dobyUtils.addSerie,
          editSerie: dobyUtils.editSerie,
          deleteSerie: dobyUtils.deleteSerie,

          //-- [Partners] --
          getPartners: this.getPartners,
          refreshPartners: this.refreshPartners,
          addPartner: dobyUtils.addPartner,
          editPartner: dobyUtils.editPartner,
          deletePartner: dobyUtils.deletePartner,

          //-- [Climbs] --
          getClimbs: this.getClimbs,
          addClimb: dobyUtils.addClimb,
          editClimb: dobyUtils.editClimb,
          deleteClimb: dobyUtils.deleteClimb,

          //-- [User] --
          getUsers: this.getUsers,
          getUser: dobyUtils.getUser,
          followUser: dobyUtils.followUser,
          unfollowUser: dobyUtils.unfollowUser,
          subscribe: dobyUtils.subscribe,

          //-- [Files] --
          getFile: dobyUtils.getFile,

          ...this.state,
        }}
      >
        {children}
      </DobyContext.Provider>
    );
  }
}

export default DobyProvider;
