import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import TextInput from "../shared/TextInput";
import Select from "../shared/Select";
import Form from "../shared/form/Form";
import Switch from '../shared/Switch';
import NumInput from "../shared/NumInput";

import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";

const styles = theme => ({
  nameTextInput: {
    display: "flex",
    flex: 1
  }
});

const INIT_STATE = {
  name: "",
  countryId: null,
  disabled: false,
  price: 0,
  key: 0
};

class FormWrapper extends React.Component {
  constructor(props) {
    super(props);

    this.state = props.data || INIT_STATE
  }

  handleChangeCountryRef = (name, value) => {
    this.setState({ [name]: { id: value } });
  };

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleSubmit = async () => {
    const {
      addTransport,
      refreshTransports,
      editTransport
    } = this.props.store;

    try {
      if (this.state._id) {
        await editTransport(this.state);
      } else {
        await addTransport(this.state);
      }
      refreshTransports();
      this.setState(INIT_STATE);
      if (this.props.onSubmit) this.props.onSubmit();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  async componentDidMount() {
    try {
      const { getEnums, } = this.props.store;
      const filter = [
        { field: "enumTypeId", value: 5 },
        { field: "disabled", value: false },
      ];
      await getEnums(filter);
    } catch (e) {
      console.error(e);
    }
  }

  render() {
    const { classes, disabled } = this.props;
    const { name, price, key, countryId } = this.state;
    const { enums } = this.props.store;

    return (
      <Form 
        onSubmit={this.handleSubmit} 
        inline
        disabled={disabled}
      >
        <div className={classes.nameTextInput}>
          <TextInput
            value={name}
            field="name"
            onChange={this.handleChange}
            fullWidth
            required
            disabled={disabled}
          />
        </div>
        <Select
          value={countryId}
          field="countryId"
          onChange={this.handleChange}
          data={enums.data}
          textField="name"
          disabled={disabled}
        />
        <NumInput
          value={price}
          field="price"
          onChange={this.handleChange}
          min={0}
          required
        />
        <NumInput
          value={key}
          field="key"
          onChange={this.handleChange}
          min={0}
          step={1}
          required
        />
        <Switch
          label=""
          value={this.state.disabled}
          field="disabled"
          onChange={this.handleChange}
        />
      </Form>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object,
  onSubmit: PropTypes.func,
  date: PropTypes.instanceOf(Date),
  disabled: PropTypes.bool
};

export default compose(withStyles(styles), withStore, withUser)(FormWrapper);
