import config from  '../config';

export const formatPrice = (price) => (Math.round((price * 100) + Number.EPSILON) / 100).toFixed(2) + '\xa0' + getCurrencySymbol(config.currency);
export const getCurrencySymbol = (currency) => {
    switch(currency) {
        case 'EUR': 
            return '';
        case 'USD': 
            return '$';
        default:
            return '';
    }
}