import React from "react";
import { hydrate, render } from "react-dom";
import "./index.css";
import App from "./App";
import * as serviceWorker from "./serviceWorker";
import { init } from "kd-web";
import config from "./config";

import moment from "moment";
import "moment/locale/sk";

import UserProvider from "./providers/UserProvider";
import StoreProvider from "./providers/StoreProvider";
import DobyProvider from './providers/DobyProvider';

import { MuiPickersUtilsProvider } from "material-ui-pickers";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import MomentUtils from "@date-io/moment";
import { LocalizationProvider } from "./components/shared/localization";
import en from './localization/en-US.json';
import sk from './localization/sk-SK.json';

init(config);

const locale = config.locale.split('-')[0];
moment.locale(locale);

const theme = createMuiTheme({
  palette: config.palette,
  typography: {
      fontFamily:[
        'IBM Plex Sans',
        'sans-serif',
      ].join(','),
  },
  breakpoints: {
    values: {
        // xs: 0,       
        // sm: 380,
        // md: 768,
        // lg: 992,
        // xl: 1200,
        xs: 380,       
        sm: 768,
        md: 992,
        lg: 1200,
        xl: 1920,
    },
  },
});

class AppWrapper extends React.Component {
  componentDidMount() {
    document.documentElement.style.setProperty("--primary-color", config.palette.primary.main);
    document.documentElement.style.setProperty("--secondary-color", config.palette.secondary.main);
    document.title = `${process.env.REACT_APP_NAME} v${process.env.REACT_APP_VERSION}`;
  }

  render() {
    return (
      <UserProvider>
        <StoreProvider>
          <DobyProvider>
            <LocalizationProvider
              locale={config.locale}
              en-US={en}
              sk-SK={sk}
            >
              <MuiThemeProvider theme={theme}>
                <MuiPickersUtilsProvider utils={MomentUtils} locale={locale}>
                  <App />
                </MuiPickersUtilsProvider>
              </MuiThemeProvider>
            </LocalizationProvider>
          </DobyProvider>
        </StoreProvider>
      </UserProvider>
    );
  }
}

// ReactDOM.render(<AppWrapper />, document.getElementById("root"));
const rootElement = document.getElementById("root");
if (rootElement.hasChildNodes()) {
  hydrate(<AppWrapper />, rootElement);
} else {
  render(<AppWrapper />, rootElement);
}

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
