import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import compose from '../../../compose';
import { withLocalization } from '../../shared/localization'
import withStore from "../../../consumers/withStore";
import withDoby from "../../../consumers/withDoby";
import withUser from "../../../consumers/withUser";
import config from '../../../config'

import classNames from 'classnames';
import getSharedStyles from '../../../style';

import TextField from '../../dobyComponents/form/TextField';
import Checkbox from '../../dobyComponents/form/CheckboxField';
import Button from '../../dobyComponents/form/Button';

import Grid from '@material-ui/core/Grid';
import CheckouCartItem from './CheckouCartItem';

import { loadStripe } from "@stripe/stripe-js";
import { formatPrice, getCurrencySymbol } from '../../../utils/helpers';

const styles = theme => ({
    ...getSharedStyles(theme),
    starterPackDiscount: {
        border: '1px solid #C8D2E1',
        background: '#ECECEC',
    },
    fixedBottomBar: {
        position: 'fixed',
        bottom: '0',
        left: '0',
        width: '100%',
        background: '#F8F8F8',
        zIndex: 1,
    },
    spacingBottom: {
        paddingBottom: '190px'
    }
});

class CheckoutSummary extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            starterPackDiscount: false,
            discountCode: "",
            standardPack: null,
            standardPackProduct: null,
        }
    }

    componentDidMount() {
        this.props.doby.cartGetProducts();
        this.fetchData();
        this.checkStarterpack();
    }

    fetchData = async () => {
        const { getEnums } = this.props.store;
        const filter = [
            { field: "enumTypeId", value: 5 },
            { field: "disabled", value: false },
        ];

        await getEnums(filter);
        await this.props.store.getTransports();
    }

    handleChange = async (field, value) => {
        this.setState({
            [field]: value
        });

        if (field === 'discountCode' && value.length === config.discountCodeLength) {
            await this.props.doby.redeeemDiscountCode(value);
        }
    }

    checkStarterpack = async () => {
        const { cart } = this.props.doby;
        
        for(let item of cart.data.products) {
            if(item.product.catalog_id === 'pack') {
                const filter = [
                    { field: "_id", value: item.product.productsRef.map(item=>item.id), op:'in' },
                ];
                let products = await this.props.doby.getProducts(filter);
                for(let packProduct of products.data) {
                    if(packProduct.catalog_id === 'coupon') {
                        this.setState({standardPack: packProduct, standardPackProduct: item.product});
                    }
                }
            }
        }
    }

    renderCartitems() {
        const { cart } = this.props.doby;
        return cart.data.products.map(item => {
            const { product } = item;
            return (
                <CheckouCartItem
                    key={product.id}
                    productId={product.id}
                    productName={product.name}
                    priceWithTax={product.price_with_tax}
                    thumbnailId={product.thumbnailId}
                    productCount={item.count}
                    countInStock={product.count}
                //techParams={product.techParams}
                />
            );
        });
    }

    getTotalPrice = () => {
        const { transports } = this.props.store;
        const { coupon } = this.props.doby.checkout.data;
        const { deliveryType } = this.props.doby.checkout.data.deliveryData;
        const { products } = this.props.doby.cart.data;

        let priceProducts = 0;
        let pricePack = 0;
        let price = 0;

        for (const item of products) {
            if(item.product.catalog_id === 'pack')
                pricePack += item.product.price_with_tax;
            else 
                priceProducts += (item.count * item.product.price_with_tax);
        }

        //NOTE: apply discounts, before adding price for delivery
        if (coupon._id) {
            //TODO: enums ?
            if (coupon.discountType === 1) {
                price = price * (100.0 - coupon.amount) / 100.0;
            } else {
                price -= coupon.amount;
            }
        }
        let couponBalance = this.state.standardPack ? this.state.standardPack.price_with_tax : 0;
        if(!this.state.starterPackDiscount) {
            price = priceProducts + pricePack;
        } else {
            if(priceProducts < couponBalance) {
                priceProducts = 0;
                couponBalance -= priceProducts;
            } else {
                priceProducts = priceProducts - couponBalance;
                couponBalance = 0;                
            }
            price = priceProducts + pricePack;

         
        }

        //NOTE: add delivery price
        const transport = transports.data.find(x => x._id === deliveryType);
        if (transport) {
            price += transport.price;
        }

        return formatPrice(price);
    }

    proceedCheckout = async () => {
        const { cart } = this.props.doby;
        const { deliveryData, coupon } = this.props.doby.checkout.data;

        let items = [];
        for (const product of cart.data.products) {
            product.product.count = product.count;
            if(this.state.standardPackProduct && product.product._id === this.state.standardPackProduct._id) {
                items.push({...product.product, id: product.product._id, useCoupon: this.state.starterPackDiscount});
                continue;
            }
            
            items.push({...product.product, id: product.product._id});
        }

        //TODO: validation ( function ? )
        const name = deliveryData.nameSurname.split(' ')[0];
        const surname = deliveryData.nameSurname.split(' ')[1];
        const country_obj = this.props.store.enums.data.find((x => x._id === deliveryData.country));
        const id_delivery_obj = this.props.store.transports.data.find((x => x._id === deliveryData.deliveryType));

        if (!country_obj || !id_delivery_obj)
            return;

        const destination_country_id = country_obj.key;
        const id_delivery = id_delivery_obj.key;

        const order = await this.props.doby.createOrder({
            //--- 
            name,
            surname,
            phone: deliveryData.tel,
            email: deliveryData.email,
            street: deliveryData.street,
            street_number: deliveryData.streetNumber,
            postal_code: deliveryData.psc,
            city: deliveryData.city,
            country: deliveryData.country,
            couponRef: { ...coupon, id: coupon._id},

            //--- FA
            fa_city: deliveryData.invoiceCity,
            fa_company: '',
            fa_country: deliveryData.invoiceCountry,
            fa_postal_code: deliveryData.invoicePsc,
            fa_street: deliveryData.invoiceStreet,
            fa_street_number: deliveryData.invoiceStreet,
            //---

            id_delivery,
            payment_method_id: deliveryData.paymentMethod,
            items,
            destination_country_id,
            ru: config.stripe.redirect_url
        });

        if (order) {
            const { payment_id } = order;
            const stripe = await loadStripe(config.stripe.id);

            const result = await stripe.redirectToCheckout({
                sessionId: payment_id,
            });

            if (result.error) {
                // If `redirectToCheckout` fails due to a browser or network
                // error, display the localized error message to your customer
                // using `result.error.message`.
                console.error(result.error);
            }
        }
        console.log(order);
    }

    renderDiscounts = () => {
        const { classes } = this.props;
        const { coupon } = this.props.doby.checkout.data;
        const getDiscountByType = (type, amount) => type === 1 ? `-${amount}%` : `-${amount}${getCurrencySymbol(config.currency)}`;
        return (
            <>
                { coupon._id &&
                    <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.justifyContentBetween, classes.w100)}>
                        <div className={classNames(classes.textXs)}>
                            {this.props.translate('coupon_discount')}
                        </div>
                        <div className={classNames(classes.textXs)}>
                            {getDiscountByType(coupon.discountType, coupon.amount)}
                        </div>
                    </div>
                }
            </>
        )
    }

    renderStarterpackDiscount = () => {
        const { classes } = this.props;
        
        return ( this.state.standardPack && 
            <Grid item xs={12} className={classes.mt3}>
                <div className={classNames(classes.starterPackDiscount, classes.px4, classes.py2, classes.roundedMd, classes.dFlex, classes.alignItemsCenter)}>
                    <div>
                        <Checkbox
                            onChange={this.handleChange}
                            fullWidth
                            required
                            value={this.state.starterPackDiscount}
                            field="starterPackDiscount"
                            noLabel
                        />
                    </div>
                    <div className={classNames(classes.flexCol)}>
                        <div className={classNames(classes.textXs)}>
                            {this.props.translate('starterpack_discount')}
                        </div>
                        <div className={classNames(classes.textXs2)}>
                            - {formatPrice(this.state.standardPack.price_with_tax)} na produkty
                        </div>
                    </div>
                </div>
            </Grid>
        )
    }

    render() {
        const { classes } = this.props;
        const { deliveryData } = this.props.doby.checkout.data;
        const country = this.props.store.enums.data.find((x => x._id === deliveryData.country));
        return (
            <>
                <div className={classNames(classes.container, classes.spacingBottom)}>
                    <Grid
                        container
                        direction="row"
                        justify="center"
                        alignItems="center"
                    >
                        <Grid item xs={12} className={classes.mt4}>
                            <div className={classNames(classes.flexCol, classes.w100)}>
                                <div className={classNames(classes.dFlex, classes.w100, classes.justifyContentBetween, classes.alignItemsCenter)}>
                                    <div className={classNames(classes.textXs, classes.fontMedium)}>
                                        {this.props.translate('field.name')}
                                    </div>
                                    <div className={classes.textXs}>
                                        {deliveryData.nameSurname}
                                    </div>
                                </div>
                                <div className={classNames(classes.dFlex, classes.w100, classes.justifyContentBetween, classes.alignItemsCenter, classes.mt3)}>
                                    <div className={classNames(classes.textXs, classes.fontMedium)}>
                                        {this.props.translate('field.email')}
                                    </div>
                                    <div className={classes.textXs}>
                                        {deliveryData.email}
                                    </div>
                                </div>
                                <div className={classNames(classes.dFlex, classes.w100, classes.justifyContentBetween, classes.alignItemsCenter, classes.mt3)}>
                                    <div className={classNames(classes.textXs, classes.fontMedium)}>
                                        {this.props.translate('field.tel')}
                                    </div>
                                    <div className={classes.textXs}>
                                        {`+${deliveryData.tel}`}
                                    </div>
                                </div>

                                <div className={classNames(classes.dFlex, classes.w100, classes.justifyContentBetween, classes.mt6)}>
                                    <div className={classNames(classes.textXs, classes.fontMedium)}>
                                        {this.props.translate('delivery_address')}
                                    </div>
                                    <div className={classNames(classes.flexCol, classes.textRight)}>
                                        <div className={classes.textXs}>
                                            {deliveryData.street}
                                        </div>
                                        <div className={classNames(classes.textXs, classes.mt1)}>
                                            {`${deliveryData.email}, ${deliveryData.psc}`}
                                        </div>
                                        <div className={classNames(classes.textXs, classes.mt1)}>
                                            {country ? country.name : ''}
                                        </div>
                                        <div className={classNames(classes.textXs, classes.mt2, classes.fontSemibold)} onClick={() => { this.props.changeStep(3) }}>
                                            {this.props.translate('edit')}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Grid>

                        <Grid item xs={12} className={classes.mt5}>
                            <hr className={classNames(classes.w100, classes.mb4, classes.hrLine)} />
                            {this.renderCartitems()}
                        </Grid>

                        {this.renderStarterpackDiscount()}

                        <Grid item xs={12} className={classes.mt6}>
                            <hr className={classNames(classes.w100, classes.mb4, classes.hrLine)} />
                            <TextField
                                onChange={this.handleChange}
                                fullWidth
                                required
                                value={this.state.discountCode}
                                field="discountCode"
                            />
                        </Grid>
                    </Grid>
                </div>

                <div className={classNames(classes.fixedBottomBar, classes.py4)}>
                    <div className={classNames(classes.container)}>
                        <div className={classNames(classes.flexCol, classes.w100)}>

                            {this.renderDiscounts()}

                            <div className={classNames(classes.dFlex, classes.mt2, classes.justifyContentBetween)}>
                                <div className={classNames(classes.flexCol)}>
                                    <div className={classNames(classes.fontMedium)}>
                                        {this.props.translate('totalPrice')}
                                    </div>
                                    <div className={classNames(classes.textXs2)}>
                                        {this.props.translate('price_with_tax')}
                                    </div>
                                </div>
                                <div className={classNames(classes.fontBold)}>
                                    {this.getTotalPrice()}
                                </div>
                            </div>
                            <Button
                                onClick={this.proceedCheckout}
                                fullwidth
                                className={classNames(classes.mt4)}>
                                {this.props.translate('continue')}
                            </Button>
                        </div>
                    </div>
                </div>
            </>
        );
    };
}

export default compose(
    withStyles(styles),
    withLocalization,
    withStore,
    withDoby,
    withUser,
)(CheckoutSummary);