import React from "react";
import PropTypes from "prop-types";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Tooltip from "@material-ui/core/Tooltip";
import { withLocalization } from "../localization";

class EnhancedTableHead extends React.Component {
  createSortHandler = property => event => {
    this.props.onRequestSort(event, property);
  };

  render() {
    const {
      onSelectAllClick,
      sort,
      numSelected,
      rowCount,
      rows,
      sortable,
      translate
    } = this.props;

    return (
      <TableHead>
        <TableRow>
          <TableCell padding="checkbox">
            <Checkbox
              indeterminate={numSelected > 0 && numSelected < rowCount}
              checked={numSelected === rowCount}
              onChange={onSelectAllClick}
            />
          </TableCell>
          {rows.map(row => {
            const label = row.label || translate(`field.${row.idField || row.field}`)

            return (
              <TableCell
                key={row.field}
                align={row.type === "num" ? "right" : "left"}
                padding={row.disablePadding ? "none" : "default"}
                sortDirection={sort.field === row.field ? sort.dir : false}
              >
                {sortable === false || row.sortable === false ? (
                  label
                ) : (
                  <Tooltip
                    title={translate("table.sort")}
                    placement={
                      row.type === "num" ? "bottom-end" : "bottom-start"
                    }
                    enterDelay={300}
                  >
                    <TableSortLabel
                      active={sort.field === row.field}
                      direction={sort.dir}
                      onClick={this.createSortHandler(row.field)}
                    >
                      {label}
                    </TableSortLabel>
                  </Tooltip>
                )}
              </TableCell>
            );
          }, this)}
          <TableCell padding="none"></TableCell>
        </TableRow>
      </TableHead>
    );
  }
}

EnhancedTableHead.propTypes = {
  numSelected: PropTypes.number.isRequired,
  onRequestSort: PropTypes.func.isRequired,
  onSelectAllClick: PropTypes.func.isRequired,
  sort: PropTypes.object.isRequired,
  rowCount: PropTypes.number.isRequired,
  rows: PropTypes.array.isRequired,
  sortable: PropTypes.bool
};

export default withLocalization(EnhancedTableHead);
