import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import compose from "../../compose";

import classNames from "classnames";
import getSharedStyles from "../../style";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  challengeCard: {
    background: "#333442",
    color: "white",
  },
  dateText: {
    color: "rgba(255,255,255,0.4)",
  },
  progressLine: {
    width: "100%",
    height: "5px",
    borderRadius: "5px",
    background: "#FF8C00",
  },
});

const convertDate = (date) => {
  return `${date.getDate()}.${date.getMonth() + 1}.${date.getFullYear()}`;
};

const handleDates = (sDateFrom, sDateTo) => {
  const dateFrom = new Date(sDateFrom);
  const dateTo = new Date(sDateTo);

  //const differenceInMiliseconds = dateTo.getTime() - dateFrom.getTime();
  const differenceInMiliseconds = new Date() - dateTo.getTime();  
  const differenceInDays = Math.round(
    Math.abs(differenceInMiliseconds) / (1000 * 3600 * 24)
  );
  const differenceInHours = Math.round(differenceInMiliseconds / (1000 * 3600));
  const differenceInMinutes = Math.round(differenceInMiliseconds / (1000 * 60));

  return {
    dateFrom: convertDate(dateFrom),
    dateTo: convertDate(dateTo),
    remaining:
      differenceInDays !== 0
        ? differenceInDays + "dn"
        : `${differenceInHours}h ${differenceInMinutes}min`,   
  };
};

function ActiveChallenge(props) {
  const { classes } = props;
  const { dateFrom, dateTo, remaining } = handleDates(
    props.dateFrom,
    props.dateTo
  );

  return (
    <>
      <div
        className={classNames(
          classes.challengeCard,
          classes.roundedMd,
          classes.p5,
          props.className
        )}
      >
        <div className={classNames(classes.flexCol, classes.w100)}>
          <div
            className={classNames(
              classes.dFlex,
              classes.justifyContentBetween,
              classes.w100
            )}
          >
            <div className={classNames(classes.flexCol)}>
              <div
                className={classNames(
                  classes.textWhite,
                  classes.textMonoH4,
                  classes.fontBold, 
                  classes.pr2
                )}
              >
                {props.name}
              </div>
              <div className={classNames(classes.dateText, classes.textXs2)}>
                {`${dateFrom} - ${dateTo}`}
              </div>
            </div>
            <div className={classNames(classes.flexCol)}>
              <div className={classNames(classes.textCenter)}>ikona</div>
              <div className={classNames(classes.textXs2)}>{remaining}</div>
            </div>
          </div>
          <div
            className={classNames(classes.flexCol, classes.w100, classes.mt4)}
          >
            <div
              className={classNames(
                classes.dFlex,
                classes.justifyContentBetween,
                classes.w100
              )}
            >
              <div className={classNames(classes.textXs2)}>tty</div>
              <div className={classNames(classes.textXs2)}>
                {`${props.summitsFinished}/${props.allSummits}`}
              </div>
            </div>
            <div className={classNames(classes.mt2)}>
              <div className={classNames(classes.progressLine)}></div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

ActiveChallenge.propTypes = {
  className: PropTypes.object.isRequired,
  name: PropTypes.string,
  allSummits: PropTypes.number,
  summitsFinished: PropTypes.number,
  dateFrom: PropTypes.string,
  dateTo: PropTypes.string,
};

export default compose(withStyles(styles))(ActiveChallenge);
