import React from "react";
import PropTypes from "prop-types";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import MoreIcon from "@material-ui/icons/MoreVert";
import IconButton from "@material-ui/core/IconButton";
import AlertDialog from "./AlertDialog";
import { withLocalization } from "./localization";

class ActionsMenu extends React.Component {
  state = {
    anchorEl: null,
    isOpenDelete: false,
  };

  handleOpenMenu = (event) => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleCloseMenu = () => {
    this.setState({ anchorEl: null });
  };

  handleCloseDelete = () => {
    this.setState({ isOpenDelete: false });
  };

  handleOpenClone = () => {
    this.setState({ isOpenDelete: true, anchorEl: null });
  };

  handleOpenDelete = () => {
    this.setState({ isOpenDelete: true, anchorEl: null });
  };

  onDelete = () => {
    this.props.onDelete();
    this.handleCloseDelete();
  };

  onEdit = () => {
    this.props.onEdit();
    this.handleCloseMenu();
  };

  onClone = () => {
    this.props.onClone();
    this.handleCloseMenu();
  };

  render() {
    const { anchorEl, isOpenDelete } = this.state;
    const { translate } = this.props;

    return (
      <div>
        <IconButton
          aria-owns={anchorEl ? "actions-menu" : undefined}
          aria-haspopup="true"
          edge="end"
          aria-label="actions"
          onClick={this.handleOpenMenu}
          disabled={this.props.disabled}
        >
          <MoreIcon />
        </IconButton>
        <Menu
          id="actions-menu"
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={this.handleCloseMenu}
        >
          <MenuItem onClick={this.onEdit}>{translate("table.edit")}</MenuItem>
          {this.props.onClone && (
            <MenuItem onClick={this.onClone}>
              {translate("table.clone")}
            </MenuItem>
          )}
          <MenuItem onClick={this.handleOpenDelete}>
            {translate("table.delete")}
          </MenuItem>
        </Menu>
        <AlertDialog
          isOpen={isOpenDelete}
          onClose={this.handleCloseDelete}
          onConfirm={this.onDelete}
          title={translate("table.delete")}
          description={translate("table.sure")}
        />
      </div>
    );
  }
}

ActionsMenu.propTypes = {
  onDelete: PropTypes.func.isRequired,
  onEdit: PropTypes.func.isRequired,
  disabled: PropTypes.bool.isRequired,
  onClone: PropTypes.func,
};

export default withLocalization(ActionsMenu);
