import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../style';
import classNames from 'classnames';
import { withLocalization } from '../shared/localization'
import compose from '../../compose';

import MuiExpansionPanel from '@material-ui/core/ExpansionPanel';
import MuiExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import MuiExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';

import DropdownFaqClosedIcon from '../dobyComponents/icons/DropdownFaqClosed';
import DropdownFaqOpenedIcon from '../dobyComponents/icons/DropdownFaqOpened';

const styles = theme => ({
    ...getSharedStyles(theme),
    panelRoot: {
        borderTop: '1px solid rgba(0,0,0,.125)',
        borderBottom: '1px solid rgba(0,0,0,.125)',
        boxShadow: 'none',
        '&:not(:last-child)': {
            borderBottom: 0,
        },
        '&:before': {
            display: 'none',
        },
    },
    panelExpanded: {
        margin: 'auto',
    },

    summaryRoot: {
        backgroundColor: 'transparent',
        border: '0',
        marginBottom: -1,
        minHeight: 56,
        padding: '0',
        '&$summaryExpanded': {
            minHeight: 56,
        },
    },
    summaryContent: {
        '&$summaryExpanded': {
            margin: '12px 0',           
        },
    },
    summaryExpanded: {},
    
    detailsRoot: {
        padding: '0rem 0rem 1.25rem 0rem'
    },
    iconSpacing: {
        width: '30px'
    },
    textIconSpacing: {
        paddingLeft: '30px',
    }
    
    
});

function Faq(props) {
    const { classes } = props;
    const [expanded, setExpanded] = useState(false);

    const handleChange = panel => (event, expanded) => {
        console.log(panel)
        console.log(expanded)

        setExpanded(expanded ? panel : false)
    };
    return (
        <>
            <div className={classNames(props.className)}>
               {
                   props.questions && props.questions.map((item, index)=>{
                       return (
                        <MuiExpansionPanel
                            square
                            expanded={expanded === index}
                            onChange={handleChange(index)}
                            classes={{root: classes.panelRoot, expanded: classes.panelExpanded}}
                        >
                            <MuiExpansionPanelSummary classes={{root: classes.summaryRoot, content: classes.summaryContent ,expanded: classes.summaryExpanded}}>
                                <div className={classNames(classes.dFlex, classes.alignItemsCenter)}>
                                    <div className={classNames(classes.iconSpacing, classes.dFlex, classes.alignItemsCenter)}>
                                        {expanded === index ? <DropdownFaqOpenedIcon/> : <DropdownFaqClosedIcon/>}
                                    </div>
                                    <div className={classNames(classes.fontBold,)}>
                                        {item.description}
                                    </div>
                                </div>
                            </MuiExpansionPanelSummary>
                            <MuiExpansionPanelDetails classes={{root: classes.detailsRoot}}>
                                <div className={classNames(classes.textM, classes.textIconSpacing)}>
                                    {item.content}
                                </div>
                            </MuiExpansionPanelDetails>
                        </MuiExpansionPanel>
                       )
                   })
               }               
            </div>
        </>
    )
}

Faq.propTypes = {
    className: PropTypes.string,
    questions: PropTypes.array.isRequired,
};

export default compose(
    withStyles(styles),
    withLocalization
)(Faq);