import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function MountainsIcon(props) {
    const { classes } = props;
    return (
        <>          
            <svg width="100%" height="100%" viewBox="0 0 27 27" fill="none" xmlns="http://www.w3.org/2000/svg" className={classNames(classes.icon)} style={{height: `${props.size}px`}}>
                <path d="M9.44747 12.0849L15.9289 22.0564H2.80969L9.44747 12.0849Z" stroke="white" stroke-width="1.5625"/>
                <path d="M10.9432 14.5778L15.9289 5.10489L24.9033 22.0565H15.4474" stroke="white" stroke-width="1.5625"/>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(MountainsIcon);