import React from 'react';
import PropTypes from 'prop-types';

import { withRouter } from 'react-router';
import { withStyles } from '@material-ui/core/styles';
import { withLocalization } from '../shared/localization'

import config from '../../config';
import getSharedStyles from '../../style';
import compose from '../../compose';
import withDoby from '../../consumers/withDoby';
import withStore from '../../consumers/withStore';
import withUser from '../../consumers/withUser';
import classNames from 'classnames';
import Select from '../shared/Select';
import Button from '../dobyComponents/form/Button';
import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Grid from '@material-ui/core/Grid';
import LinearProgress from '@material-ui/core/LinearProgress';
import ItemCard from './ItemCard';
import RollerModal from './RollerModal';

import { formatPrice, getCurrencySymbol } from '../../utils/helpers';

const styles = theme => ({
    ...getSharedStyles(theme),
    root: {
        backgroundColor: '#f7f7f8'
    },
    content: {
        padding: 0,
    },
    imageContainer: {
        width: '100%',
        height: '300px',
        overflow: 'hidden',
        position: 'relative',
        //background: 'red',
        // margin: -2 * theme.spacing.unit
    },
    productImage: {
        position: 'absolute',
        width: '100%',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%,-50%)'
    },
    productContent: {
        position: 'relative',
        padding: 10
    },
    productTitle: {
        margin: 0,
        padding: 0,
        paddingBottom: 5,
        fontSize: 19
    },
    productPrice: {
        margin: 0,
        padding: 0,
        fontSize: 15,
        display: 'inline-block'
    },
    productPriceDPH: {
        color: 'grey',
        fontSize: 9
    },
});

const _defaultState = {
    detailImage: null,
    selectedVariants: {},
    selectedProduct: null,
    detailsModalOpened: false,
    showSnackbar: false,
    lastLocation: window.location
};

class ProductDetail extends React.Component {
    constructor(props) {
        super(props);
        this.state = _defaultState;
    }

    componentDidMount() {
        this.fetchData();
    }

    componentDidUpdate() {
        if(this.state.lastLocation !== window.location.href) {
            this.setState({..._defaultState, lastLocation: window.location.href});
            this.fetchData();
        }
    }

    fetchDetailImage = async () => {
        const { imageId } = this.state.selectedProduct;
        if(imageId) {
            const data = await this.props.store.getFile(imageId);
            let reader = new FileReader();
            reader.onloadend = () => this.setState({detailImage: reader.result});
            reader.readAsDataURL(data);
        } 
    }

    fetchData = async () => {
        const { _id } = this.props.match.params;
        await this.props.doby.getProductsByCatalogId(_id);

        const filter = [{ field: "main", value: true }];
        await this.props.doby.getProducts(filter);

        const catalogProducts = this.props.doby.catalog.data;
        if(catalogProducts.length > 0) {
            this.setState({ selectedProduct: catalogProducts[0] }, () => {
                this.fetchDetailImage();
            });
        }
    }

    handleChange = (name, value) => this.setState({ [name]: value });

    handleChangeDropdown = (name, value) => {
        this.setState((state) => {
            return {
                ...state,
                selectedVariants: {
                    ...state.selectedVariants,
                    [name]: value
                }
            };
        }, () => {
            const product = this.getProductByDropdownSelection();
            this.setState({ selectedProduct: product }, ()=> this.fetchDetailImage());
        });
    }

    handleProductImageLoaded = (e) => {
        let width = e.target.naturalWidth;
        let height = e.target.naturalHeight;
        if (height < width) {
            e.target.style.height = '100%';
            e.target.style.width = 'auto';
        } else {
            e.target.style.width = '100%';
            e.target.style.height = 'auto';
        }
    }

    renderDropdowns = () => {
        let dropdowns = [];
        for (let variantKey in this.props.doby.productVariants.data) {
            dropdowns.push(
                <Select
                    key={variantKey}
                    value={this.state.selectedVariants[variantKey] ? this.state.selectedVariants[variantKey].toString() : ''}
                    field={variantKey}
                    onChange={this.handleChangeDropdown}
                    data={this.props.doby.productVariants.data[variantKey].data}
                    textField={"name"}
                    fullWidth
                    required
                />
            );
        }
        return (
            <>
                { dropdowns}
            </>
        );
    }

    getProductByDropdownSelection = () => {
        //NOTE: musme vybrat tolko dropdownov kolko mame unikatnych parametrov pre produkt variant
        if (Object.keys(this.state.selectedVariants).length !== Object.keys(this.props.doby.productVariants.data).length)
            return null;

        for (let variant of this.props.doby.catalog.data) {
            let hasAllRequirements = false;
            for (let variantKey in this.state.selectedVariants) {
                if (variant.tech_param && variant.tech_param[variantKey] === this.state.selectedVariants[variantKey]) {
                    hasAllRequirements = true;
                    break;
                }
            }

            if (hasAllRequirements)
                return variant
        }
        return null;
    }

    addToCart = () => {
        if (this.state.selectedProduct) {
            this.props.doby.cartAddProduct(this.state.selectedProduct);
            this.setState({showSnackbar: true});
        }
    }

    showProductDetail = (product) => {
        if (product.catalog_id && product.catalog_id.length > 0)
            this.props.history.push(`/eshop/product/${product.catalog_id}`);
    }

    renderSimillarProducts = () => {
        const { products } = this.props.doby;
        return products.isLoading ? (
            <div style={{ flexGrow: 1 }}>
                <LinearProgress color="secondary" />
            </div>) : products.data.map(product => {
                return (
                    <Grid item xs={6} sm={3} lg={2} key={product._id}>
                        <ItemCard
                            onClick={() => this.showProductDetail(product)}
                            name={product.name}
                            price={product.price_with_tax}
                            curr={config.currency}
                            Id={product.thumbnailId}
                            onViz={this.props.store.getFile}
                            source={product.count > 0 ? this.props.translate('in_stock') : this.props.translate('out_stock')}
                            //membersOnly
                        />
                    </Grid>
                );
            })
    }

    showAllProductDetails = () => this.setState({ detailsModalOpened: true });
    closeDetailsModal = () => this.setState({ detailsModalOpened: false });

    renderProductDetail = () => {
        const { classes } = this.props;
        return (
            <>
                <div className={classes.imageContainer}>
                    <img
                        className={classes.productImage}
                        src={this.state.detailImage ? this.state.detailImage : ''}
                        onLoad={(e) => { this.handleProductImageLoaded(e) }}
                        alt={''}
                    />
                </div>
                <div className={classNames(classes.container, classes.mt5)}>
                    <h5 className={classNames(classes.fontBold)}>{this.state.selectedProduct ? this.state.selectedProduct.name : ''}</h5>
                    <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.justifyContentBetween, classes.mb3)}>
                        <div className={classes.fontBold}>{this.state.selectedProduct ? formatPrice(this.state.selectedProduct.price_with_tax) : ''} <small className={classes.productPriceDPH}>s DPH</small> </div>
                        <div className={classes.productStock}>{this.state.selectedProduct ? this.state.selectedProduct.count > 0 ? this.props.translate('in_stock') : this.props.translate('out_stock') : ''}</div>
                    </div>

                    {this.renderDropdowns()}
                    { console.log(this.state.selectedProduct)} 

                    <Button
                        className={classNames(classes.mt4, classes.mb3)}
                        onClick={this.addToCart}
                        disabled={!this.state.selectedProduct || (this.state.selectedProduct && this.state.selectedProduct.disabled)}
                        fullwidth
                    >
                        {this.props.translate('add_to_cart')}
                    </Button>

                    <p>
                        {this.state.selectedProduct ? this.state.selectedProduct.short_description : ''}
                    </p>

                    <Button
                        className={classNames(classes.mt4, classes.mb3)}
                        onClick={this.showAllProductDetails}
                        fullwidth
                    >
                        {this.props.translate('all_product_details')}
                    </Button>

                    <h5 className={classNames( classes.fontBold, classes.mt7)}>{this.props.translate('find_similar_products')}</h5>

                    <Grid container spacing={24} className={classes.mt2}>
                        {this.renderSimillarProducts()}
                    </Grid>
                </div>

                { <RollerModal
                    onClose={this.closeDetailsModal}
                    opened={this.state.detailsModalOpened}
                />}
            </>
        );
    }

    handleClose = () => this.setState({showSnackbar: false});
    render() {
        return (
            <>
                { this.props.doby.catalog.isLoading ? (
                    <div style={{ flexGrow: 1 }}>
                        <LinearProgress color="secondary" />
                    </div>
                ) : this.renderProductDetail()}

                <Snackbar
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'left',
                    }}
                    open={this.state.showSnackbar}
                    autoHideDuration={6000}
                    onClose={this.handleClose}
                    message={this.props.translate('successfully_added_to_cart')}
                    action={
                        <React.Fragment>
                            <IconButton size="small" aria-label="close" color="inherit" onClick={this.handleClose}>
                                <CloseIcon fontSize="small" />
                            </IconButton>
                        </React.Fragment>
                    }
                />
            </>
        );
    }
}

ProductDetail.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default compose(
    withRouter,
    withStyles(styles),
    withDoby,
    withStore,
    withUser,
    withLocalization
)(ProductDetail);