import React from "react";
import DobyContext from "../contexts/DobyContext";

const withDoby = (WrappedComponent) => {
  return (props) => {
    return (
      <DobyContext.Consumer>
        {doby =>
          <WrappedComponent
            {...props}
            doby={doby}
          />
        }
      </DobyContext.Consumer>
    );
  }
};

export default withDoby;