import config from "../config";
import { auth as a } from "kd-web";

export const auth = async (username, password, token = "") => {
  try {
    return await a.auth(username, password, token);
  } catch (error) {
    throw error;
  }
};

const buildRedirectUri = (provider) => {
  let url = process.env.REACT_APP_HOMEPAGE;
  // fb
  if (provider === "5") {
    url += "/";
  }

  return `${url}?provider=${provider}`;
};

export const oAuth = async (provider, code) => {
  try {
    return await a.oAuth(
      provider,
      code,
      buildRedirectUri(provider),
      config.configId
    );
  } catch (error) {
    throw error;
  }
};

export const reset = async (username) => {
  try {
    return await a.reset(username, process.env.REACT_APP_HOMEPAGE);
  } catch (error) {
    throw error;
  }
};

export const updateOauth = async (provider, code) => {
  try {
    return await a.updateOauth(provider, code, buildRedirectUri(provider));
  } catch (error) {
    throw error;
  }
};

export const generateSecret = async () => {
  try {
    return await a.generateSecret();
  } catch (error) {
    throw error;
  }
};

export const getUsers = async (filters, sort, page) => {
  try {
    return await a.getUsers(filters, sort, page);
  } catch (error) {
    throw error;
  }
};

export const getUser = async (id) => {
  try {
    return await a.getUser(id);
  } catch (error) {
    throw error;
  }
};

export const updateUser = async (id, data) => {
  try {
    return await a.updateUser(id, data);
  } catch (error) {
    throw error;
  }
};

export const checkUser = async (username) => {
  try {
    return await a.checkUser(username);
  } catch (error) {
    throw error;
  }
};

export const registerUser = async (user) => {
  try {
    return await a.register(user);
  } catch (error) {
    throw error;
  }
};
