import React from "react";
import UserContext from "../contexts/UserContext";

const withUser = (WrappedComponent) => {
  return (props) => {
    return (
      <UserContext.Consumer>
        {user =>
          <WrappedComponent
            {...props}
            user={user}
          />
        }
      </UserContext.Consumer>
    );
  }
};

export default withUser;