import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import ListItemText from '@material-ui/core/ListItemText';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Checkbox from '@material-ui/core/Checkbox';
import { withLocalization } from './localization';
import classNames from 'classnames';

const styles = theme => ({
  formControl: {
    // marginLeft: theme.spacing.unit,
    // marginRight: theme.spacing.unit,
    minWidth: 120
  },
});

function TextInputComponent(props) {
  const { classes, data, textField, required, value, onChange, field, multiple } = props;
  if(required && (value === null || value === undefined || value === '') && data && data.length > 0) {
    onChange(field, data[0]._id);
  }

  const getText = (selected) => {
    const value = data.find(x => x._id === selected);
    return value ? value[textField] : null
  }

  const renderValue = selected => {
    if(!multiple) {
      return getText(selected);
    }
    
    return selected.map(s => getText(s)).join(' | ');
  }

  const label = props.label || props.translate('field.' +props.field);

  return (
    <FormControl
      className={classNames(classes.formControl, props.className)}
      required={required}
      fullWidth={props.fullWidth}
    >
      {label &&
        <InputLabel htmlFor={props.field}>{label}</InputLabel>
      }
      <Select
        value={props.value !== null ? props.value : ''}
        onChange={(e) => onChange(props.field, e.target.value)}
        id={props.field}
        multiple={props.multiple}
        renderValue={renderValue}
        disabled={props.disabled}
      >
        {(!props.required && !multiple) && 
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
        }
        {data.map(x => (
          <MenuItem key={x._id} value={x._id}>
            {multiple &&
              <Checkbox checked={value.find(d => d === x._id) !== undefined} />
            }
            <ListItemText primary={x[textField]} />
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
}

TextInputComponent.propTypes = {
  className: PropTypes.string,
  classes: PropTypes.object.isRequired,
  label: PropTypes.string,
  field: PropTypes.string.isRequired,
  value: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  required: PropTypes.bool,
  data: PropTypes.array.isRequired,
  textField: PropTypes.string.isRequired,
  fullWidth: PropTypes.bool,
  multiple: PropTypes.bool,
  disabled: PropTypes.bool
};

export default withLocalization(withStyles(styles)(TextInputComponent));