import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function Icon(props) {
    const { classes } = props;
    return (
        <>
            <svg width="100%" height="100%" viewBox="0 0 32 32" fill="none" xmlns="http://www.w3.org/2000/svg" className={classNames(classes.icon)} style={{height: `${props.size}px`}}>
                <path d="M30 16.5L19.2692 7V13.3463H1V19.6537H19.2692V26L30 16.5Z" fill="white"/>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(Icon);