import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function StartIcon(props) {
    const { classes } = props;
    return (
        <>   
            <svg width="100%" height="100%" viewBox="0 0 27 27" fill="none" xmlns="http://www.w3.org/2000/svg" className={classNames(classes.icon)} style={{height: `${props.size}px`}}>
                <circle cx="13.5" cy="13.5" r="9.4375" stroke="white" stroke-width="2.125"/>
                <circle cx="13.4996" cy="13.4999" r="4.375" fill="white"/>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(StartIcon);