import React from 'react';
import { withRouter } from 'react-router';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../style';
import classNames from 'classnames';
import withDoby from '../../consumers/withDoby';
import withUser from '../../consumers/withUser';
import { withLocalization } from '../shared/localization'
import compose from '../../compose';
import Grid from '@material-ui/core/Grid';
import Flickity from "react-flickity-component";
import ChallengeCard from './ChallengeCard';

import ItemCard from '../eshop/ItemCard';
import config from '../../config';
import withStore from '../../consumers/withStore';
import PartnerCard from './PartnerCard';
import BlogPost from './BlogPost'

import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';

import Button from '../dobyComponents/form/Button';

const styles = theme => ({
    ...getSharedStyles(theme),
    introImage: {
        width: '100%',
        // height: 'calc(100vh - 60px - 120px)',
        height: 'calc(100vh + 60px)',
        background: 'url("/img/intro-hill.png")',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        position: 'relative',
        '&:after': {
            content: "''",
            display: 'block',
            position: 'absolute',
            bottom: '-1px',
            left: 0,
            width: '100%',
            height: '100%',
            background: 'linear-gradient(180deg, rgba(7, 10, 26, 0) 36.46%, rgba(7, 10, 26, 0.6) 64.06%, #070A1A 100%)'
        }
    },
    introContent: {
        zIndex: 10,
        height: 'calc(100% - 150px)',
        width: '100%', 
    },
    introTitle: {
        fontSize: '34px',
    },    
    introBtn: {
        background: 'rgba(27, 32, 62, 0.5)',
        backdropFilter: 'blur(5px)',
        padding: '20px',        
        borderRadius: '0px',        
    },
    dashedBtn: {
        position: 'relative',
        '&:after': {
            content: "''",
            display: 'block',
            position: 'absolute',
            bottom: 0,
            left: 0,
            width: '100%',
            height: '100%',
            background: 'linear-gradient(to right, white 50%, rgba(255, 255, 255, 0) 0%), linear-gradient(white 50%, rgba(255, 255, 255, 0) 0%), linear-gradient(to right, white 50%, rgba(255, 255, 255, 0) 0%), linear-gradient(white 50%, rgba(255, 255, 255, 0) 0%)',
            backgroundPosition: 'top, right, bottom, left',
            backgroundRepeat: 'repeat-x, repeat-y',
            backgroundSize: '10px 1px, 1px 10px',
            zIndex: 15,
        }
    },
    howItWorks: {      
        background: 'linear-gradient(180deg, #080A1A 0%, #1A1D33 100%), #080A1A'
    },   
    howItWorksRow: {
        marginBottom: '130px',
    },   
    textCol: {
        width: 'calc(100% - 100px)',
        color: 'white'
    },
    howItWorksItemNumber: {
        height: '70px',
        width: '70px',
        borderRadius: 50,
        textAlign: 'center',
        lineHeight: '70px',
        background: 'rgba(10, 18, 49, 0.7)',
        border: '1px solid #6D6E79',
        color: 'white',     
    },
    challenges: {
        background: 'url("/img/trees.jpg")',
        backgroundSize: 'cover',
        backgroundPositionY: 'top',
        position: 'relative',
        '&:before': {
            content: "''",
            display: 'block',
            width: '100%',
            height: '100%',
            position: 'absolute',
            top: '0',
            left: '0',
            background: 'linear-gradient(180deg, #1A1D33 0%, rgba(26, 29, 51, 0.8) 56.31%, rgba(26, 29, 51, 0.8) 110.75%), url(.jpg)',
            zIndex: '1',
        },
        '&:after': {
            content: "''",
            display: 'block',
            width: '103%',
            height: '100px',
            background: 'url("img/triangle.svg")',
            backgroundSize: 'contain',
            backgroundPositionY: 'bottom',
            backgroundRepeat: 'no-repeat',
            bottom: '-2px',
            position: 'relative',
            zIndex: '20',
        }
    },
    challengesContent: {
        position: 'relative',
        zIndex: 3,
    },
    challengeCarousel: {
        '& .flickity-viewport': {
            //padding: '7px'
        },
        '& .carousel-cell:first-child': {
            paddingLeft: '1.25rem'
        },
        '& .carousel-cell:last-child': {
            paddingRight: '0.5rem',
        }
    },
    whyGetInvolvedImg: {
        maxWidth: '100%'
    },


    blog: {
        background: '#ffffff'
    },
    smallImg: {
        width: '47%',
    }
    // triangleSeparator: {
    //     width: '100%',        
    // }
});

const flickityOptions = {
    cellAlign: 'left',
    contain: true,
    prevNextButtons: false,
    pageDots: false
}

class Landing extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            partners: [],
            generalPartners: [],
            mainPartners: [],
            expandedFaq: 'panel1'
        }
    }

    componentDidMount() {
        this.fetchData();
    }

    fetchData = async () => {
        await this.props.doby.getChallenges();
        await this.props.doby.getProducts();
        await this.props.doby.getPartners();
        await this.props.doby.getPosts();

        const { partners } = this.props.doby;

        this.setState({
            partners: partners.data.filter(item => item.type.key === 30),
            generalPartners: partners.data.filter(item => item.type.key === 10),
            mainPartners: partners.data.filter(item => item.type.key === 20),
        })

    }

    renderChallenges = () => {
        const { challenges } = this.props.doby;
        const { classes } = this.props;        
        return challenges.data.map(challenge => {
            return (
                <div className={"carousel-cell"}>
                    <ChallengeCard
                        key={challenge._id}
                        title={challenge.name}
                        start={challenge.start}
                        end={challenge.end}
                        sumitsCount={challenge.summitsRef.length}                        
                        onClick={() => this.joinChallenge(challenge)}
                        customClasses={classNames(classes.mt2, classes.mb2, classes.mr3)}
                    />
                </div>
            );
        })
    }

    renderProducts = () => {
        const { products } = this.props.doby;
        let retVal = [];
        for (let i = 0; i < 2; i++) {
            let product = products.data[i];
            product && retVal.push(
                <Grid item xs={6} sm={3} lg={2}>
                    <ItemCard
                        key={product._id}
                        onClick={() => this.showProductDetail(product)}
                        name={product.name}
                        price={product.price_with_tax}
                        curr={config.currency}
                        Id={product.thumbnailId}
                        onViz={this.props.store.getFile}
                        source={product.count > 0 ? this.props.translate('in_stock') : this.props.translate('out_stock')}
                    //membersOnly
                    />
                </Grid>
            )
        }
        return retVal;
    }

    renderPartners = () => {
        console.log(this.state.partners)
        return this.state.partners.map(item => {
            return (
                <Grid item xs={2} sm={3} lg={2}>
                    <PartnerCard
                        key={item._id}
                        name={item.name}
                        desc={item.description}
                        logoId={item.logoId}
                        getFile={this.props.store.getFile}
                    >
                    </PartnerCard>
                </Grid>
            )
        })
    }
    renderGeneralPartners = () => {
        return this.state.generalPartners.map(item => {
            return (
                <Grid item xs={2} sm={3} lg={2}>
                    <PartnerCard
                        key={item._id}
                        name={item.name}
                        desc={item.description}
                        logoId={item.logoId}
                        getFile={this.props.store.getFile}
                    >
                    </PartnerCard>
                </Grid>
            )
        })
    }
    renderMainPartners = () => {
        return this.state.mainPartners.map(item => {
            return (
                <Grid item xs={2} sm={3} lg={2}>
                    <PartnerCard
                        key={item._id}
                        name={item.name}
                        desc={item.description}
                        logoId={item.logoId}
                        getFile={this.props.store.getFile}
                    >
                    </PartnerCard>
                </Grid>
            )
        })
    }
    renderBlogPosts = () => {
        return this.props.doby.posts.data.map(post => {
            return (
                <div className={"carousel-cell"}>
                    <BlogPost
                        key={post._id}
                        title={post.title}
                        content={post.content}
                        created={post.created}
                        thumbnailId={post.thumbnailId}
                        getFile={this.props.store.getFile}
                    />
                </div>
            );
        })
    }

    handleExpandFaq = panel => (event, expanded) => {
        this.setState({
          expandedFaq: expanded ? panel : false,
        });
      };

    render() {
        const { classes } = this.props;
        const { expandedFaq } = this.state;
        return (
            <>
                <div className={classNames(classes.introImage,classes.flexCol)}>
                    <div className={classNames(classes.flex, classes.flexCol, classes.textCenter, classes.pl7, classes.pr7, classes.pb7, classes.introContent, classes.justifyContentEnd)}>
                        <div className={classNames(classes.textWhite, classes.fontBold, classes.introTitle)}>
                            {this.props.translate('intro_title')}
                        </div>
                        <div className={classNames(classes.textWhite, classes.mt3)}>
                            {this.props.translate('intro_subtitle')}
                        </div>
                        <Button fullwidth className={classNames(classes.introBtn, classes.mt4, classes.dashedBtn, classes.mb7)}>Ako to funguje?</Button>
                    </div>
                </div>
                <div className={classNames(classes.howItWorks, classes.flexCol)}>
                    
                    <div className={classNames(classes.container)}>
                        <div className={classNames(classes.howItWorksRow, classes.dFlex, classes.w100, classes.justifyContentBetween)}>
                            <div className={classNames(classes.howItWorksItemNumber, classes.textMonoH3)}>
                                1
                            </div>
                            <div className={classNames(classes.textCol)}>
                                <div className={classNames(classes.flexCol)}>
                                    <h3 className={classNames(classes.textMonoH4, classes.fontBold)}>{this.props.translate('register')}</h3>
                                    <div className={classNames(classes.mt3, classes.textXs)}>
                                        Prejdi 4 jednoduch kroky. Trv to v priemere len 1:20 min.
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className={classNames(classes.howItWorksRow, classes.dFlex, classes.w100, classes.justifyContentBetween)}>
                            <div className={classNames(classes.textCol)}>
                                <div className={classNames(classes.flexCol)}>
                                    <h3 className={classNames(classes.textMonoH4, classes.fontBold)}>{this.props.translate('we_plant_tree')}</h3>
                                    <div className={classNames(classes.mt3, classes.textXs)}>
                                        Vysadili sme spolu u 14 500 stromov. Naviac z kadho nkupu prispievame 5% zo sumy na ochranu kamzkov.
                                    </div>
                                </div>
                            </div>
                            <div className={classNames(classes.howItWorksItemNumber, classes.textMonoH3)}>
                                2
                            </div>
                        </div>

                        <div className={classNames(classes.howItWorksRow, classes.dFlex, classes.w100, classes.justifyContentBetween)}>
                            <div className={classNames(classes.howItWorksItemNumber, classes.textMonoH3)}>
                                3
                            </div>
                            <div className={classNames(classes.textCol)}>
                                <div className={classNames(classes.flexCol)}>
                                    <h3 className={classNames(classes.textMonoH4, classes.fontBold)}>{this.props.translate('conquer_summits')}</h3>
                                    <div className={classNames(classes.mt3, classes.textXs)}>
                                        Na kadom vrchole idelne s merchom Doby Hory, nie je to ale podmienka.
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className={classNames(classes.howItWorksRow, classes.dFlex, classes.w100, classes.justifyContentBetween)}>
                            <div className={classNames(classes.textCol)}>
                                <div className={classNames(classes.flexCol)}>
                                    <h3 className={classNames(classes.textMonoH4, classes.fontBold)}>{this.props.translate('take_picture')}</h3>
                                    <div className={classNames(classes.mt3, classes.textXs)}>
                                        Na kadom vrchole idelne s merchom Doby Hory, nie je to vak podmienka.
                                    </div>
                                </div>
                            </div>
                            <div className={classNames(classes.howItWorksItemNumber, classes.textMonoH3)}>
                                4
                            </div>
                        </div>

                        <div className={classNames(classes.howItWorksRow, classes.dFlex, classes.w100, classes.justifyContentBetween)}>
                            <div className={classNames(classes.howItWorksItemNumber, classes.textMonoH3)}>
                                5
                            </div>
                            <div className={classNames(classes.textCol)}>
                                <div className={classNames(classes.flexCol)}>
                                    <h3 className={classNames(classes.textMonoH4, classes.fontBold)}>{this.props.translate('get_medal')}</h3>
                                    <div className={classNames(classes.mt3, classes.textXs)}>
                                        Dorume ti ju po schvlen vetkch fotiek, aby si mohol na toto navdy spomna.
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
                <div className={classNames(classes.challenges, classes.pt6)}>
                    <Grid
                        container
                        direction="row"
                        justify="center"
                        alignItems="center"
                    >

                        <Grid item xs={12} className={classNames(classes.challengesContent)}>
                            <div className={classNames(classes.textWhite, classes.textCenter, classes.fontBold)}>
                                <div>{this.props.translate('intro_challenges_title_1')}</div>
                                <div>{this.props.translate('intro_challenges_title_2')}</div>
                            </div>

                            <div className={classNames(classes.mt7)}>
                                <Flickity
                                    className={classNames(classes.mt4, classes.challengeCarousel)}
                                    options={flickityOptions}
                                    reloadOnUpdate={true}
                                >
                                    {this.renderChallenges()}
                                </Flickity>
                            </div>
                        </Grid>
                    </Grid>
                </div>

                <div className={classNames(classes.container, classes.pb8, classes.bgWhite)}>
                    <Grid
                        container
                        direction="row"
                        justify="center"
                        alignItems="center"
                    >                        

                        <Grid item xs={12} className={classNames(classes.mt9)}>
                            <div className={classNames(classes.fontBold, classes.textCenter)}>
                                {this.props.translate('why_get_involved')}
                            </div>
                        </Grid>

                        <Grid item xs={12} className={classNames()}>
                            <img src={'./img/winOverYourself.png'} className={classNames(classes.whyGetInvolvedImg)}/>
                        </Grid>

                        <Grid item xs={12} className={classNames(classes.mt2)}>
                            <div className={classNames(classes.w100, classes.flexCol)}>
                                <div className={classNames(classes.textMonoH4, classes.fontBold)}>
                                    {this.props.translate('win_over_yourself')}
                                </div>
                                <div className={classNames(classes.mt3, classes.textXs)}>
                                    Nepredbiehame sa a nevyhrvame jeden nad druhm. Cieom je zai tie najkrajie zitky a spomna na ne do konca ivota. Vystp z komfortu a zai to, o om si doteraz len snval.
                                </div>
                            </div>
                        </Grid>


                        <Grid item xs={12} className={classNames(classes.mt9)}>
                            <img src={'./img/helpNature.png'} className={classNames(classes.whyGetInvolvedImg)}/>
                        </Grid>

                        <Grid item xs={12} className={classNames(classes.mt2)}>
                            <div className={classNames(classes.w100, classes.flexCol)}>
                                <div className={classNames(classes.textMonoH4, classes.fontBold)}>
                                    {this.props.translate('help_nature')}
                                </div>
                                <div className={classNames(classes.mt3, classes.textXs)}>
                                    Prroda ti d vea, vr jej to. Pom nm ju v rmci vzvy oisti a udra jej istotou za o dostane pecilny odznak na tvoj profil.
                                </div>
                            </div>
                        </Grid>

                        <Grid item xs={12} className={classNames(classes.mt9)}>
                            <img src={'./img/localShops.png'} className={classNames(classes.whyGetInvolvedImg)}/>
                        </Grid>

                        <Grid item xs={12} className={classNames(classes.mt2)}>
                            <div className={classNames(classes.w100, classes.flexCol)}>
                                <div className={classNames(classes.textMonoH4, classes.fontBold)}>
                                    {this.props.translate('support_local_shops')}
                                </div>
                                <div className={classNames(classes.mt3, classes.textXs)}>
                                    Udratenos a kvalita je pre ns na prvom mieste, preto kad produkt v naom eshope pochdza od loklnych vrobcov. Pri produkcii a vbere materilov kladieme maximlny draz na udratenos a ekolgiu.
                                </div>
                            </div>
                        </Grid>






                        <Grid item xs={12} className={classNames(classes.mt9)}>
                            <div className={classNames(classes.w100, classes.flexCol)}>
                                <div className={classNames(classes.fontBold, classes.textCenter)}>
                                    {this.props.translate('help_nature')}
                                </div>
                                <div className={classNames(classes.mt3, classes.textBase, classes.textCenter)}>
                                    Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco
                                </div>
                            </div>
                        </Grid>

                        <Grid item xs={12} className={classNames(classes.mt9)}>
                            <div className={classNames(classes.w100, classes.flexCol)}>
                                <div className={classNames(classes.fontBold, classes.textCenter)}>
                                    {this.props.translate('be_part_of_community')}
                                </div>
                                <div className={classNames(classes.mt3, classes.textBase, classes.textCenter)}>
                                    Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco
                                </div>
                                <div className={classNames(classes.dFlex, classes.justifyContentBetween, classes.w100, classes.mt5)}>
                                    <img src="/img/community1.jpg" className={classNames(classes.smallImg)} />
                                    <img src="/img/community2.jpg" className={classNames(classes.smallImg)} />
                                </div>
                            </div>
                        </Grid>

                        <Grid item xs={12} className={classNames(classes.mt9)}>
                            <div className={classNames(classes.w100, classes.flexCol)}>
                                <div className={classNames(classes.fontBold, classes.textCenter)}>
                                    {this.props.translate('share_experiences')}
                                </div>
                                <div className={classNames(classes.mt3, classes.textBase, classes.textCenter)}>
                                    Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco
                                </div>
                                <div className={classNames(classes.dFlex, classes.justifyContentBetween, classes.w100, classes.mt5)}>
                                    <img src="/img/experience1.jpg" className={classNames(classes.smallImg)} />
                                    <img src="/img/experience2.jpg" className={classNames(classes.smallImg)} />
                                </div>
                            </div>
                        </Grid>

                        <Grid item xs={12} className={classNames(classes.mt9)}>
                            <div className={classNames(classes.w100, classes.flexCol)}>
                                <div className={classNames(classes.fontBold, classes.textCenter)}>
                                    {this.props.translate('eshop')}
                                </div>
                                <Grid container spacing={24} className={classes.mt3}>
                                    {this.renderProducts()}
                                </Grid>
                            </div>
                        </Grid>
                        

                        <Grid item xs={12} className={classNames(classes.mt9)}>
                            <div className={classNames(classes.w100, classes.flexCol)}>
                                <div className={classNames(classes.fontBold, classes.textCenter)}>
                                    FAQ
                                </div>
                                <div className={classes.mt5}>
                                <ExpansionPanel
                                        square
                                        expanded={expandedFaq === 'panel1'}
                                        onChange={this.handleExpandFaq('panel1')}
                                    >
                                        <ExpansionPanelSummary>
                                            Otazka 1
                                        </ExpansionPanelSummary>
                                        <ExpansionPanelDetails>
                                           Odpoved 1
                                        </ExpansionPanelDetails>
                                    </ExpansionPanel>
                                    <ExpansionPanel
                                        square
                                        expanded={expandedFaq === 'panel2'}
                                        onChange={this.handleExpandFaq('panel2')}
                                    >
                                        <ExpansionPanelSummary>
                                            Otazka 2
                                        </ExpansionPanelSummary>
                                        <ExpansionPanelDetails>
                                            Odpoved 2
                                        </ExpansionPanelDetails>
                                    </ExpansionPanel>
                                    <ExpansionPanel
                                        square
                                        expanded={expandedFaq === 'panel3'}
                                        onChange={this.handleExpandFaq('panel3')}
                                    >
                                        <ExpansionPanelSummary>
                                            Otazka 3
                                        </ExpansionPanelSummary>
                                        <ExpansionPanelDetails>
                                            Odpoved 3
                                        </ExpansionPanelDetails>
                                    </ExpansionPanel>
                                </div>
                            </div>
                        </Grid>
                    </Grid>
                </div>


                <div className={classNames(classes.blog, classes.pt6)}>
                    <Grid
                        container
                        direction="row"
                        justify="center"
                        alignItems="center"
                    >
                        <Grid item xs={12}>
                            <div className={classNames(classes.fontBold, classes.textCenter)}>
                                {this.props.translate('blog')}
                            </div>

                            <div className={classNames(classes.mt2)}>
                                <Flickity
                                    className={classNames(classes.mt4, classes.challengeCarousel)}
                                    options={flickityOptions}
                                    reloadOnUpdate={true}
                                >
                                    {this.renderBlogPosts()}
                                </Flickity>
                            </div>
                        </Grid>
                    </Grid>
                </div>           

                <div className={classNames(classes.container, classes.howItWorks, classes.pb8)}>
                    <Grid
                        container
                        direction="row"
                        justify="center"
                        alignItems="center"
                    >
                        <Grid item xs={12} className={classNames(classes.mt9)}>
                            <div className={classNames(classes.w100, classes.flexCol)}>
                                <div className={classNames(classes.fontBold, classes.textCenter)}>
                                    {this.props.translate('general_partners')}
                                </div>
                                <Grid container spacing={24} className={classes.mt3}>
                                    {this.renderGeneralPartners()}
                                </Grid>
                            </div>
                        </Grid>

                        <Grid item xs={12} className={classNames(classes.mt9)}>
                            <div className={classNames(classes.w100, classes.flexCol)}>
                                <div className={classNames(classes.fontBold, classes.textCenter)}>
                                    {this.props.translate('main_partners')}
                                </div>
                                <Grid container spacing={24} className={classes.mt3}>
                                    {this.renderMainPartners()}
                                </Grid>
                            </div>
                        </Grid>

                        <Grid item xs={12} className={classNames(classes.mt9)}>
                            <div className={classNames(classes.w100, classes.flexCol)}>
                                <div className={classNames(classes.fontBold, classes.textCenter)}>
                                    {this.props.translate('partners')}
                                </div>
                                <Grid container spacing={24} className={classes.mt3}>
                                    {this.renderPartners()}
                                </Grid>
                            </div>
                        </Grid>

                    </Grid>
                </div>

            </>
        )
    }
}

export default compose(
    withRouter,
    withStore,
    withStyles(styles),
    withDoby,
    withUser,
    withLocalization
)(Landing);