import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Avatar from "@material-ui/core/Avatar";
import Tooltip from "@material-ui/core/Tooltip";

const styles = theme => ({

});

class ImageAvatars extends React.Component {
  render() {
    const { value, defaultValue, tooltip, onClick, className, placement } = this.props;

    return (
      <Tooltip title={tooltip} aria-label={tooltip} placement={placement}>
        <Avatar
          style={{ textTransform: 'uppercase' }}
          alt={defaultValue}
          src={value ? "data:image/jpeg;base64," + value : null}
          onClick={onClick}
          className={className}
        >
          {tooltip[0]}
        </Avatar>
      </Tooltip>
    );
  }
}

ImageAvatars.propTypes = {
  classes: PropTypes.object.isRequired,
  value: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
  className: PropTypes.string.isRequired,
  placement: PropTypes.string,
  tooltip: PropTypes.string.isRequired
};

export default withStyles(styles)(ImageAvatars);
