import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import compose from '../../../compose';

import classNames from 'classnames';
import getSharedStyles from '../../../style';

import ArrowRight from '../icons/ArrowRight'


const styles = theme => ({
    ...getSharedStyles(theme),
    button: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',        
        whiteSpace: 'nowrap',        
        userSelect: 'none',
        fontSize: '14px',
        lineHeight: '130%',
        fontFamily: "'IBM Plex Mono', monospace",
        letterSpacing: '0.5px',
        padding: '0 20px',
        height: '52px',       
        background: theme.palette.primary.main,
        color: '#ffffff',
        fontWeight: '700',       
        //borderRadius: '50px',
        border: `2px solid ${theme.palette.primary.main}`,
        textTransform: 'uppercase',
        outline: 'none',
        cursor: 'pointer',
        transition: 'all 0.25s ease',
        '&:hover': {
            background: theme.palette.primary.dark,
            borderColor: theme.palette.primary.dark,
        }
    },
    btnFullwidth: {        
        width: '100%',
        maxWidth: '400px',
    },
    btnOutline: {
         border: '1px solid #333442!important',
         background: 'transparent!important',
         color: '#333442!important',
    },
    //color variants
    btnSecondary: {
        background: theme.palette.primary.main,
    },
    //size
    btnLarge: {
        padding: '0 25px',
        height: '52px',  
        fontSize: '14px',     
    },
    btnSmall: {
        padding: '0 15px',
        height: '30px',  
        fontSize: '11px',
    },
    btnDisabled: {
        background: 'black'
    },
    btnArrow: {
        justifyContent: 'space-between'
    },
});

function Button(props) {   
    const { classes, fullwidth, disabled, outline, size, variant, minWidth, arrow} = props;
   
    return (
        <>
            <button 
                className={classNames(props.className, classes.button, disabled && classes.btnDisabled, fullwidth && classes.btnFullwidth, outline && classes.btnOutline, size === 'lg' && classes.btnLarge, size === 'sm' && classes.btnSmall, variant === 'secondary' && classes.btnSecondary, arrow && classes.btnArrow)}
                onClick={() => !disabled && props.onClick()}
                style={{minWidth: minWidth ? minWidth+'px' : '0px'}}
            >
                {props.children}
                {props.arrow && <ArrowRight size={28}/>}
            </button>
        </>
    );
}

export default compose(
    withStyles(styles),
)(Button);