import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import TextInput from "../shared/TextInput";
import Form from "../shared/form/Form";

import withDoby from "../../consumers/withDoby";
import withUser from "../../consumers/withUser";
import compose from "../../compose";

const styles = (theme) => ({
  nameTextInput: {
    display: "flex",
    flex: 1,
  },
});

const INIT_STATE = {
  name: "",
};

class FormWrapper extends React.Component {
  constructor(props) {
    super(props);
    this.state = props.data || INIT_STATE;
  }

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleSubmit = async () => {
    const { addSerie, refreshSeries, editSerie } = this.props.doby;

    try {
      if (this.state._id) {
        await editSerie(this.state);
      } else {
        await addSerie(this.state);
      }

      refreshSeries();
      this.setState(INIT_STATE);

      if (this.props.onSubmit) this.props.onSubmit();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { classes, disabled } = this.props;
    const { name } = this.state;
    return (
      <Form onSubmit={this.handleSubmit} inline disabled={disabled}>
        <div className={classes.nameTextInput}>
          <TextInput
            value={name}
            field="name"
            onChange={this.handleChange}
            required
            fullWidth
            disabled={disabled}
          />
        </div>
      </Form>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object,
  onSubmit: PropTypes.func,
  date: PropTypes.instanceOf(Date),
  disabled: PropTypes.bool,
};

export default compose(withStyles(styles), withDoby, withUser)(FormWrapper);
