import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';

import compose from '../../../compose';
import { withLocalization } from '../../shared/localization'
import { withRouter } from 'react-router';
import classNames from 'classnames';
import withStore from "../../../consumers/withStore";
import withDoby from "../../../consumers/withDoby";
import withUser from "../../../consumers/withUser";
import getSharedStyles from '../../../style';
import Button from '../../dobyComponents/form/Button';

import config from '../../../config';
import * as mailUtils from "../../../utils/mailUtils";

const styles = theme => ({
    ...getSharedStyles(theme),
    emptyCartImg: {
        width: '60%'
    },
});

class VerificationPayment extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            succeeded: false
        };
    }

    componentDidMount() {
        this.fetchData();
    }

    sendCouponMail = async(coupon) =>{
        const { user } = this.props.user;
        const data = {
            subject: this.props.translate('email_coupon_subject'),
            from: config.mail.address,
            fromName: config.mail.name,
            recipientRef: {
                id: user._id
            },
            templateId: config.mail.sendCouponTemplate,
            params: [user.name, coupon]
        };
        await mailUtils.send(data);
    }

    fetchData = async () => {
        const query = new URLSearchParams(this.props.location.search);
        if (query) {
            const id = query.get('id');
            if (id) {
                const result = await this.props.doby.verifyOrder(id);
                if(result) {
                    this.setState({succeeded: true});
                    this.props.doby.cartRemoveProducts();

                    if(result.coupon) {
                        await this.sendCouponMail(result.coupon.code);
                    }
                }
            }
        }
    }

    goToEshop = () => this.props.history.push(`/eshop`);

    render = () => {   
        const { classes } = this.props;
        return (
            <>
                <div className={classNames(classes.flexCol, classes.container)}>
                    { 
                        this.props.doby.checkout.isLoading ? (<CircularProgress/>) : (
                            <>
                                <div className={classNames(classes.mt8, classes.textCenter)}>
                                    <img className={classes.emptyCartImg} alt="" src={this.state.succeeded ? "https://cdn3.iconfinder.com/data/icons/flat-actions-icons-9/792/Tick_Mark_Dark-512.png" : "https://nika.shop/wp-content/uploads/2020/01/fail-png-7.png"} />
                                </div>
                                <div className={classNames(classes.textCenter, classes.mt4)}>
                                    {this.props.translate(this.state.succeeded ? 'payment_successfull' : 'payment_failed')}
                                </div>
                            </>
                        ) 
                    }

                    <Button 
                        className={classNames(classes.mt4)}
                        onClick={this.goToEshop}
                        fullwidth
                    >
                        {this.props.translate('go_to_eshop')}
                    </Button>
    
                </div>
            </>
        );
    }
}

export default compose(
    withStyles(styles),
    withLocalization,
    withStore,
    withDoby,
    withUser,
    withRouter,
)(VerificationPayment);
