import React from "react";
import Table from "../../shared/table/Table";
import FormDialog from "../../shared/FormDialog";
import Form from "./Form";

import compose from "../../../compose";
import withDoby from "../../../consumers/withDoby";
import withUser from "../../../consumers/withUser";
import withStore from "../../../consumers/withStore";
import { withLocalization } from "../../shared/localization";

const ROWS = [
  { field: "name", disablePadding: true },
  { field: "description" },
  { field: "start", type: "date" },
  { field: "end", type: "date" },
  { field: "_id" },
];

class TableWrapper extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      x: null,
      isOpen: false,
    };
  }

  handleChangeClient = (name, value) => {
    this.setState({ clientId: value });
  };

  handleClose = () => {
    this.setState({ isOpen: false });
  };

  handleOpen = () => {
    this.setState({ isOpen: true });
  };

  handleChange = async (filters, page, sort, render) => {
    try {
      return await this.props.doby.getChallenges(filters, sort, page, render);
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  onEdit = async (x) => {
    this.setState({ x, isOpen: true });
  };

  isDisabled = (x) => {
    const isStarted = new Date(x.start) - new Date() <= 0;
    return isStarted;
  };

  render() {
    const { deleteChallenge } = this.props.doby;
    const { translate } = this.props;

    return (
      <div>
        <Table
          rows={ROWS}
          data="challenges"
          onChange={this.handleChange}
          onDelete={deleteChallenge}
          title={translate("challenges_title")}
          onEdit={this.onEdit}
          store={{ ...this.props.doby, ...this.props.store }}
          filters={[]}
          isDisabled={this.isDisabled}
          filename="challenges"
        />
        <FormDialog
          title={translate("challenge_title")}
          onClose={this.handleClose}
          isOpen={this.state.isOpen}
        >
          <Form data={this.state.x} onSubmit={this.handleClose} />
        </FormDialog>
      </div>
    );
  }
}

export default compose(
  withStore,
  withDoby,
  withUser,
  withLocalization
)(TableWrapper);
