import React from "react";
import PropTypes from "prop-types";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import Typography from "@material-ui/core/Typography";
import ArrowLeftIcon from "@material-ui/icons/ArrowLeft";
import { withRouter } from "react-router";
import withStyles from "@material-ui/core/styles/withStyles";
import { withLocalization } from "../shared/localization";
import Grid from "@material-ui/core/Grid";
import classnames from "classnames";
import TextField from "../dobyComponents/form/TextField";
import classNames from "classnames";

import { Redirect } from "react-router-dom";
import withUser from "../../consumers/withUser";
import withStore from "../../consumers/withStore";
import compose from "../../compose";
import GithubButton from "../shared/login/GithubButton";
import GoogleButton from "../shared/login/GoogleButton";
import StravaButton from "../shared/login/StravaButton";
import FbButton from "../shared/login/FbButton";
import config from "../../config";
import _ from "lodash";

const styles = (theme) => ({
  root: {
    height: "100vh",
  },
  image: {
    backgroundImage: `url(/img/intro-bg-desktop.jpg)`,
    backgroundRepeat: "no-repeat",
    backgroundColor:
      theme.palette.type === "light"
        ? theme.palette.grey[50]
        : theme.palette.grey[900],
    backgroundSize: "cover",
    backgroundPosition: "center",
  },
  paper: {
    margin: theme.spacing.unit * 8,
    marginBottom: theme.spacing.unit * 4,
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing.unit,
    // backgroundColor: theme.palette.secondary.main,
    width: 128,
    height: 128,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing.unit,
  },
  submit: {
    marginTop: theme.spacing.unit * 3,
    marginBottom: theme.spacing.unit,
  },
  center: {
    display: "flex",
    alignItems: "center",
    flexDirection: "column",
  },
});

const INIT_STATE = {
  password: {
    value: "",
    errorText: "",
  },
  repeatedPassword: {
    value: "",
    errorText: "",
  },
  email: {
    value: "",
    isLoading: false,
    errorText: "",
  },
  disabled: false,
};

const buildState = () => {
  return {
    ...INIT_STATE,
  };
};

class Register extends React.Component {
  constructor(props) {
    super(props);

    this.state = buildState();
  }

  handleSubmit = async (e) => {
    e.preventDefault();

    const isValid = this.validPasswords() && (await this.validEmail());
    if (!isValid) {
      return;
    }

    const { email, password } = this.state;
    const user = {
      username: "u:" + email.value,
      name: email.value,
      config: config.configId,
      email: email.value,
      password: password.value,
    };

    await this.props.user.registerUser(user);
    this.props.history.push("/login");
  };

  handleChange = (name, value) => {
    this.setState((state) => _.set(state, name, value));
  };

  isValid = async (username) => {
    const ids = await this.props.user.checkUser(username);
    return ids.length === 0;
  };

  validEmail = async () => {
    const { translate } = this.props;
    const { email } = this.state;

    this.setState((state) => ({
      email: {
        ...state.email,
        isLoading: true,
      },
    }));

    const isValid = !email.value || (await this.isValid(email.value));
    const errorText = isValid ? "" : translate("field.emailInUse");

    this.setState((state) => ({
      email: { ...state.email, errorText, isLoading: false },
    }));

    return isValid;
  };

  validPasswords = () => {
    const { password, repeatedPassword } = this.state;
    const { translate } = this.props;

    const regex = new RegExp(config.passwordPattern);
    const isValidPassword = !password.value || regex.test(password.value);
    const errorTextPassword = isValidPassword
      ? ""
      : translate("field.passwordWeak");

    const isValidRepeatedPassword = password.value === repeatedPassword.value;
    const errorTextRepeatedPassword = isValidRepeatedPassword
      ? ""
      : translate("field.passwordNoMatch");

    this.setState((state) => ({
      password: { ...state.password, errorText: errorTextPassword },
      repeatedPassword: {
        ...this.state.repeatedPassword,
        errorText: errorTextRepeatedPassword,
      },
    }));

    return isValidPassword && isValidRepeatedPassword;
  };

  handleClickReset = async () => {
    const { username } = this.state;

    if (username) {
      try {
        await this.props.user.reset(username.trim().toLowerCase());
        this.props.user.showMsg("Mail sended.");
      } catch (error) {
        this.props.user.showError(error);
      }
    }
  };

  handleRegister = () => {
    console.log("clicked on register");
  };

  goToLogIn = () => this.props.history.push(`/login`);

  render() {
    const { classes, user, translate } = this.props;
    const { email, password, repeatedPassword, disabled, oAuth } = this.state;

    const { from } = this.props.location.state || {
      from: { pathname: config.home },
    };

    if (oAuth) return null;
    if (user.user) return <Redirect to={from} />;

    return (
      <Grid
        container
        component="main"
        className={classnames(
          classes.root
        )}
      >
        {/* HEADER */}
        <Grid item xs={12} className={classNames(classes.mt5)}>
          <div className={classNames(classes.container)}>
            <div
              className={classNames(
                classes.dFlex,
                classes.w100,
                classes.alignItemsCenter,
                classes.justifyContentBetween
              )}
            >
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                  flexWrap: "wrap",
                }}
              >
                <ArrowLeftIcon onClick={this.goToLogIn} />
                <span>{translate("back")}</span>
              </div>
            </div>
          </div>
        </Grid>

        <CssBaseline />
        <Grid item xs={false} sm={4} md={7} className={classes.image} />
        <Grid item xs={12} sm={8} md={5} component={"div"} elevation={6} square>
          <div className={classes.paper}>
            <img alt="logo" className={classes.avatar} src={config.logo} />

            <GithubButton
              fullWidth
              disabled={disabled}
              title={translate("githubRegister")}
              config={config.github}
            />
            <GoogleButton
              fullWidth
              disabled={disabled}
              title={translate("googleRegister")}
              config={config.google}
            />
            <FbButton
              fullWidth
              disabled={disabled}
              title={translate("fbRegister")}
              config={config.fb}
            />
            <StravaButton
              fullWidth
              disabled={disabled}
              title={translate("stravaRegister")}
              config={config.strava}
            />

            <Typography variant="body1">{translate("or")}</Typography>

            <form className={classes.form} onSubmit={this.handleSubmit}>
              <TextField
                type="email"
                value={email.value}
                field="email.value"
                onChange={this.handleChange}
                disabled={disabled || email.isLoading}
                onBlur={this.validEmail}
                errorText={email.errorText}
                fullWidth
                required
              />
              <TextField
                type="password"
                value={password.value}
                field="password.value"
                onChange={this.handleChange}
                disabled={disabled}
                onBlur={this.validPasswords}
                errorText={password.errorText}
                fullWidth
                required
              />
              <TextField
                type="new-password"
                value={repeatedPassword.value}
                field="repeatedPassword.value"
                onChange={this.handleChange}
                disabled={disabled}
                onBlur={this.validPasswords}
                errorText={repeatedPassword.errorText}
                fullWidth
                required
              />

              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                disabled={disabled}
              >
                {translate("doRegister")}
              </Button>
            </form>
          </div>
        </Grid>
      </Grid>
    );
  }
}

Register.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default compose(
  withStyles(styles),
  withRouter,
  withUser,
  withStore,
  withLocalization
)(Register);
