import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import TextInput from "../shared/TextInput";
import Select from "../shared/Select";
import Form from "../shared/form/Form";
import Switch from '../shared/Switch';
import NumInput from "../shared/NumInput";

import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";

const styles = theme => ({
  nameTextInput: {
    display: "flex",
    flex: 1
  }
});

const INIT_STATE = {
  name: "",
  enumTypeId: 1,
  disabled: false,
  key: 0
};

class FormWrapper extends React.Component {
  constructor(props) {
    super(props);

    this.state = props.data || INIT_STATE
  }

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleSubmit = async () => {
    const {
      addEnum,
      refreshEnums,
      editEnum
    } = this.props.store;

    try {
      if (this.state._id) {
        await editEnum(this.state);
      } else {
        await addEnum(this.state);
      }
      refreshEnums();
      this.setState(INIT_STATE);
      if (this.props.onSubmit) this.props.onSubmit();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { classes, disabled } = this.props;
    const { name, enumTypeId, key } = this.state;
    const { enumTypes } = this.props.store;

    return (
      <Form 
        onSubmit={this.handleSubmit} 
        inline
        disabled={disabled}
      >
        <div className={classes.nameTextInput}>
          <TextInput
            value={name}
            field="name"
            onChange={this.handleChange}
            fullWidth
            required
            disabled={disabled}
          />
        </div>
        <Select
          value={enumTypeId}
          field="enumTypeId"
          onChange={this.handleChange}
          data={enumTypes.data}
          textField="name"
          required
          disabled={disabled}
        />
        <NumInput
          value={key}
          field="key"
          onChange={this.handleChange}
          min={0}
          step={1}
          required
        />
        <Switch
          label=""
          value={this.state.disabled}
          field="disabled"
          onChange={this.handleChange}
        />
      </Form>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object,
  onSubmit: PropTypes.func,
  date: PropTypes.instanceOf(Date),
  disabled: PropTypes.bool
};

export default compose(withStyles(styles), withStore, withUser)(FormWrapper);
