const getSharedStyles = (theme) => ({
    ...generateSpacings(theme),
    ...generateUtils(theme),
    // ---------- grid
    container: {
        paddingLeft: '1.25rem',
        paddingRight: '1.25rem',
        maxWidth: '1300px',
        margin: '0 auto'
        // [theme.breakpoints.up('md')]: {
        //     paddingLeft: '1.25rem',
        //     paddingRight: '1.25rem',
        // },
    },
    row: {
        display: 'flex',
        flexDirection: 'row',
        flexWrap: 'wrap',
    },
    flexGrow: {
        flexGrow: 1
    },
    // ---------- rounded
    roundedSm: {
        borderRadius: '0.25rem',
    },
    roundedMd: {
        borderRadius: '0.5rem',
    },
    roundedLg: {
        borderRadius: '0.75rem',
    },   
    // ---------- bg colors
    bgWhite: {
        background: 'white',
    },
    bg300: {
        background: '#F3F3F3',
    },
    bgPrimary: {
        background: theme.palette.primary.main,
    },
    bgSecondary: {
        background: theme.palette.secondary.main,
    },
    // ---------- text colors
    textWhite: {
        color: '#fff'
    },
    textPrimary: {
        color: theme.palette.primary.main,
    },
    textSecondary: {
        color: theme.palette.secondary.main,
    },
    textSecondaryLight: {
        color: '#A4B1C4'
    },
    textError: {
        color: 'red',
        marginLeft: '0'
    },
    // ---------- text utils    
    uppercase: {
        textTransform: 'uppercase'
    }, 
    capitalize: {
        textTransform: 'capitalize'
    }, 
    textMono: {
        fontFamily: "'IBM Plex Mono', monospace",
    }, 
    textSans: {
        fontFamily: "'IBM Plex Sans', monospace",
    },
    // ---------- text sizes
    textBase: {
        fontSize: '16px',
        lineHeight: '130%',
    },
    textM: {
        fontSize: '14px',
        lineHeight: '130%',
    },
    textS: {
        fontSize: '12px',
        lineHeight: '130%',
    },
    textXs: {
        fontSize: '10px',
        lineHeight: '130%',
    },

    textXl: {
        lineHeight: '120%',
        letterSpacing: '-1px',
        fontSize: '50px',
        fontWeight: 700,
    },
    textH1: {
        lineHeight: '120%',
        letterSpacing: '-1px',
        fontSize: '40px',
        fontWeight: 700,
    },
    textH2: {        
        lineHeight: '120%',
        letterSpacing: '-0.5px',
        fontSize: '32px',
        fontWeight: 700,
    },
    textH3: {
        lineHeight: '120%',
        letterSpacing: '-0.5px',
        fontSize: '24px',
        fontWeight: 700,
    },
    textH4: {
        lineHeight: '120%',
        letterSpacing: '-0.5px',
        fontSize: '20px',
        fontWeight: 700,
    },   
    textBlock: {
        lineHeight: '150%'
    },
    //text color
    text200: {
        color: 'rgba(51,52,66,0.5)',
    },
    text300: {
        color: 'rgba(51,52,66,0.7)',
    },
    text400: {
        color: '#333442',
    },
    textEl250: {
        color: '#A4B1C4',
    },
    // ---------- font weights
    fontRegular: {
        fontWeight: 400,
    },
    fontMedium: {
        fontWeight: 500,
    }, 
    fontSemibold: {
        fontWeight: 600,
    },     
    fontBold: {
        fontWeight: 700,
    },    
    // ---------- utils
    hrLine: {
        border: 0,
        borderBottom: '1px solid rgba(0,0,0,0.08)',
    },
    positionRelative: {
        position: 'relative',
    },
    positionAbsolute: {
        position: 'absolute',
    },
    w100: {
        width: '100%',
    },
    w50:{
        width: '50%',
    },    
    h100: {
        height: '100%',
    },
    h50: {
        height: '50%',
    },
    dBlock: {
        display: 'block',
    },
    dBlockInline: {
        display: 'block-inline',
    },    
    alignItemsCenter: {
        alignItems: 'center'
    },
    alignItemsStart: {
        alignItems: 'flex-start'
    },
    alignItemsEnd: {
        alignItems: 'flex-end'
    },    
    carouselFullwidth: {
        marginRight: '-1.25rem',
        marginLeft: '-1.25rem',
    },
    carouselSpacing: {
        '& .flickity-viewport': {
            //padding: '7px'
        },
        '& .carousel-cell:first-child': {
            paddingLeft: '1.25rem'
        },
        '& .carousel-cell:last-child': {
            paddingRight: '1.25rem',
        }
    },

    // ---------- card
    cardShadow: {
        boxShadow: "1px 1px 10px 1px rgba(0,0,0,0.08)",
    },
    // ---------- shadows
    shadowTiny: {
        boxShhadow: '0px 1px 3px rgba(0, 0, 0, 0.14)',
    },
    shadowSmall: {
        boxShhadow: '2px 4px 10px 2px rgba(0, 0, 0, 0.07)',
    },
    shadowMedium: {
        boxShhadow: '0px 7px 20px 4px rgba(0, 0, 0, 0.08)',
    },
    shadowLarge: {
        boxShhadow: '8px 8px 50px 4px rgba(0, 0, 0, 0.2)',
    },
    shadowXl: {
        boxShhadow: '0px 16px 64px rgba(0, 0, 0, 0.06), 0px 12px 24px rgba(0, 0, 0, 0.04), 0px 6px 8px rgba(0, 0, 0, 0.02), 0px 2px 8px 2px rgba(0, 0, 0, 0.08)',
    },
    shadowXxl: {
        boxShhadow: '0px 16px 64px rgba(0, 0, 0, 0.06), 0px 12px 24px rgba(0, 0, 0, 0.04), 0px 6px 8px rgba(0, 0, 0, 0.02)',
    },
    // ---------- images
    imageFit: {
        position: 'absolute',
        left: 0,
        top: 0,
        zIndex: 0,
        width: '100%',
        height: '100%',        
        objectFit: 'cover',
    },
    imageFitHolder: {
        position: 'relative',
        overflow: 'hidden',
    }   
    // ----------
    
})

const spacings = [  
    '0rem',  
    '0.25rem',
    '0.5rem',
    '0.75rem',
    '1rem',
    '1.25rem',
    '1.5rem',
    '2rem',
    '2.5rem',
    '3rem',
    '4rem',
    '5rem',
    '7rem',
    '10rem',
]

const generateSpacings = (theme) => {
    let ret = {};

    let keys = [...theme.breakpoints.keys];
    keys.unshift("")

    keys.forEach(key => {
        spacings.forEach((x, index) => { 
            
            //margin   
            ret[`mt${capitalize(key)}${index}`] = {
                ...(
                    key !== "" ?
                   {[theme.breakpoints.up(key)]: {
                        marginTop: x+'!important',
                    }} : {marginTop: x+'!important',}
                )                
            };
            ret[`mr${capitalize(key)}${index}`] = {
                ...(
                    key !== "" ?
                   {[theme.breakpoints.up(key)]: {
                        marginRight: x+'!important',
                    }} : {marginRight: x+'!important',}
                )                
            };
            ret[`mb${capitalize(key)}${index}`] = {
                ...(
                    key !== "" ?
                   {[theme.breakpoints.up(key)]: {
                        marginBottom: x+'!important',
                    }} : {marginBottom: x+'!important',}
                )               
            };
            ret[`ml${capitalize(key)}${index}`] = {
                ...(
                    key !== "" ?
                   {[theme.breakpoints.up(key)]: {
                        marginLeft: x+'!important',
                    }} : {marginLeft: x+'!important',}
                )                
            };
            ret[`m${capitalize(key)}${index}`] = {
                ...(
                    key !== "" ?
                   {[theme.breakpoints.up(key)]: {
                        margin: x+'!important',
                    }} : {margin: x+'!important',}
                )                 
            };
            ret[`my${capitalize(key)}${index}`] = {
                ...(
                    key !== "" ?
                   {[theme.breakpoints.up(key)]: {
                        marginTop: x+'!important',
                        marginBottom: x+'!important',
                    }} : {marginTop: x+'!important', marginBottom: x+'!important',}
                )                
            };
            ret[`mx${capitalize(key)}${index}`] = {
                ...(
                    key !== "" ?
                   {[theme.breakpoints.up(key)]: {
                        marginLeft: x+'!important',
                        marginRight: x+'!important',
                    }} : {marginLeft: x+'!important', marginRight: x+'!important',}
                )                
            };
            //pading
            ret[`pt${capitalize(key)}${index}`] = {
                ...(
                    key !== "" ?
                   {[theme.breakpoints.up(key)]: {
                        paddingTop: x+'!important',
                    }} : {paddingTop: x+'!important',}
                )                
            };
            ret[`pr${capitalize(key)}${index}`] = {
                ...(
                    key !== "" ?
                   {[theme.breakpoints.up(key)]: {
                        paddingRight: x+'!important',
                    }} : {paddingRight: x+'!important',}
                )               
            };
            ret[`pb${capitalize(key)}${index}`] = {
                ...(
                    key !== "" ?
                   {[theme.breakpoints.up(key)]: {
                        paddingBottom: x+'!important',
                    }} : {paddingBottom: x+'!important',}
                )                
            };
            ret[`pl${capitalize(key)}${index}`] = {
                ...(
                    key !== "" ?
                   {[theme.breakpoints.up(key)]: {
                        paddingLeft: x+'!important',
                    }} : {paddingLeft: x+'!important',}
                )               
            };
            ret[`p${capitalize(key)}${index}`] = {
                ...(
                    key !== "" ?
                   {[theme.breakpoints.up(key)]: {
                        padding: x+'!important',
                    }} : {padding: x+'!important',}
                )               
            };
            ret[`py${capitalize(key)}${index}`] = {
                ...(
                    key !== "" ?
                   {[theme.breakpoints.up(key)]: {
                        paddingTop: x+'!important',
                        paddingBottom: x+'!important',
                    }} : {paddingTop: x+'!important', paddingBottom: x+'!important',}
                )               
            };
            ret[`px${capitalize(key)}${index}`] = {
                ...(
                    key !== "" ?
                   {[theme.breakpoints.up(key)]: {
                        paddingLeft: x+'!important',
                        paddingRight: x+'!important',
                    }} : {paddingLeft: x+'!important', paddingRight: x+'!important',}
                )               
            };
        }); 
        
        
    })
    return ret;
}

const displayProps = [
    {
        name: 'Block',
        value: 'block',
    },
    {
        name: 'Flex',
        value: 'flex',
    },
    {
        name: 'InlineBlock',
        value: 'inline-block',
    },
    {
        name: 'None',
        value: 'none',
    },   
]

const justifyContentProps = [
    {
        name: 'Center',
        value: 'center',
    },
    {
        name: 'Start',
        value: 'flex-start',
    },
    {
        name: 'End',
        value: 'flex-end',
    },
    {
        name: 'Between',
        value: 'space-between',
    },   
]

const flexDirectionProps = [
    {
        name: 'Col',
        value: 'column'
    },
    {
        name: 'Row',
        value: 'row'
    }
]

const alignItemsProps = [
    {
        name: 'Start',
        value: 'flex-start'
    },
    {
        name: 'End',
        value: 'flex-end'
    },
    {
        name: 'Center',
        value: 'center'
    }
]

const textAlignProps = [
    {
        name: 'Center',
        value: 'center',
    },
    {
        name: 'Left',
        value: 'left',
    },
    {
        name: 'Right',
        value: 'right',
    },      
]

const generateUtils = (theme) => {  
    let ret = {};   

    let keys = [...theme.breakpoints.keys];
    keys.unshift("")

    keys.forEach(key => {
        displayProps.forEach(x => {       
            ret[`d${capitalize(key)}${x.name}`] = {
                ...(                
                    key !== "" ?
                        {[theme.breakpoints.up(key)]: {
                            display: x.value,
                        }} : {display: x.value}
                )
            }
        });  
        
        justifyContentProps.forEach(x => {       
            ret[`justifyContent${capitalize(key)}${x.name}`] = {
                ...(
                    key !== "" ?
                    {[theme.breakpoints.up(key)]: {
                            justifyContent: x.value,
                        }} : {justifyContent: x.value}
                )
            }
        });  

        flexDirectionProps.forEach(x => {       
            ret[`flex${capitalize(key)}${x.name}`] = {
                ...(
                    key !== "" ?
                    {[theme.breakpoints.up(key)]: {
                            flexDirection: x.value,
                        }} : {flexDirection: x.value}
                )
            }
        }); 

        alignItemsProps.forEach(x => {       
            ret[`alignItems${capitalize(key)}${x.name}`] = {
                ...(
                    key !== "" ?
                    {[theme.breakpoints.up(key)]: {
                            alignItems: x.value,
                        }} : {alignItems: x.value}
                )
            }
        }); 
        
        textAlignProps.forEach(x => {       
            ret[`text${capitalize(key)}${x.name}`] = {
                ...(
                    key !== "" ?
                    {[theme.breakpoints.up(key)]: {
                            textAlign: x.value,
                        }} : {textAlign: x.value}
                )
            }
        }); 
    })
    return ret;
}

const capitalize = (s) => {
    if (typeof s !== 'string') return ''
    return s.charAt(0).toUpperCase() + s.slice(1)
  }


export default getSharedStyles