import React from "react";
import { withRouter } from "react-router";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import { withLocalization } from "../../shared/localization";
import compose from "../../../compose";
import getSharedStyles from "../../../style";
import classNames from "classnames";
import withUser from "../../../consumers/withUser";
import withDoby from "../../../consumers/withDoby";
import withStore from "../../../consumers/withStore";

import ClearIcon from "@material-ui/icons/Clear";
import Button from "../../dobyComponents/form/Button";
import Flickity from "react-flickity-component";

import ActiveChallengeCard from "../../dobyComponents/ActiveChallengeCard";
import ChallengeCard from "../../dobyComponents/ChallengeCard";
import ActivityPost from "../../dobyComponents/ActivityPost";
import Autocomplete from "../../shared/Autocomplete";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  profileImgHolder: {
    width: "120px",
    height: "120px",
    borderRadius: "100px",
    overflow: "hidden",
    position: "relative",
  },
  profileImg: {
    width: "100%",
    height: "100%",
    position: "absolute",
    top: "0",
    left: "0",
    objectFit: "cover",
  },
  medalItem: {
    width: "120px",
    position: "relative",
    "&:after": {
      content: "''",
      display: "block",
      position: "absolute",
      left: 0,
      top: 0,
      width: "100%",
      height: "140px",
      borderRadius: "10px",
      background: "#F5F5F5",
      zIndex: "-1",
    },
  },
  medalImgHolder: {
    width: "100px",
    height: "150px",
  },
  medalImg: {
    width: "100%",
    height: "auto",
  },
  medalTitle: {
    minHeight: "40px",
  },
  //   medalItem: {
  //     width: "120px",
  //     height: "160px",
  //     position: "relative",
  //   },
  //   medalItemBg: {
  //     background: "#F5F5F5",
  //     //background: 'red',
  //     borderRadius: "10px",
  //     width: "100%",
  //     height: "160px",
  //   },
  //   medalItemImg: {
  //     width: "95px",
  //     position: "absolute",
  //     top: "45px",
  //     left: 0,
  //     right: 0,
  //     margin: "0 auto",
  //   },
  medalCarousel: {
    "& .flickity-viewport": {
      //padding: '7px'
    },
    "& .carousel-cell:first-child": {
      paddingLeft: "1.25rem",
    },
    "& .carousel-cell:last-child": {
      //paddingRight: '0.5rem',
    },
  },
  challengesCarousel: {
    "& .flickity-viewport": {
      //padding: '7px'
    },
    "& .carousel-cell:first-child": {
      paddingLeft: "1.25rem",
    },
    "& .carousel-cell:last-child": {
      //paddingRight: '0.5rem',
    },
  },
});

const flickityOptions = {
  cellAlign: "left",
  contain: true,
  prevNextButtons: false,
  pageDots: false,
};

class Profile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      friend: null,
      dobyFriend: null,
      dobyUser: null,
      medals: [],
      unfinishedChallenges: [],
      challengesUserNotParticipate: [],
      currUserFollowingFriend: false,
      myClimbs: [],
      summits: [],
      foundFriend: null,
      watchingYourself: false,
    };
  }

  buildFilter = (value) => {
    return [
      {
        field: "userRef.id",
        op: "eq",
        value,
      },
    ];
  };

  async componentDidMount() {
    const { _id } = this.props.match.params;
    const { user } = this.props.user;
    const { getUser, getClimbs } = this.props.doby;

    const watchingYourself = _id === user._id;
    this.setState({ watchingYourself });

    const dobyUser = await getUser(user._id);
    this.setState({ dobyUser });

    const filter = this.buildFilter(_id || user._id);
    const climbs = await getClimbs(filter);

    if (_id) {
      const dobyFriend = await this.props.doby.getUser(_id);
      const friend = await this.props.user.getUser(_id);
      const currUserFollowingFriend =
        dobyUser.followingRef.find((f) => f.id === _id) !== undefined;

      this.setState({ dobyFriend, friend, currUserFollowingFriend });

      this.setMyClimbs(climbs.data, friend);
      this.setUnfinishedChallenges(dobyFriend);
      this.setMedals(dobyFriend);
    } else {
      this.setMyClimbs(climbs.data, user);
      this.setUnfinishedChallenges(dobyUser);
      this.setMedals(dobyUser);
    }
  }

  setMyClimbs = async (climbs, user) => {
    const myClimbs = climbs.map((c) => {
      if (c.summit && c.summit.imageId)
        this.getFile(c.summit.imageId, this.setImageToClimbCallback, c._id);

      return {
        ...c,
        userName: user.name,
        userAddress: `${user.city} ${user.country}`,
        avatar: user.avatar,
      };
    });

    this.setState({ myClimbs });
  };

  setUnfinishedChallenges = (dobyUser) => {
    const { challenges, challengesRef } = dobyUser;

    let unfinishedChallenges = [];
    let unfinishedChallengesRef = [];
    if (challengesRef) {
      unfinishedChallengesRef = challengesRef
        .filter((c) => c.finished === null)
        .map((c) => ({ id: c.id, count: c.count }));
    }

    unfinishedChallenges = unfinishedChallengesRef.map((activeC) => {
      const found = challenges.find((c) => c._id === activeC.id);
      return {
        ...found,
        countFinishedSummits: activeC.count,
      };
    });

    this.setState({ unfinishedChallenges });
  };

  setMedals = (dobyUser) => {
    const { challenges, challengesRef } = dobyUser;

    let medals = [];
    let finishedChallengesRefIDs = [];
    if (challengesRef) {
      finishedChallengesRefIDs = challengesRef
        .filter((c) => c.finished !== null)
        .map((c) => c.id);
    }

    medals = finishedChallengesRefIDs.map((id) => {
      const medal = challenges.find((c) => c._id === id);
      this.getFile(medal.logoId, this.setLogoToMedalCallback, medal._id);

      return medal;
    });

    this.setState({ medals });
  };

  // Follow/Unfollow user
  followUser = async () => {
    const { _id } = this.props.match.params;

    this.props.doby.followUser(_id);
    this.setState({ currUserFollowingFriend: true });
  };
  unfollowUser = async (id) => {
    const { _id } = this.props.match.params;

    this.props.doby.unfollowUser(_id);
    this.setState({ currUserFollowingFriend: false });
  };

  // Callbacks
  setLogoToMedalCallback = (logoSrc, id) => {
    const medals = [...this.state.medals];

    this.setState({
      medals: [
        ...medals.map((m) => {
          if (m._id === id) return { ...m, logoSrc };
          return m;
        }),
      ],
    });
  };
  setImageToClimbCallback = (imgSrc, id) => {
    const myClimbs = [...this.state.myClimbs];

    this.setState({
      myClimbs: [
        ...myClimbs.map((m) => {
          if (m._id === id) return { ...m, imgSrc };
          return m;
        }),
      ],
    });
  };

  getFile = async (fileId, callback, identificator) => {
    try {
      if (fileId) {
        const data = await this.props.store.getFile(fileId);

        var reader = new FileReader();
        reader.onloadend = async () => {
          callback(reader.result, identificator);
        };
        reader.readAsDataURL(data);
      }
    } catch (e) {
      console.error(e);
    }
  };

  joinChallenge = (challenge) => {
    if (challenge._id)
      this.props.history.push(`/challenge-checkout/${challenge._id}`);
  };

  handleGetChallengesUserNotParticipate = async () => {
    const { dobyUser } = this.state;

    if (dobyUser) {
      const userChals = dobyUser.challenges.map((c) => c._id);
      const filter = [
        { field: "end", value: new Date().toISOString(), op: "gt" },
      ];

      const filteredChals = await this.props.doby.getChallenges(
        filter,
        { field: "name", dir: "asc" },
        { limit: 1000, skip: 0 },
        false
      );

      const challengesUserNotParticipate = filteredChals
        ? filteredChals.data.filter(
            (filteredChal) =>
              userChals.find(
                (userChal) => userChal._id === filteredChal._id
              ) === undefined
          )
        : [];

      this.setState({ challengesUserNotParticipate });
    }
  };

  handleOnChangeSearchFriends = (name, friend) => {
    this.goToProfileFriend(friend._id);
  };

  handleOnFilterChangeSearchFriends = async (filter) => {
    await this.props.user.getUsers(filter);
  };

  renderMedals = () => {
    const { classes } = this.props;
    return this.state.medals.map((medal) => {
      return (
        <div className={"carousel-cell"}>
          <div
            className={classNames(
              classes.flexCol,
              classes.medalItem,
              classes.alignItemsCenter
            )}
          >
            <div
              className={classNames(
                classes.medalTitle,
                classes.textXs,
                classes.textCenter,
                classes.flexCol,
                classes.justifyContentCenter,
                classes.px3,
                classes.pt3,
                classes.pb2
              )}
            >
              {medal.name}
            </div>
            <div className={classNames(classes.medalImgHolder)}>
              <img
                alt="medalImg"
                src={medal.logoSrc}
                className={classNames(classes.medalImg)}
              />
            </div>
          </div>
        </div>
      );
    });
  };

  renderMyChallenges = () => {
    const { classes } = this.props;

    return this.state.unfinishedChallenges.map((challenge) => {
      return (
        <ActiveChallengeCard
          className={classNames(classes.mt3)}
          name={challenge.name}
          allSummits={challenge.summitsRef ? challenge.summitsRef.length : 0}
          summitsFinished={challenge.countFinishedSummits}
          dateFrom={challenge.start}
          dateTo={challenge.end}
        />
      );
    });
  };

  renderChallenges = () => {
    const { classes } = this.props;

    return this.state.challengesUserNotParticipate.map((challenge) => {
      return (
        <div className={classNames("carousel-cell", classes.py2)}>
          <ChallengeCard
            className={classNames(classes.mr5)}
            title={challenge.name}
            description={challenge.description}
            onClickParticipate={() => this.joinChallenge(challenge)}
            onClickMoreInfo={() => {}}
          />
        </div>
      );
    });
  };

  renderMyClimbs = () => {
    const { classes } = this.props;
    const { myClimbs, dobyUser, dobyFriend } = this.state;

    return myClimbs.map((climb) => {
      const {
        description,
        userName,
        userAddress,
        avatar,
        created,
        summit,
        challenge,
        imgSrc,
        likesCount,
      } = climb;

      const challengeRefFound =
        dobyFriend && challenge
          ? dobyFriend.challengesRef.find((c) => c.id === challenge._id)
          : dobyUser && challenge
          ? dobyUser.challengesRef.find((c) => c.id === challenge._id)
          : null;
      return (
        <ActivityPost
          className={classNames(classes.mt3)}
          description={description}
          userName={userName}
          userAddress={userAddress}
          avatar={avatar}
          image={imgSrc}
          summitName={summit ? summit.name : "NaN"}
          summitElevation={summit ? summit.elevation : 0}
          climbCreated={created}
          challengeName={challenge ? challenge.name : "NaN"}
          challengeAllSummits={challenge ? challenge.summitsRef.length : 0}
          challengeFinishedSummits={
            challengeRefFound ? challengeRefFound.count : 0
          }
          likesCount={likesCount || 0}
        />
      );
    });
  };

  goToProfileFriend = (id) => this.props.history.push(`/profile-friend/${id}`);
  goToProfileSettings = () => this.props.history.push(`/profile-settings`);

  render() {
    const { classes, translate } = this.props;
    const { user, users } = this.props.user;
    const {
      friend,
      medals,
      unfinishedChallenges,
      dobyFriend,
      dobyUser,
      currUserFollowingFriend,
      foundFriend,
      watchingYourself,
    } = this.state;

    const isFriendProfile = friend !== null;
    return (
      <>
        <div>
          <Grid container direction="row" justify="center" alignItems="center">
            {/* HEADER */}
            <Grid item xs={12} className={classNames(classes.mt5)}>
              <div className={classNames(classes.container)}>
                <div
                  className={classNames(
                    classes.dFlex,
                    classes.w100,
                    classes.alignItemsCenter,
                    classes.justifyContentBetween
                  )}
                >
                  <div className={classNames(classes.textXs)}>
                    {translate("home")}
                  </div>
                  {!isFriendProfile && (
                    <div>
                      <ClearIcon onClick={this.goToProfileSettings} />
                    </div>
                  )}
                </div>
              </div>
            </Grid>

            {/* PROFILE IMAGE + FIND FRIENDS */}
            <Grid item xs={12}>
              <div className={classNames(classes.container)}>
                <div
                  className={classNames(
                    classes.dFlex,
                    classes.alignItemsCenter,
                    classes.w100,
                    classes.mt4
                  )}
                >
                  <div className={classNames(classes.profileImgHolder)}>
                    <img
                      alt="avatarImg"
                      src={
                        friend && friend.avatar
                          ? "data:image/jpeg;base64," + friend.avatar
                          : user && user.avatar
                          ? "data:image/jpeg;base64," + user.avatar
                          : "./img/user-img.jpg"
                      }
                      className={classNames(classes.profileImg)}
                    />
                  </div>
                  <div
                    className={classNames(
                      classes.flexCol,
                      classes.flexGrow,
                      classes.ml5
                    )}
                  >
                    <div
                      className={classNames(
                        classes.dFlex,
                        classes.justifyContentBetween,
                        classes.w100
                      )}
                    >
                      <div className={classNames(classes.flexCol)}>
                        <div
                          className={classNames(
                            classes.textBase2,
                            classes.fontMedium
                          )}
                        >
                          {isFriendProfile && dobyFriend
                            ? dobyFriend.climbsCnt
                            : dobyUser
                            ? dobyUser.climbsCnt
                            : 0}
                        </div>
                        <div
                          className={classNames(
                            classes.textXs2,
                            classes.text300
                          )}
                        >
                          {translate("ofHills")}
                        </div>
                      </div>
                      <div className={classNames(classes.flexCol)}>
                        <div
                          className={classNames(
                            classes.textBase2,
                            classes.fontMedium
                          )}
                        >
                          {isFriendProfile && dobyFriend
                            ? dobyFriend.followersRef.length
                            : dobyUser
                            ? dobyUser.followersRef.length
                            : 0}
                        </div>
                        <div
                          className={classNames(
                            classes.textXs2,
                            classes.text300
                          )}
                        >
                          {`${
                            isFriendProfile
                              ? translate("followers")
                              : translate("followMe")
                          }`}
                        </div>
                      </div>
                      <div className={classNames(classes.flexCol)}>
                        <div
                          className={classNames(
                            classes.textBase2,
                            classes.fontMedium
                          )}
                        >
                          {isFriendProfile && dobyFriend
                            ? dobyFriend.followingRef.length
                            : dobyUser
                            ? dobyUser.followingRef.length
                            : 0}
                        </div>
                        <div
                          className={classNames(
                            classes.textXs2,
                            classes.text300
                          )}
                        >
                          {`${
                            isFriendProfile
                              ? translate("heFollow")
                              : translate("following")
                          }`}
                        </div>
                      </div>
                    </div>
                    <div className={classNames(classes.mt3, classes.w100)}>
                      {isFriendProfile ? (
                        <Button
                          disabled={watchingYourself}
                          fullwidth
                          onClick={
                            currUserFollowingFriend
                              ? this.unfollowUser
                              : this.followUser
                          }
                        >
                          {`${
                            currUserFollowingFriend
                              ? translate("following")
                              : translate("follow")
                          }`}
                        </Button>
                      ) : (
                        <Autocomplete
                          classes={classes}
                          onChange={this.handleOnChangeSearchFriends}
                          onFilterChange={
                            this.handleOnFilterChangeSearchFriends
                          }
                          data={users ? users.data : []}
                          field="foundFriend"
                          textField="username"
                          label={translate("searchFriends")}
                          value={foundFriend}
                          fullWidth
                        />
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </Grid>

            {/* POPIS */}
            <Grid item xs={12} className={classNames(classes.mt4)}>
              <div className={classNames(classes.container)}>
                <div className={classNames(classes.fontBold, classes.dBlock)}>
                  {isFriendProfile ? friend.name : user.name}
                </div>
                <div
                  className={classNames(
                    classes.fontBold,
                    classes.textXs,
                    classes.dBlock,
                    classes.mt3
                  )}
                >
                  {isFriendProfile
                    ? `${friend.city}, ${friend.country}`
                    : `${user.city}, ${user.country}`}
                </div>
                <div
                  className={classNames(
                    classes.textXs2,
                    classes.dBlock,
                    classes.mt1
                  )}
                >
                  {isFriendProfile ? friend.bio : user.bio}
                  {/* Mountain lover  MTB enthusiast. Mountain lover  MTB
                  enthusiast.Mountain lover  MTB enthusiast. */}
                </div>
                <hr
                  className={classNames(
                    classes.w100,
                    classes.mt4,
                    classes.mb4,
                    classes.hrLine
                  )}
                />
              </div>
            </Grid>

            {/* MEDALS */}
            <Grid
              item
              xs={12}
              className={classNames(
                classes.mt7,
                classes.bgWhite,
                classes.pt6,
                classes.pb6
              )}
            >
              <div className={classNames(classes.container)}>
                <div
                  className={classNames(
                    classes.dFlex,
                    classes.alignItemsCenter,
                    classes.justifyContentBetween
                  )}
                >
                  <div className={classNames(classes.fontSemibold)}>
                    {`${translate("medals")} (${medals.length})`}
                  </div>
                  <div
                    className={classNames(
                      classes.textXs,
                      classes.text300,
                      classes.fontMedium
                    )}
                  >
                    {translate("showAll")}
                  </div>
                </div>
              </div>
              <div>
                <Flickity
                  className={classNames(classes.mt4, classes.medalCarousel)}
                  options={flickityOptions}
                  reloadOnUpdate={true}
                >
                  {this.renderMedals()}
                </Flickity>
              </div>
            </Grid>

            {/* ACTUAL CHALLENGES */}
            <Grid item xs={12} className={classNames(classes.mt7)}>
              <div className={classNames(classes.container)}>
                <div
                  className={classNames(
                    classes.dFlex,
                    classes.alignItemsCenter,
                    classes.justifyContentBetween
                  )}
                >
                  <div className={classNames(classes.fontSemibold)}>
                    {`${translate("activeChallenges")} (${
                      unfinishedChallenges.length
                    })`}
                  </div>
                  <div
                    className={classNames(
                      classes.textXs,
                      classes.text300,
                      classes.fontMedium
                    )}
                  >
                    {isFriendProfile ? "" : translate("showAll")}
                  </div>
                </div>

                {this.renderMyChallenges()}
              </div>
            </Grid>

            {/* CHALLENGES */}
            {!isFriendProfile && (
              <Grid item xs={12} className={classNames(classes.mt7)}>
                <div className={classNames(classes.container)}>
                  <div
                    className={classNames(
                      classes.dFlex,
                      classes.alignItemsCenter,
                      classes.justifyContentBetween
                    )}
                  >
                    <div className={classNames(classes.fontSemibold)}>
                      Nezmekaj
                    </div>
                    <div
                      className={classNames(
                        classes.textXs,
                        classes.text300,
                        classes.fontMedium
                      )}
                    >
                      {translate("showAll")}
                    </div>
                  </div>
                </div>
                <div>
                  <Flickity
                    className={classNames(
                      classes.mt2,
                      classes.challengesCarousel
                    )}
                    options={flickityOptions}
                    reloadOnUpdate={true}
                  >
                    {this.renderChallenges()}
                  </Flickity>
                </div>
              </Grid>
            )}

            {/* CLIMBS */}
            <Grid
              item
              xs={12}
              className={classNames(classes.mt7, classes.bgWhite, classes.pt6)}
            >
              <div className={classNames(classes.container)}>
                <div
                  className={classNames(classes.fontSemibold, classes.dBlock)}
                >
                  {translate("climbs")}
                </div>
              </div>
              <div>{this.renderMyClimbs()}</div>
            </Grid>
          </Grid>
        </div>
      </>
    );
  }
}

Profile.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default compose(
  withStyles(styles),
  withDoby,
  withUser,
  withStore,
  withLocalization,
  withRouter
)(Profile);
