import React from "react";
import MaskedInput from "react-text-mask";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import { withLocalization } from "./localization";

const styles = (theme) => ({
  formControl: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 40,
  },
});

function Mask(props) {
  const { inputRef, ...other } = props;

  return (
    <MaskedInput
      {...other}
      ref={inputRef}
      mask={[/\d/, /\d/, ":", /\d/, /\d/]}
      placeholderChar={"\u2000"}
      showMask
    />
  );
}

Mask.propTypes = {
  inputRef: PropTypes.func.isRequired,
};

function TimeInput(props) {
  const { classes } = props;
  const label = props.label || props.translate("field." + props.field);

  return (
    <FormControl className={classes.formControl}>
      <InputLabel htmlFor={props.field}>{label}</InputLabel>
      <Input
        value={props.value}
        onChange={(e) => props.onChange(props.field, e.target.value)}
        id={props.field}
        inputComponent={Mask}
        placeholder={props.placeholder}
        disabled={props.disabled}
      />
    </FormControl>
  );
}

TimeInput.propTypes = {
  classes: PropTypes.object.isRequired,
  label: PropTypes.string,
  field: PropTypes.string.isRequired,
  value: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  placeholder: PropTypes.string,
  disabled: PropTypes.bool,
};

export default withLocalization(withStyles(styles)(TimeInput));
