import React from "react";
import StoreContext from "../contexts/StoreContext";

const withStore = (WrappedComponent) => {
  return (props) => {
    return (
      <StoreContext.Consumer>
        {store =>
          <WrappedComponent
            {...props}
            store={store}
          />
        }
      </StoreContext.Consumer>
    );
  }
};

export default withStore;