import React from "react";
import Table from "../shared/table/Table";
import FormDialog from "../shared/FormDialog";
import Form from "./Form";

import compose from "../../compose";
import withStore from "../../consumers/withStore";
import withDoby from "../../consumers/withDoby";
import withUser from "../../consumers/withUser";
import { withLocalization } from "../shared/localization";

const ROWS = [
  { field: "name", disablePadding: true },
  { field: "price_without_tax", type: "num" },
  { field: "tax", type: "num" },
  { field: "ean" },
  { field: "price_with_tax", type: "num" },
  { field: "disabled", type: "bool" },
  { field: "_id" },
];

class TableWrapper extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      x: null,
      isOpen: false,
    };
  }

  handleChangeClient = (name, value) => {
    this.setState({ clientId: value });
  };

  handleClose = () => {
    this.setState({ isOpen: false });
  };

  handleOpen = () => {
    this.setState({ isOpen: true });
  };

  handleChange = async (filters, page, sort, render) => {
    try {
      return await this.props.doby.getProducts(filters, sort, page, render);
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  handleClone = async (x) => {
    this.setState({ x: { ...x, _id: "" }, isOpen: true });
  };

  onEdit = async (x) => {
    this.setState({ x, isOpen: true });
  };

  isDisabled = (x) => {
    return false;
  };

  render() {
    const { deleteProduct } = this.props.doby;
    const { translate } = this.props;

    return (
      <div>
        <Table
          rows={ROWS}
          data="products"
          onChange={this.handleChange}
          onClone={this.handleClone}
          onDelete={deleteProduct}
          title={translate("products")}
          onEdit={this.onEdit}
          store={{ ...this.props.doby, ...this.props.store }}
          filters={[]}
          isDisabled={this.isDisabled}
          filename="products"
        />
        <FormDialog
          title={translate("product")}
          onClose={this.handleClose}
          isOpen={this.state.isOpen}
        >
          <Form data={this.state.x} onSubmit={this.handleClose} />
        </FormDialog>
      </div>
    );
  }
}

export default compose(
  withStore,
  withDoby,
  withUser,
  withLocalization
)(TableWrapper);
